<template>
  <div class="telecomBottom">
       <lazy-image v-for="(item,index) of elementList" :key="'telemcomB'+index" v-if="index<3"
                :src='item.elementImg'
                :placeholder='placeholder'
                :imgClass = "round"
                  />
      <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
  </div>
</template>
<style scoped>
.telecomBottom {
  position: relative;
  width: 1190px;
  display: flex;
  height: 160px;
  margin-top: 26px;
}
.telecomBottom .imglazy {
  width: 360px;
  height: 155px;
  margin-right: 16px;
}
.imglazy {
  width: 100%;
  height: 100%;
}
.imglazy.hover {
  border: 3px solid #ffffff;
  transform: scale(1.1);
  margin: -3px 13px -3px -3px;
  border-radius: 18px;
  z-index: 99;
}
</style>
<script>
import { mapGetters } from "vuex";
import c from "../../js/common.js";
import { bp } from "../../js/ga.js";
import {xha} from '../../js/xha.js';
export default {
  name: "TelecomBottom",
  props: ["initData", "itemSort"],
  data() {
    return {
      isShow: false,
      placeholder: require("../../assets/img_loading_352x190.png"),
      initScore: require("../../assets/corner_mark.png"),
      itemNo: 0,
      pos: 0,
      eleId: "",
      elementList: [],
      round: "round"
    };
  },
  created() {
    this.elementList = this.initData.elementList;
    this.eleId = this.initData.specialId;
  },
  mounted() {},
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.removeCss();
        this.itemNo = 0;
        this.pos = 0;
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "back");
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
         let posId = '';
        let columnId = c.BPTypefilters(this.category[this.navpos].catCode);
        if(this.itemSort){
          posId =  this.itemSort + '0'+(this.itemNo+1)
        }
        c.setPosition(posId,columnId); // 保存推荐位置
        xha.pageLoad(this.category[this.navpos].catName,"",this.elementList[this.itemNo].elementName,this.elementList[this.itemNo].elementId,posId,columnId); // xha 埋点
        const elementId = this.elementList[this.itemNo].elementId;
        const trackId = this.category[this.navpos].catId + "|" + elementId;
        bp.categoryClick(
          this.category[this.navpos].catId,
          elementId,
          trackId,
          1,
          ""
        );
        const eleDetail = this.elementList[this.itemNo];
        if(eleDetail.layout=="Subject_Home"){
                c.routerSkip(
                  eleDetail.jsonUrl,
                  "12",
                  eleDetail.elementCode,
                  this.$router
                );
              }else{
                c.routerSkip(
                  eleDetail.jsonUrl,
                  eleDetail.elementType,
                  eleDetail.elementCode,
                  this.$router
                );
         }
      }
    },
    left() {
      if (this.itemNo == 0) return;
      this.removeCss();
      this.itemNo--;
      this.addCss();
    },
    right() {
      if (this.itemNo == 2) return;
      this.removeCss();
      this.itemNo++;
      this.addCss();
    },
    up() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "up");
    },
    down() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "down");
    },
    addCss() {
      if (this.pos == 0) {
        $(".telecomBottom .imglazy")
          .eq(this.itemNo)
          .addClass("hover");
      }
    },
    removeCss() {
      if (this.pos == 0) {
        $(".telecomBottom .imglazy")
          .eq(this.itemNo)
          .removeClass("hover");
      }
    },
    updateStast(bl, pos, isEnd) {
      this.isShow = bl;
      this.addCss();
      if (bl) {
        let eleH = this.cEleOffsetTop(".telecomBottom");
        this.$emit("eleScroll", this.name, pos, eleH);
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset().top;
      let realHeight = 0;
      realHeight = fEleDistance + 200;
      return realHeight;
    }
  },
  watch: {
    initData() {
      this.eleId = this.initData.specialId;
      this.elementList = this.initData.elementList;
    }
  },
  computed: {
    ...mapGetters(["category", "navpos"])
  }
};
</script>



