<template>
    <div class="showBg">
        <img :src="bgImgSrc" />
        <div class="vipButton"><img al="" :src="vipBtn" /></div>
        <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
        <order v-if="isOrder" v-on:controllMeg="componentmMsg" :orders="orderList" :assetInfo="assetInfo"  ></order>
    </div>
</template>
<style scoped>
  .vipButton{position:absolute;top:521px;left:471px }
</style>
<script>
import { HTTPUtil as api } from "../../fetch/api.js";
import { client as yh } from "../../js/client.js";
import { Authentication as auth } from "../../js/auth.js";
import order from "../../tool/order/order";
import Toast from '../../tool/toast/toast.js'
export default {
  props: ["showBg", "jsonUrl"],
  name: "Poster",
  data() {
    return {
      isShow: true,
      bgImgSrc: require("../../assets/ypys.jpg"),
      vipBtn:require("../../assets/vipbutton.png"),
      isOrder:false, // 是否弹窗
      orderListHistory:null,
      orderList:[{"productName":"优品影视包年","productCode":"810098000001","productType":"1","price":"36000","cycleUnit":"6","cycleCount":"1"},{"productName":"优品影视包季","productCode":"810098000002","productType":"1","price":"10000","cycleUnit":"5","cycleCount":"1"},{"productName":"优品影视包月","productCode":"810098000012","productType":"1","price":"3600","cycleUnit":"4","cycleCount":"1"}], // 可订购列表
      assetInfo:{
          "contentCode": "",
          "contentName": "",
          "assetImg": "",
          "assetSp": "YPYS"
      } // 资产信息
      // orderList:[{"productName":"优品影视包年","productCode":"810098000001","productType":"1","price":"1","cycleUnit":"6","cycleCount":"1"},{"productName":"优品影视包季","productCode":"810098000002","productType":"1","price":"1","cycleUnit":"5","cycleCount":"1"},{"productName":"优品影视包月","productCode":"810098000012","productType":"1","price":"1","cycleUnit":"4","cycleCount":"1"}], // 测试环境

    };
  },
  created() {
    window.orderHistorys = this.orderHistorys;
    this.queryYpOrder();
    if (this.jsonUrl) {
      this.init();
    }
  },
  components:{
    order,Toast
  },
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.$router.go(-1);
      }else if(kc == "KeyEnter"){
        console.log('产品包个数'+this.orderListHistory)
        if(this.orderListHistory == null){
          console.log('产品包查询结果:'+this.orderListHistory == null)
          Toast('请稍等，查询中。')
          setTimeout(function(){
            if(this.orderListHistory != null){
              this.EnterOperation();
            }else{
              console.log('timeuot')
            }
          },50)
        }else{
           this.EnterOperation();
        }
      }
    },
    init() {
      api.get(this.jsonUrl + "&returnType=jsonp").then(response => {
        if (response.code == 200) {
          this.bgImgSrc = response.data[0].imgUrl;
        }
      });
    },
    EnterOperation(){
      if(this.orderListHistory.length == 0){
          this.isShow =false;
          this.isOrder = true;
          return;    
      }
      let str = '';                  
      for (let index = 0; index < this.orderListHistory.length; index++) {
        const element = this.orderListHistory[index];   
          str+=element.productName;
        if(index+1 == this.orderListHistory.length){
          if(str.indexOf('优品') == -1 ){
            console.log('没有订购优品产品包')
            this.isShow =false;
            this.isOrder = true;
            console.log(this.orderListHistory)
          }else{
            console.log(JSON.stringify(this.orderListHistory))
            Toast({"message":"您已订购过该会员","position":'top'});
          }
        }
      }
    },
    componentmMsg(bl, name, pos, msg){
      console.log(bl+"-"+ name+"-"+ pos);
      if("orderBack" === pos ){
        this.isOrder =  false;
        this.isShow = true;
      }else if("orderCancle" === pos){
        if(msg === true){
           this.isOrder =  false;
           this.isShow = true;
          Toast("订购成功！");
        }else{
           this.isOrder =  false;
           this.isShow = true;
        }
      }
    },
    queryYpOrder() {
      if(process.env.NODE_ENV === "production" || process.env.NODE_ENV === "local"){
        
        yh.HttpPost(
          auth.OrderList,
          auth.OrderListParams("1", "1", 1, 10),
          "orderHistorys"
        );
      }else{
        let params ={};
        params.url="/api/OrderList";
        params.type="post";
        params.data=JSON.parse(auth.OrderListParams("1","0",1,10));
        api.ajax(params).then((jsonData)=>{
          console.log(JSON.stringify(jsonData));
            if(jsonData.code==200){
              this.orderHistorys(jsonData);
            }
        })
      }
    },
    orderHistorys(jsonData) {
      console.log('优品宣传页获取订购产品包'+jsonData);
      if (jsonData.code == 200) {
        this.orderListHistory = jsonData.result.orders;
        console.log('产品包查询结果2:'+JSON.stringify(this.orderListHistory))
      }else{
        console.log('获取产品包失败')
      }
    },
  }
};
</script>


