<template>
    <div class="LiveMain">
        <live-top v-on:pressDown="liveDo" v-on:eleScroll="liveHeight" :initData="liveData" ref="liveTop" itemSort="01"></live-top>
        <live-common v-on:pressDown="liveDo" v-on:eleScroll="liveHeight" :initData="liveData" ref="liveCom" itemSort="02"></live-common>
        <live-pop-hot v-on:pressDown="liveDo" v-on:eleScroll="liveHeight" :initData="liveData" ref="liveHot" itemSort="03"></live-pop-hot>
    </div>
</template>
<style scoped>
</style>
<script>
import LiveTop from "./Category_Live_Top_HN.vue";
import LiveCommon from "./Category_Live_PicList8_HN.vue";
import LivePopHot from "./Category_LiveHot_Top12_HN";
import { client as yh } from "../../js/client.js";
export default {
  props: ["initData", "itemSort"],
  name: "LiveMain",
  components: { LiveTop, LiveCommon, LivePopHot },
  data() {
    return {
      liveData: {},
      eleId: "LiveMain",
      pageSize: 0,
      cheight: []
    };
  },
  created() {
    this.liveData = this.initData;
    if (this.liveData.liveJsonUrl.indexOf("areaCode") < 0) {
      this.liveData.liveJsonUrl =
        this.liveData.liveJsonUrl + "&areaCode=" + yh.areaCode;
    }
    if (this.liveData.liveJsonUrl.indexOf("playType") < 0) {
      this.liveData.liveJsonUrl =
        this.liveData.liveJsonUrl + "&playType=" + yh.stbType;
    }
  },
  activated() {},
  methods: {
    updateStast(bl, pos, children) {
      if (children == false) {
        this.$refs.liveTop.updateStast(bl, pos, "init");
      }
    },
    liveDo(bl, eleId, pos) {
      if (pos == "down") {
        if (eleId == "liveTopRec") {
          this.$refs.liveCom.updateStast(true, pos, false);
        } else if (eleId == "liveCommon") {
          this.$refs.liveHot.updateStast(true, pos, true);
        }
      } else if (pos == "up") {
        if (eleId == "liveTopRec") {
          this.$emit("pressDown", false, "LiveMain", "up");
        } else if (eleId == "liveCommon") {
          this.$refs.liveTop.updateStast(true, pos, false);
        } else if (eleId == "LiveHot12") {
          this.$refs.liveCom.updateStast(true, pos, false);
        }
      } else if (pos == "back") {
        this.pageSize = 0;// 返回需要将页数置为0，避免上下移动页面拉伸问题
        this.$emit("pressDown", false, "LiveMain", pos);
      }else if(pos == "updateChannel"){
        console.log("父级通知常看频道更新");
        this.$refs.liveCom.updateChannel();
      }
    },
    liveHeight(name, pos, height) {
      console.log(name+"|"+pos+"|"+height);
      let scrollY = $(".scroll_box_wrap").position().top - 20;
      let ebox = document.getElementsByClassName("scroll_box_wrap")[0];
      let _this = this;
      if ($(".scroll_box_wrap").is(":animated")) {
        return;
      }
      if (pos == "down") {
        if (height > 700) {
          let _temp = height - 640;
          scrollY -= _temp;
          $(".scroll_box_wrap")
            .stop(false, true)
            .animate({ top: scrollY + "px" }, 200, function() {
              _this.cheight.push(_temp);
              _this.pageSize++;
            });
        }
      } else if (pos == "up") {
        if (this.pageSize == 0) {
          $(".scroll_box_wrap")
            .stop(false, true)
            .animate({ top: "0px" }, 200);
          return;
        }
        scrollY += this.cheight[this.pageSize - 1];
        $(".scroll_box_wrap")
          .stop(true, true)
          .animate({ top: scrollY + "px" }, 200, function() {
            _this.cheight.splice(_this.pageSize - 1, 1);
            _this.pageSize--;
          });
      }
    }
  }
};
</script>



