<template>
  <div class="recommendBottom">
      <div v-for="(item,index) in elementList" :key="'rb_'+index" v-if="index<2"  :class="'rb_'+(index+1)">
            <lazy-image
                :src='item.elementImg'
                :placeholder='placeholder'
                :imgClass = "round"
                  />
      </div>
      <div class="rb_3">
          <div v-for="(item,index) in elementList" :key="('rb_3_')+index" v-if="index>1&&index<4" :class="('rb_3_'+(index-1))">
              <lazy-image
                :src='item.elementImg'
                :placeholder='placeholder'
                :imgClass = "round"
                  />
          </div>
      </div>
      <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
  </div>
</template>
<style scoped>
.recommendBottom {
  position: relative;
  display: flex;
  width: 1227px;
  height: 350px;
  margin-top: 30px;
}

.rb_1,
.rb_2,
.rb_3 {
  position: relative;
  width: 361px;
  height: 100%;
  margin-right: 15px;
}
.rb_1,
.rb_2 {
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.61);
}
.rb_3_2,
.rb_3_1,
.rb_1,
.rb_2 {
  border-radius: 15px;
  border: 1px solid #ffffff;
}
.rb_3_1,
.rb_3_2 {
  width: 360px;
  height: 158px;
  margin-bottom: 30px;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.61);
}
.rb_1.hover,
.rb_2.hover {
  border: 3px solid #ffffff;
  margin: -2px 13px -2px -2px;
  transform: scale(1.05);
  border-radius: 18px;
  z-index: 999;
}
.rb_3_1.hover,
.rb_3_2.hover {
  border: 3px solid #ffffff;
  margin: -2px -2px 28px -2px;
  transform: scale(1.05);
  border-radius: 18px;
  z-index: 999;
}
</style>

<script>
import c from "../../js/common.js";
import { mapGetters } from "vuex";
import { bp } from "../../js/ga.js";
import {xha} from '../../js/xha.js'
export default {
  props: ["initData", "itemSort"],
  name: "RecommendBottom",
  data() {
    return {
      placeholder: require("../../assets/img_loading_160x230.png"),
      initScore: require("../../assets/corner_mark.png"),
      isShow: false,
      eleId: "",
      timer: 0,
      pos: 0,
      itemNo: 0,
      round: "round",
      elementList: []
    };
  },
  created() {
    this.elementList = this.initData.elementList;
    this.eleId = this.initData.specialId; //默认数据第一个为组件内的id
  },
  mounted() {},
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.removeCss();
        this.pos = 0;
        this.itemNo = 0;
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "back");
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        const obj = this.elementList[this.itemNo];
        const elementId = this.elementList[this.itemNo].elementId;
        const trackId = this.category[this.navpos].catId + "|" + elementId;
        bp.categoryClick(
          this.category[this.navpos].catId,
          elementId,
          trackId,
          1,
          ""
        );
        let posId = "";
        const columnId = c.BPTypefilters(this.category[this.navpos].catCode);
        if(this.itemSort){
            posId =  this.itemSort + '0' +(this.itemNo+1);
        }
        c.setPosition(posId,columnId); // 保存推荐位置
        xha.pageLoad(this.category[this.navpos].catName,"",obj.elementName,elementId,posId,columnId); // xha 埋点
        if(obj.layout=="Subject_Home"){
             c.routerSkip(
              obj.jsonUrl,
              "12",
              obj.elementCode,
              this.$router
            );
          }else{
            c.routerSkip(
              obj.jsonUrl,
              obj.elementType,
              obj.elementCode,
              this.$router
            );
          }
      }
    },
    right() {
      if (this.itemNo >= 3) return;
      this.removeCss();
      this.itemNo++;
      this.addCss();
    },
    left() {
      if (this.itemNo == 0) return;
      this.removeCss();
      if (this.itemNo == 3) {
        this.itemNo = 1;
      } else {
        this.itemNo--;
      }
      this.addCss();
    },
    down() {
      if (this.itemNo == 2) {
        this.removeCss();
        this.itemNo++;
        this.addCss();
      }
    },
    up() {
      if (this.itemNo == 3) {
        this.removeCss();
        this.itemNo--;
        this.addCss();
      } else {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "up");
      }
    },
    addCss() {
      if (this.pos == 0) {
        if (this.itemNo == 0) {
          $(".rb_1").addClass("hover");
        } else if (this.itemNo == 1) {
          $(".rb_2").addClass("hover");
        } else if (this.itemNo == 2) {
          $(".rb_3_1").addClass("hover");
        } else if (this.itemNo == 3) {
          $(".rb_3_2").addClass("hover");
        }
      }
    },
    removeCss() {
      if (this.itemNo == 0) {
        $(".rb_1").removeClass("hover");
      } else if (this.itemNo == 1) {
        $(".rb_2").removeClass("hover");
      } else if (this.itemNo == 2) {
        $(".rb_3_1").removeClass("hover");
      } else if (this.itemNo == 3) {
        $(".rb_3_2").removeClass("hover");
      }
    },
    updateStast(bl, pos, isEnd) {
      this.isShow = bl;
      this.addCss();
      if (bl == true && isEnd == false) {
        let eleH = "";
        if (this.itemNo == 0 || this.itemNo == 2) {
          eleH = this.cEleOffsetTop(".rb_" + (this.itemNo + 1));
        } else {
          // eleH = this.cEleOffsetTop(".rb_3_" + (this.itemNo + 1));
          eleH = this.cEleOffsetTop(".rb_" + (this.itemNo + 1));
        }
        this.$emit("eleScroll", this.eleId, "down", eleH);
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset().top;
      let realHeight = fEleDistance + 350;
      return realHeight;
    }
  },
  computed: {
    ...mapGetters(["category", "navpos"])
  },
  watch: {
    initData() {
      this.elementList = this.initData.elementList;
      this.eleId = this.initData.specialId; //默认数据第一个为组件内的id
      this.$nextTick(function() {
        this.$lazyImages.loadImage();
      });
    }
  }
};
</script>



