<template>
    <div class="singleBar" :id="'single_'+eleId">
        <lazy-image
                :src='background'
                :placeholder='placeholder'
                :imgClass='round'
                  />
        <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
    </div>
</template>
<style scoped>
.singleBar {
  position: relative;
  width: 1112px;
  height: 101px;
  border-radius: 17px;
  border: 1px solid #707070;
  margin-top: 20px;
}
.singleBar.hover {
  border: 3px solid #ffffff;
  margin: 18px -2px -2px -2px;
  transform: scale(1.05);
}
</style>
<script>
import { mapGetters } from "vuex";
import c from "../../js/common.js";
import { bp } from "../../js/ga.js";
import {xha} from '../../js/xha.js'
export default {
  name: "SingleBar",
  props: ["initData", "itemSort"],
  data() {
    return {
      isShow: false,
      placeholder: require("../../assets/img_loading_352x190.png"),
      initScore: require("../../assets/corner_mark.png"),
      itemNo: 0,
      pos: 0,
      eleId: "",
      elementList: [],
      round: "round",
      background: ""
    };
  },
  created() {
    this.elementList = this.initData.elementList;
    this.eleId = this.initData.specialId;
    this.background = this.initData.backgroungImg;
  },
  mounted() {},
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "back");
      } else if (kc == "right") {
      } else if (kc == "left") {
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        try {
          const elementId = this.elementList[this.itemNo].elementId;
          const trackId = this.category[this.navpos].catId + "|" + elementId;
          bp.categoryClick(
            this.category[this.navpos].catId,
            elementId,
            trackId,
            1,
            ""
          );
          let posId = "";
          const columnId = c.BPTypefilters(this.category[this.navpos].catCode);
          if(this.itemSort){
              posId =  this.itemSort + '0' +(this.itemNo+1);
          }
          c.setPosition(posId,columnId); // 保存推荐位置
          xha.pageLoad(this.category[this.navpos].catName,"",this.elementList[this.itemNo].elementName,elementId,posId,columnId); // xha 埋点

        } catch (error) {}
        const eleDetail = this.elementList[this.itemNo];
        if(this.elementList[0].layout == "Subject_Home"){
            c.routerSkip(
              eleDetail.jsonUrl,
              "12",
              this.category[this.navpos].catCode,
              this.$router
            );
        }else{
            c.routerSkip(
              eleDetail.jsonUrl,
              eleDetail.elementType,
              {},
              this.$router
            );
        }
      }
    },
    up() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "up");
    },
    down() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "down");
    },
    addCss() {
      $("#single_" + this.eleId).addClass("hover");
    },
    removeCss() {
      $("#single_" + this.eleId).removeClass("hover");
    },
    updateStast(bl, pos, isEnd) {
      this.isShow = bl;
      this.addCss();
      if (bl) {
        let eleH = this.cEleOffsetTop("#single_" + this.eleId);
        this.$emit("eleScroll", this.eleId, pos, eleH);
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset().top;
      let realHeight = 0;
      realHeight = fEleDistance + 103;
      return realHeight;
    }
  },
  watch: {
    initData() {
      this.elementList = this.initData.elementList;
      this.eleId = this.initData.specialId;
      this.background = this.initData.backgroungImg;
    }
  },
  computed: {
    ...mapGetters(["category", "navpos"])
  }
};
</script>



