<<template>
  <div class="filtrate">
     <ul>
        <li id="tagName0" >类别筛选</li>
        <li v-for="(item,index) in tagName" v-if="index<5" :key="item.id" :id="('tagName'+(index+1))">{{item.elementName}}</li>
      </ul>
      <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
  </div>
</template>
<style >
.filtrate {
  position: relative;
  width: 1227px;
  height: 60px;
  margin-top: 30px;
}
.filtrate ul {
  clear: both;
  content: "";
  display: block;
}
.filtrate li {
  float: left;
  margin-right: 25px;
  width: 162px;
  text-align: center;
  font-size: 18px;
  height: 45px;
  line-height: 45px;
  background-color: rgba(255, 255, 255, 0.3);
  border: 1px solid #ffffff;
  border-radius: 16px;
  color: #ffffff;
  letter-spacing: 2px;
}
.filtrate li.hover {
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.55);
  border: 3px #fff solid;
  background-color: #0098bc;
  margin: -2px 23px -2px -2px;
  transform: scale(1.1);
  border-radius: 12px;
}
</style>
<script>
import { client as yh } from "../../js/client.js";
import { bp as bi } from "../../js/ga.js";
import c from "../../js/common.js";
import {xha} from '../../js/xha.js';
import { mapGetters } from "vuex";
export default {
  props: ["initData", "catCode", "catId", "itemSort"],
  name: "filtrate",
  data() {
    return {
      name: "filtrate",
      itemNo: 0,
      position: 0,
      pos: "",
      eleId: "",
      isShow: false,
      tagName: [
        {
          elementId: "",
          elementName: "",
          elementType: ""
        }
      ]
    };
  },
  computed: {},
  created() {
    this.tagName = this.initData.elementList;
    this.eleId = this.initData.specialId;
  },
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        let posId = '';
        let columnId = c.BPTypefilters(this.category[this.navpos].catCode);
        let eleName = "";
        let pageType = this.$route.name;
        if (pageType == "index") {
          try {
            let traceId =
              this.catId + "|" + this.tagName[this.itemNo - 1].elementId;
            let traceType = "1";
            bi.categoryClick(
              this.catId,
              this.tagName[this.itemNo - 1].elementId,
              3,
              1,
              this.catId,
              traceId,
              traceType
            );
          } catch (error) {
            console.log(error);
          }
        }
        if (this.itemNo == 0) {
          if(this.itemSort){
            posId =  this.itemSort + '01';
          }
          eleName = "类别筛选";
          this.$router.push({
            name: "filter",
            params: {
              catCode: this.catCode,
              categoryName: eleName,
              specialName: this.initData.specialName,
              topCatCode:this.category[this.navpos].catCode,
            }
          });
           c.setPosition(posId,columnId); // 保存推荐位置
           xha.pageLoad(this.category[this.navpos].catName,"",eleName,'',posId,columnId); // xha 埋点
          return false;
        } else {
          if(this.itemSort){
            posId =  this.itemSort + '0' +(this.itemNo+1);
          }
           c.setPosition(posId,columnId); // 保存推荐位置
          eleName = this.tagName[this.itemNo - 1].elementName;
          xha.pageLoad(this.category[this.navpos].catName,"",eleName,'',posId,columnId); // xha 埋点
        }
        const eleDetail = this.tagName[this.itemNo - 1];
        try {
          let playType = eleDetail.elementType;
          if (playType === "8" || 8 === playType) {
            c.routerSkip(
              eleDetail.jsonUrl,
              eleDetail.elementType,
              {},
              this.$router
            );
          } else {
            this.$router.push({
              name: "filter",
              params: {
                catCode: this.catCode,
                categoryName: eleName,
                specialName: this.initData.specialName,
                topCatCode:this.category[this.navpos].catCode,
              }
            });
          }
        } catch (error) {
          this.$router.push({
            name: "filter",
            params: {
              catCode: this.catCode,
              categoryName: eleName,
              specialName: this.initData.specialName,
              topCatCode:this.category[this.navpos].catCode,
            }
          });
        }
      }
    },
    back() {
      this.removeCss();
      this.isShow = false;
      this.itemNo = 0;
      this.$emit("pressDown", this.isShow, this.eleId, "back");
    },
    up() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "up");
    },
    down() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "down");
    },
    left() {
      if (this.itemNo == 0) return;
      this.removeCss();
      this.itemNo--;
      this.addCss();
    },
    right() {
      if (this.itemNo == this.tagName.length) return;
      if (this.itemNo > 4) return;
      this.removeCss();
      this.itemNo++;
      this.addCss();
    },
    addCss() {
      $("#tagName" + this.itemNo).addClass("hover");
    },
    removeCss() {
      $("#tagName" + this.itemNo).removeClass("hover");
    },
    updateStast(bl, pos, isEnd) {
      this.isShow = bl;
      this.pos = pos;
      if (bl) {
        this.addCss();
        let eleH = this.cEleOffsetTop("#tagName" + this.itemNo);
        this.$emit("eleScroll", this.name, this.pos, eleH);
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset().top;
      let realHeight = fEleDistance + 95;
      return realHeight;
    }
  },
  components: {},
  computed: {
    ...mapGetters(["category", "navpos", "isSouth"])
  },
  watch: {
    initData() {
      this.tagName = this.initData.elementList;
      this.eleId = this.initData.specialId;
    }
  }
};
</script>




