<template>
    <div class="vipTop">
        <div class="vt_1"><img :src="elementList[0].elementImg" /></div>
        <div class="vt_2">
            <div v-for="(item,index) in elementList" v-if="index>0&&index<3" :key="'vt_'+index" :class="'vt_2_'+index"><img :src="item.elementImg" /></div>
        </div>
        <!-- 独立付费专区 -->
        <div class="vt_3">
            <h3>独立付费专区</h3>
            <ul>
                <li>腾讯专区</li>
                <li>爱奇艺专区</li>
                <li>华数专区</li>
                <li>华数专区</li>
                <li>华数专区</li>
                <li>腾讯专区</li>
                <li>优酷专区</li>
                <li>飞狐专区</li>
                <li>腾讯专区</li>
            </ul>
        </div>
        <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
    </div>
</template>
<style scoped>
.vipTop {
  position: relative;
  display: flex;
  width: 1120px;
  height: 304px;
}
.vt_1 {
  position: relative;
  width: 540px;
  height: 304px;
  border-radius: 15px;
  border: 1px solid #95989a;
}
.vt_1 img {
  width: 100%;
  height: 100%;
  border-radius: 15px;
}
.vt_1.hover {
  border: 3px solid #ffffff;
  margin: -2px;
  transform: scale(1.05);
  z-index: 999;
  border-radius: 18px;
}
.vt_2 {
  position: relative;
  width: 270px;
  height: 304px;
  margin: 0px 12px 0px 15px;
}
.vt_3 {
  position: relative;
  width: 282px;
  height: 304px;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 15px;
}
.vt_2 {
  position: relative;
  display: flex;
  flex-direction: column;
}
.vt_2 img {
  width: 100%;
  height: 100%;
  border-radius: 15px;
}
.vt_2_1,
.vt_2_2 {
  position: relative;
  width: 270px;
  height: 142px;
  border: 1px solid #ffffff;
  margin-bottom: 16px;
  border-radius: 15px;
}
.vt_2_1.hover,
.vt_2_2.hover {
  position: relative;
  border-radius: 18px;
  border: 3px solid #ffffff;
  margin: -2px -2px 14px -2px;
  transform: scale(1.1);
  z-index: 999;
}
.vt_3 h3 {
  width: 100%;
  height: 50px;
  border-radius: 15px 15px 0px 0px;
  background-color: rgba(0, 0, 0, 0.22);
  box-shadow: 0px 0px 2px #000000;
  text-align: center;
  line-height: 50px;
  color: #76abeb;
  font-size: 20px;
  font-family: "Helvetica Neue,Helvetica Neue";
  font-weight: 400;
}
.vt_3 ul {
  width: 100%;
}
.vt_3 ul li {
  width: 135px;
  height: 50px;
  float: left;
  text-align: center;
  line-height: 50px;
}
.vt_3 ul li.hover {
  border: 3px solid #ffffff;
  border-radius: 15px;
  box-shadow: 0px 30px 40px #000000;
  background-color: #0098bc;
  margin: -3px;
  z-index: 999;
}
</style>
<script>
export default {
  name: "VipReCommendTop",
  props: ["initData", "itemSort"],
  data() {
    return {
      isShow: false,
      placeholder: require("../../assets/img_loading_352x190.png"),
      initScore: require("../../assets/corner_mark.png"),
      itemNo: 0,
      pos: 0,
      eleId: "",
      elementList: [],
      toScroll: -1,
      timer: 0
    };
  },
  created() {
    this.elementList = this.initData.elementList;
    this.eleId = this.initData.specialId;
  },
  mounted() {},
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
      }
    },
    back() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "back");
    },
    left() {
      if (this.pos == 0) {
        if (this.itemNo == 1 || this.itemNo == 2) {
          this.removeCss();
          this.itemNo = 0;
          this.addCss();
        }
      } else if (this.pos == 1) {
        if (this.itemNo == 0) {
          this.removeCss();
          this.pos = 0;
          this.itemNo = 1;
          this.addCss();
        } else {
          this.removeCss();
          this.itemNo--;
          this.addCss();
        }
      }
    },
    right() {
      if (this.pos == 0) {
        if (this.itemNo == 0) {
          this.removeCss();
          this.itemNo++;
          this.addCss();
        } else if (this.itemNo == 1) {
          this.removeCss();
          this.pos = 1;
          this.itemNo = 0;
          this.addCss();
        }
      } else if (this.pos == 1) {
        this.removeCss();
        this.itemNo++;
        this.addCss();
      }
    },
    down() {
      if (this.pos == 0) {
        if (this.itemNo == 1) {
          this.removeCss();
          this.itemNo++;
          this.addCss();
        } else {
          this.removeCss();
          this.isShow = false;
          this.$emit("pressDown", this.isShow, this.eleId, "down");
        }
      } else if (this.pos == 1) {
        this.removeCss();
        this.itemNo += 2;
        this.addCss();
      }
    },
    up() {
      if (this.pos == 0) {
        if (this.itemNo == 0 || this.itemNo == 1) {
          this.removeCss();
          this.isShow = false;
          this.$emit("pressDown", this.isShow, this.eleId, "up");
        } else {
          this.removeCss();
          this.itemNo = 1;
          this.addCss();
        }
      } else if (this.pos == 1) {
        if (this.itemNo == 0 || this.itemNo == 1) {
          this.removeCss();
          this.isShow = false;
          this.$emit("pressDown", this.isShow, this.eleId, "up");
        } else {
          this.removeCss();
          this.itemNo -= 2;
          this.addCss();
        }
      }
    },
    addCss() {
      if (this.pos == 0) {
        if (this.itemNo == 0) {
          $(".vt_1").addClass("hover");
        } else if (this.itemNo == 1 || this.itemNo == 2) {
          $(".vt_2_" + this.itemNo).addClass("hover");
        }
      } else if (this.pos == 1) {
        $(".vt_3 li")
          .eq(this.itemNo)
          .addClass("hover");
      }
    },
    removeCss() {
      if (this.pos == 0) {
        if (this.itemNo == 0) {
          $(".vt_1").removeClass("hover");
        } else if (this.itemNo == 1 || this.itemNo == 2) {
          $(".vt_2_" + this.itemNo).removeClass("hover");
        }
      } else if (this.pos == 1) {
        $(".vt_3 li")
          .eq(this.itemNo)
          .removeClass("hover");
      }
    },
    updateStast(bl, pos, isEnd) {
      this.isShow = bl;
      this.addCss();
      if (bl) {
        const eh = cEleOffsetTop(".vipTop");
        this.$emit("eleScroll", this.name, pos, eh);
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset().top;
      let realHeight = 0;
      realHeight = fEleDistance + 306;

      return realHeight;
    }
  },
  watch: {
    initData() {
      this.eleId = this.initData.specialId;
      this.elementList = this.initData.elementList;
    }
  }
};
</script>



