<template>
    <!-- 通用头部推荐 -->
    <div class="common_top">
      <div class="common_right_img" >
          <img :src="(top[0].elementImg)" />
      </div>
      <div class="common_right_news">
        <ul>
          <li v-for="(item,index) in top" v-if="index>0 && index<5" :key="item.id"  :id="('t'+index)" >
            <!-- <img :src="top[index].elementImg"/> -->
            <lazy-image
                :src='top[index].elementImg'
                :placeholder='placeholder'
                  />
          </li>
        </ul>
      </div>
      <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
    </div>
</template>
<style>
.common_top {
  position: relative;
  width: 1227px;
  /* margin-top: 10px; */
}
.common_top:after {
  clear: both;
  content: "";
  display: block;
}
.common_top img {
  cursor: pointer;
}
.common_right_img {
  display: inline-block;
  width: 540px;
  height: 305px;
  float: left;
  border-radius: 15px;
  box-sizing: border-box;
}
.common_right_img img {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  margin: 0px;
}
.common_right_news {
  /*display: inline-block;*/
  margin-left: 17px;
  width: 624px;
  float: left;
  box-sizing: border-box;
}
.common_right_news ul {
  width: 100%;
  height: 100%;

  box-sizing: border-box;
}
.common_right_news ul:after {
  clear: both;
  content: "";
  display: block;
}
.common_right_news li {
  float: left;
  width: 272px;
  height: 148px;
  box-sizing: border-box;
  margin-right: 13px;
  margin-top: 9px;
  box-sizing: border-box;
}
.common_right_news li .imglazy {
  border-radius: 15px;
}
.common_right_news li:nth-child(2) {
  margin-top: 3px;
}
.common_right_news li:nth-child(1) {
  margin-top: 3px;
}
.common_right_news li img {
  width: 100%;
  height: 100%;
  border-radius: 15px;
}
.common_right_img img.item_hover {
  margin-top: -4px;
  border: 4px solid #ffffff;
  border-radius: 18px;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.55);
  transform: scale(1.05);
  z-index: 999;
  position: relative;
}
.common_right_news li.item_hover:nth-child(3),
.common_right_news li.item_hover:nth-child(4) {
  border: 4px solid #ffffff;
  border-radius: 18px;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.55);
  transform: scale(1.15);
  z-index: 999;
  position: relative;
}
.common_right_news li.item_hover:nth-child(1),
.common_right_news li.item_hover:nth-child(2) {
  border: 4px solid #ffffff;
  border-radius: 18px;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.55);
  transform: scale(1.15);
  z-index: 999;
  position: relative;
}
</style>
<script>
import { mapGetters } from "vuex";
import c from "../../js/common.js";
import { bp } from "../../js/ga.js";
import {xha} from '../../js/xha.js'
export default {
  props: ["initData", "itemSort"],
  name: "CommonVipTop",
  data: function() {
    return {
      name: "CommonTop",
      placeholder: require("../../assets/img_loading_352x190.png"),
      initScore: require("../../assets/corner_mark.png"),
      itemNo: 0,
      position: 0,
      pos: "",
      types: "",
      eleId: "",
      top: [
        {
          elementId: "",
          elementImg: require("../../assets/img_loading_352x190.png"),
          elementName: "",
          elementType: "",
          jsonUrl: "",
          layout: "",
          score: ""
        }
      ],
      isShow: false
    };
  },
  created() {
    this.top = this.initData.elementList;
    this.eleId = this.initData.specialId;
  },
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        const elementId = this.top[this.itemNo].elementId;
        const trackId = this.category[this.navpos].catId + "|" + elementId;
        bp.categoryClick(
          this.category[this.navpos].catId,
          elementId,
          trackId,
          1,
          ""
        );
        let posId = "";
        const columnId = c.BPTypefilters(this.category[this.navpos].catCode);
        if(this.itemSort){
            posId =  this.itemSort + '0' +(this.itemNo+1);
        }
        c.setPosition(posId,columnId); // 保存推荐位置
        xha.pageLoad(this.category[this.navpos].catName,"",this.top[this.itemNo].elementName,elementId,posId,columnId); // xha 埋点
        const eleDetail = this.top[this.itemNo];
        if (eleDetail.elementType == "4") {
          if (eleDetail.layout == "Mix_Subject") {
            c.routerSkip(
              eleDetail.jsonUrl,
              "5",
              this.category[this.navpos],
              this.$router
            );
          } else if (eleDetail.layout == "Home" || eleDetail.layout == "Subject_Home") {
            c.routerSkip(
              eleDetail.jsonUrl,
              "12",
              this.category[this.navpos].catCode,
              this.$router
            );
          } else {
            c.routerSkip(
              eleDetail.jsonUrl,
              "4",
              this.category[this.navpos],
              this.$router
            );
          }
        } else if (
          eleDetail.elementType === "8" ||
          8 === eleDetail.elementType
        ) {
          c.routerSkip(
            eleDetail.jsonUrl,
            eleDetail.elementType,
            {},
            this.$router
          );
        } else {
          c.routerSkip(
            eleDetail.jsonUrl,
            eleDetail.elementType,
            this.category[this.navpos],
            this.$router
          );
        }
      }
    },
    back() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "back");
    },
    up() {
      if (this.position == 1) {
        if (this.itemNo == 1 || this.itemNo == 2) {
          this.removeCss();
          this.isShow = false;
          this.$emit("pressDown", this.isShow, this.eleId, "up");
        } else {
          this.removeCss();
          this.itemNo -= 2;
          this.addCss();
          let eleH = this.cEleOffsetTop("#t" + this.itemNo + " img");
          this.$emit("eleScroll", this.name, "up", eleH);
        }
      } else if (this.position == 0) {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "up");
      }
    },
    down() {
      let ele = "";
      if (this.position == 1) {
        if (this.itemNo == 3 || this.itemNo == 4) {
          this.removeCss();
          this.isShow = false;
          this.$emit("pressDown", this.isShow, this.eleId, "down");
        } else {
          this.removeCss();
          this.itemNo += 2;
          this.addCss();
          let eleH = this.cEleOffsetTop("#t" + this.itemNo + " img");
          this.$emit("eleScroll", this.name, "down", eleH);
        }
      } else if (this.position == 0) {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "down");
      }
    },
    left() {
      if (this.position == 0) {
        this.removeCss();
        this.position == 1;
        this.itemNo = 1;
        this.addCss();
      } else if (this.position == 1) {
        if (this.itemNo == 1) {
          this.removeCss();
          this.position = 0;
          this.itemNo = 0;
          this.addCss();
        } else if (this.itemNo == 2) {
          this.removeCss();
          this.itemNo--;
          this.addCss();
        } else if (this.itemNo == 3) {
          this.removeCss();
          this.position = 0;
          this.itemNo = 0;
          this.addCss();
        } else if (this.itemNo == 4) {
          this.removeCss();
          this.itemNo--;
          this.addCss();
        }
      }
    },
    right() {
      if (this.position == 0) {
        this.removeCss();
        this.position = 1;
        this.itemNo = 1;
        this.addCss();
      } else {
        if (this.itemNo >= 4) return;
        this.removeCss();
        this.itemNo++;
        this.addCss();
      }
    },
    removeCss() {
      if (this.position == 0) {
        $(".common_right_img img").removeClass("item_hover");
      } else {
        $("#t" + this.itemNo).removeClass("item_hover");
      }
    },
    addCss() {
      if (this.position == 0) {
        $(".common_right_img img").addClass("item_hover");
      } else {
        $("#t" + this.itemNo).addClass("item_hover");
      }
    },
    updateStast(bl, pos, isEnd) {
      this.isShow = bl;
      if (bl) {
        this.pos = pos;
        this.addCss();
        let eleH = "";
        if (this.position == 0) {
          eleH = this.cEleOffsetTop(".common_right_img img");
        } else {
          eleH = this.cEleOffsetTop("#t" + this.itemNo + " img");
        }
        this.$emit("eleScroll", this.name, this.pos, eleH);
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset().top;
      let realHeight = 0;
      if (this.position == 0) {
        realHeight = fEleDistance + 306;
      } else {
        realHeight = fEleDistance + 140;
      }
      return realHeight;
    }
  },
  computed: {
    ...mapGetters(["category", "navpos"])
  },
  watch: {
    initData() {
      this.top = this.initData.elementList;
      this.eleId = this.initData.specialId;
    }
  }
};
</script>



