<!--详情页——明星-->
<template>
  <div class="with_page" style="box-sizing: border-box;margin-top: 32px">
    <div class="page_con">
      <lazy-image v-for="(item,index) in specialList" v-if="index<7" :key="item.id" :id="('actor'+index)" class="Oval"
                :src='item.elementImg'
                :placeholder='placeholder'
                :imgClass="'circle'"
                :title = "item.elementName"
                  />
      <keyDo v-on:listenKeyCode="keyCode" v-if="isShow" ref="keyDo"></keyDo>
    </div>
  </div>
</template>
<style scoped="Mact">
.with_page {
  position: relative;
  width: 1280px;
  height: 150px;
  margin-top: 42px;
}
.page_con {
  position: absolute;
  width: 1280px;
  height: 150px;
}
.page_con:after {
  clear: both;
  content: "";
  display: block;
}
.page_con .Oval {
  background: #ffffff;
  width: 114px;
  height: 114px;
  border-radius: 100%;
  float: left;
  margin-right: 45px;
  margin-top: 15px;
  border: 2px solid rgba(255, 255, 255, 0.5);
}
.page_con .Oval img {
  width: 100%;
  height: 100%;
  border-radius: 100%;
}
.page_con .Oval .name {
  font-family: PingFangSC-Semibold;
  font-size: 20px;
  color: #ececec;
  text-align: center;
  margin-top: 10px;
}
.Oval.tran {
  border: 4px solid #ffffff;
  transform: scale(1.1);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
  margin: 13px 43px -2px -2px;
}
</style>

<script>
import { mapGetters } from "vuex";
import c from "../../js/common.js";
import { bp } from "../../js/ga.js";
import {xha} from '../../js/xha.js';
export default {
  props: ["initData", "jsonUrl", "itemSort"],
  name: "RecommendActorList",
  data() {
    return {
      placeholder: require("../../assets/img_loading_160x230.png"),
      initScore: require("../../assets/corner_mark.png"),
      itemNo: 0,
      isShow: false,
      specialList: [],
      eleId: ""
    };
  },
  created() {
    this.specialList = this.initData.elementList;
    this.eleId = this.initData.specialId; //默认数据第一个为组件内的id
    this.$nextTick(function() {
      this.$lazyImages.loadImage();
    });
  },
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        let posId = '';
        let columnId = c.BPTypefilters(this.category[this.navpos].catCode);
        if(this.itemSort){
          posId =  this.itemSort + '0'+(this.itemNo+1)
        }
         c.setPosition(posId,columnId); // 保存推荐位置
        xha.pageLoad(this.category[this.navpos].catName,"",this.specialList[this.itemNo].elementName,this.specialList[this.itemNo].elementId,posId,columnId); // xha 埋点
        const elementId = this.specialList[this.itemNo].elementId;
        const trackId = this.category[this.navpos].catId + "|" + elementId;
        bp.categoryClick(
          this.category[this.navpos].catId,
          elementId,
          trackId,
          1,
          ""
        );
        let url = this.specialList[this.itemNo].jsonUrl;
        this.$router.push({ name: "actor", params: { jsonUrl: url } });
      }
    },
    back() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "back");
    },
    left() {
      if (this.itemNo == 0) return;
      this.removeCss();
      this.itemNo--;
      this.addCss();
    },
    right() {
      if (this.itemNo >= 6) return;
      this.removeCss();
      this.itemNo++;
      this.addCss();
    },
    up() {
      $(".con_img").removeClass("tran");
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "up");
      this.$emit("eleScroll", this.name, "up");
    },
    down() {
      this.removeCss();
      this.addCss();
    },
    removeCss() {
      if (this.position == 0) {
        $(".con_img").removeClass("tran");
      } else {
        $("#actor" + this.itemNo).removeClass("tran");
      }
    },
    addCss() {
      if (this.position == 0) {
        $(".con_img").addClass("tran");
      } else {
        $("#actor" + this.itemNo).addClass("tran");
      }
    },
    updateStast(bl, pos) {
      this.isShow = bl;
      this.itemNo = 0;
      if (bl) {
        $("#actor" + this.itemNo).addClass("tran");
        if (pos == "down") {
          let eleH = this.cEleOffsetTop(".with_page .page_con");
          this.$emit("eleScroll", this.name, pos, eleH);
        }
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset();
      let realHeight = fEleDistance.top + 200;
      return realHeight;
    }
  },
  watch: {
    initData() {
      this.specialList = this.initData.elementList;
      this.eleId = this.initData.specialId; //默认数据第一个为组件内的id
      this.$nextTick(function() {
        this.$lazyImages.loadImage();
      });
    }
  },
  computed: {
    ...mapGetters(["category", "navpos"])
  }
};
</script>



