<template>
  <div class="box">
    <div class="scroll_box_wrap" :style="{backgroundImage:'url('+bgPhoto+')'}">
      <div class="scroll-box">
        <div class="top">
          <p class="name">中国IPTV-河南</p>
          <img src="../../../static/logo1.png">
          <img src="../../../static/logo2.png">
          <p class="date" v-if="this.else"></p>
          <p class="time" v-if="this.else"></p>
        </div>
        <div id="sliderSegmentedControl" class="yh-scroll-wrapper yh-slider-indicator yh-segmented-control yh-segmented-control-inverted">
            <div class="yh-scroll">
            <a :class="{'yh-control-item':true,'yh-active':isSearch==1}" id="search"><span class="jlt"></span>
            </a>
            <a v-for="(item,index) in navBar" ref='navItem' :class="{'yh-control-item':true,'yh-active':index===isActive,'yh-select':index===isSelect}" :catCode="item.catCode" :key="item.id" :id="'nav_'+index" >
              {{item.catName}}
            </a>
          </div>
          <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
        </div>
        <div class="alertMsg" v-if="position===1">
          <div class="tips"></div>
          <div class="msg">确定键搜索</div>
        </div>
        <router-view v-if="reload"  ref="childContent" v-on:navigation="IsNav" :categoryData="currentData" v-on:closeBg="closeBg" :topNum="itemNo"></router-view>
      </div>
    </div>
    <firm ref="firm" v-on:listernpopup="firm"></firm>
  </div>
</template>
<style scoped="category">
  .box {
    position: absolute;
    width: 1200px;
    padding-top: 0px;
    padding-left: 80px;
    height: 720px;
    overflow: hidden;
    /* background-color: #2D4158; */
    background-repeat: no-repeat;
    background-size: 100% 100%;
  }

  .scroll-box {
    position: relative;
    border-top: 1px solid transparent;
  }
  .scroll_box_wrap{
    position: absolute;
    left: 0px;
    padding-left: 80px;
    width: 1200px;
    height: 1670px;
    /* background: url('../../assets/testBj.jpg'); */
    background-size: 100% 100%;
    background-position: 100% 100%;
    background-repeat: no-repeat;
  }
  .yh-slider-indicator.yh-segmented-control {
    position: relative;
    bottom: auto;
    margin-left: -23px;
  }

  .yh-segmented-control.yh-scroll-wrapper {
    height: 70px;
    line-height: 70px;
  }
  .yh-segmented-control {
    font-size: 24px;
    font-weight: 400;
    position: relative;
    display: table;
    overflow: hidden;
    table-layout: fixed;
    border-radius: 3px;
    background-color: transparent;
  }

  .yh-scroll-wrapper {
    position: absolute;
    z-index: 2;
    top: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
    width: 100%;
  }
  .yh-segmented-control.yh-segmented-control-inverted {
    position: relative;
    overflow: hidden;
    border-radius: 0;
    padding-left: 12px;
  }

  .yh-segmented-control.yh-scroll-wrapper .yh-scroll {
    padding-left: 11px;
    height: 47px;
    white-space: nowrap;
    display:inline-block;
  }

  .systembar{
    height: 39px;
    line-height: 39px;
    width: 360px;
    float:right;
  }
  .systembar li{
    list-style: none;
    float: right;
    padding: 0 10px;
    background-color: rgba(255,255,255,0.2);
    margin-right: 1px;
  }
  .systembar li.hover{
    background-color: #0098BC;
  }
  .systembar li:nth-child(1){
    border-radius: 9px 0px 0px 9px;
  }
  .systembar li:nth-child(3){
    border-radius: 0px 9px 9px 0px;
  }
  .yh-scroll {
    position: absolute;
    z-index: 1;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
  }

  .yh-segmented-control.yh-segmented-control-inverted .yh-control-item {
    color: inherit;
  }
  .yh-segmented-control.yh-scroll-wrapper .yh-control-item {
    position: relative;
    display: inline-block;
    width: auto;
    padding: 0 26px;
  }

  .yh-segmented-control .yh-control-item {
    line-height: 39px;
    display: table-cell;
    overflow: hidden;
    width: 1%;
    -webkit-transition: background-color .1s linear;
    transition: background-color .1s linear;
    text-align: center;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-decoration: none;
    border-radius: 15px;
    background: 0 0;
  }
 .yh-segmented-control .yh-control-item:first-child {
   height: 38.75px;
 }

  .yh-control-item.yh-active,systembar li.active {
    background-color: #0098BC;
    border:3px solid #FFF;
    margin: -3px;
    box-shadow:0px 10px 20px rgba(0, 0, 0, 0.55);
  }

 

  .yh-control-item.yh-select {
    border-radius: 15px;
    font-size: 28px;
    margin: -4px;
    line-height: 48px;
    text-shadow:0px 0px 3px #FFFFFF;
  }
  .yh-control-item.yh-select::before{
    content: "";
    position: absolute;
    bottom: 43px;
    left: 19%;
    width: 60%;
    height: 0;
    border: 2px solid rgba(255, 255, 255, 1);
    border-radius: 5px;
    text-align: center;
  }
  .yh-control-item.yh-select::after {
    content: "";
    position: absolute;
    bottom: 35px;
    left: 47%;
    width: 0px;
    height: 0px;
    border: 4px solid transparent;
    border-top: 4px solid rgba(255, 255, 255, 1);
  }

  .select {
    background: rgba(255, 255, 255, 0.40);
    border-radius: 5px;
  }
  .top {
    margin: 20px 0;
  }
  .time{
    float: right;
    margin-right:15px;
    font-size: 20px;
    line-height: 26px;
  }
  .top img {
    height: 25px;
  }
    .date {
    float: right;
    font-size: 20px;
    line-height: 26px;
    margin-right: 80px;
    color: rgba(255,255,255,0.7);
  }

  .name {
    float: left;
    font-size: 20px;
    line-height: 26px;
    font-family: Helvetica Neue;
    font-weight: 700;
    margin-right: 25px;
  }

  .searchBtn{
    font-size: 30px;
  }
  .jlt {
    display: inline-block;
    vertical-align: middle;
    background: url(../../assets/icon.png) no-repeat -175px -245px;
    width: 35px;
    height: 35px;
    transform: scale(0.8);
  }
  .alertMsg {
    position: absolute;
    z-index:999;
    top:105px;

  }
  .alertMsg .tips {
    width: 10px;
    height: 10px;
    background-color: #FFFFFF;
    border-radius: 50%;
    margin-left: 20px;
    margin-bottom: 4px;
  }
  .alertMsg .msg {
    font-size: 20px;
    letter-spacing: 2px;
    line-height: 40px;
    font-family: "Helvetica Neue","Regular", Arial, Helvetica, sans-serif;
    font-weight: 500;
    text-align: center;
    color:#1F436E;
    background-color: #fff;
    padding: 0 20px;
    border-radius: 15px;
    border: #707070 1px solid;
  }
</style>
<script>
  import c from '../../js/common.js'
  import {mapGetters} from 'vuex'
  import {client as yh} from '../../js/client.js'
  import {HTTPUtil as api} from '../../fetch/api.js'
  import {South as south} from '../../fetch/south.js'
  import firm from '../../page/firm.vue'
  import {bp} from '../../js/ga.js'
  import {xha} from '../../js/xha.js'
  import Toast from '../../tool/toast/toast.js'
  export default {
    props:['auth'],
    name:'category',
    data: function () {
      return {
        name:'category',
        itemNo: 0,
        sNo:-1,
        else:true,
        position: 0,
        count: 0,
        navBar: {},
        isShow:false,
        currentData:{},
        isActive:0,
        isSelect:-1,
        keyPos:0,
        isOnce:false,
        isSearch:0,
        CLEARTIMES:null,
        HIDETIMES:null,
        reloadTime:0,
        reload:true,
        isLeave:false,
        isRefesh:false,
        bgPhoto:'',
        startTime:0
      }
    },created() {
      this.isLeave = false;
      window.refresh = this.refresh;
      const isrefesh = localStorage.getItem("isrefesh");
      if(isrefesh){
         console.log("BS版本更新，不需要展示开机海报");
         localStorage.removeItem("isrefesh");
      }else{
         $('.openImage').show();
         $('.openImage img').show();
         this.isShow = false;
      }
      this.openStb();
      this.ds();
      this.urls();
    }, mounted() {
      if(!c.isAllNull(c.getLocal("HenanRoot"))){
        this.navBar = this.category;
        let cData = this.filtrateRouter(this.itemNo);
        this.currentData = cData;
        this.bgPhoto = this.navBar[0].bgPhoto;
      };
    },
    activated(){
      c.removePosition(); // 移除推荐位ID
      this.isLeave = false;
      try {
          this.keyPos = parseInt(this.$route.query.pos);
          if(typeof(this.keyPos)=='Number'||typeof(this.keyPos)=='number'&&this.keyPos>=0){
            this.itemNo = this.keyPos;
          }
      } catch (error) {
          this.itemNo = 0;
      }

      try {
        let _this = this;
        let bsUrl = "";
        if (process.env.NODE_ENV === "development") { // 开发环境使用
            bsUrl = "/static/version.js";
        }else{
            bsUrl = "/henan/static/version.js";
        }
        $.get(bsUrl,function(data){
          console.log("BS页面版本："+data);
          const oldbsVersion =  localStorage.getItem("bs_version");
          if(oldbsVersion){
            if(oldbsVersion!==data){
              console.log("版本已更新,等待触发刷新页面");
              _this.isRefesh = true;
              localStorage.setItem("bs_version",data);
            }
          }else{
            console.log("缓存中无客户端版本");
            localStorage.setItem("bs_version",data);
          }
        },'script');
      } catch (error) {
        console.log("查询BS是否更新失败，暂不更新页面");
      }
     
      
    },computed: {
      ...mapGetters([
        'rootPath',
        'catList',
        'category',
        'isHTTPS',
        'behindParams',
        'httpRequestTime',
        'httpResponseTime',
        'loadStatus',
        'isSouth',
        'authStatus'
      ])
    },
    methods: {
      closeBg(flag){
        this.isShow = flag;
      },
        refresh(){
        if(this.isLeave==false){ // 当url为首页的时候执行初始化操作
          Toast("网络恢复");
          yh.resetPlayer();
          console.log("网络连接恢复!刷新首页");
          window.location.reload(); // 网络连接恢复
        }
      },
      openStb(){  // 开机获取栏目信息
        if (c.getLocal("Henan_Category")=="{}" || c.getLocal("Henan_Category")=="" || c.getLocal("Henan_Category")==null) {
              let _this = this;
              let rootPath = "";
              if(this.isSouth){
                rootPath = south.rootPath();
              }else{
                rootPath = api.rootPath();
              }
              let _h = rootPath.indexOf('https');
              if( _h >= 0){
                  this.$store.commit('GET_ISHTTPS',true);
                  localStorage.setItem("ISHTTPS", true);
              }
              this.startTime = new Date().getSeconds();
              console.log("接口请求开始时间："+(new Date()).format("yyyy-MM-dd hh:mm:ss.S"));
              api.getTheme(rootPath).then((json) => {
                console.log("接口请求响应结束时间："+(new Date()).format("yyyy-MM-dd hh:mm:ss.S"));
                _this.$store.commit('GET_PROGRAMA', json.data);
                localStorage.setItem("HenanRoot", JSON.stringify(json.data));
                var _cjson = json.data.jsonUrl;
                if (!c.isAllNull(_cjson)) { // 获取成功以后立马获取二级栏目数据
                  let curl =  _cjson + this.behindParams;
                  console.log("子栏目接口请求开始时间："+(new Date()).format("yyyy-MM-dd hh:mm:ss.S"));
                  api.get(curl).then((json)=>{
                    if (200 == json.code || "200" == json.code) {
                      console.log("子栏目接口请求结束时间："+(new Date()).format("yyyy-MM-dd hh:mm:ss.S"));
                        _this.navBar = json.data.catList;
                        _this.$store.commit('GET_CATEGORY', json.data.catList)
                        localStorage.setItem("Henan_Category", JSON.stringify(json.data.catList));
                        _this.$nextTick(function () {
                          $('.yh-control-item').eq(_this.itemNo+1).addClass('yh-active');
                          _this.currentData = _this.filtrateRouter(_this.itemNo);
                          _this.bgPhoto = _this.navBar[0].bgPhoto;
                        })
                      }
                      console.log("初始化主题及栏目数据成功,写入缓存成功");
                  },(json)=>{
                      Toast("请求二级栏目数据失败!");
                      console.log("请求二级栏目数据失败",json);
                  })
                }

              },(json)=>{
                 console.log("主题接口失败响应时间："+(new Date().getSeconds()-_this.startTime));
                 Toast("请求主题数据失败!");
                this.reloadTime++;
                if(this.reloadTime>1){
                   Toast("网络异常,请重启机顶盒");
                  console.log("requset theme interface time Out,three times loaded stop")
                  return false;
                }
                console.log("重试一次");
                this.openStb();
                // console.log("请求主题数据失败",json);
              }).catch(()=>{
                // console.log("导航栏目接口请求异常");
                // this.reloadTime++;
                // if(this.reloadTime>3){
                //   Toast("请求主题接口超时!");
                //   console.log("requset theme interface time Out,three times loaded stop")
                //   return false;
                // }
                // this.openStb();
                console.log("获取主题接口请求超时,重试");
              });
            } else {
              console.log("use Browser Local Cache");
              this.childCategory();
            }
      },
      childCategory(){
        var _this = this;
        var _cjson = this.catList.jsonUrl;
        if (!c.isAllNull(_cjson)) { // 获取成功以后立马获取二级栏目数据
                  let curl =  _cjson + this.behindParams;
                  console.log("更新栏目URL开始"+(new Date()).format("yyyy-MM-dd hh:mm:ss.S"));
                  api.get(curl).then((json)=>{
                  if (200 == json.code || "200" == json.code) {
                      console.log("更新栏目URL请求结束时间："+(new Date()).format("yyyy-MM-dd hh:mm:ss.S"));
                        _this.navBar = json.data.catList;
                        _this.$store.commit('GET_CATEGORY', json.data.catList)
                        localStorage.setItem("Henan_Category", JSON.stringify(json.data.catList));
                  }
                      console.log("初始化主题及栏目数据成功,写入缓存成功");
                  })
        }
      },
      keyCode(kc) {  // 键值捕获监听
      console.log('鉴权结果'+this.authStatus)
      console.log('健值监听:'+kc)
      console.log('AAA:'+  localStorage.getItem("AAA"))
        if(!this.authStatus){
          console.log('健值监听:'+kc)
          console.log('鉴权失败')
          return;
        }
        if (kc == "KeyBack") {
          this.back();
        } else if (kc == 'left') {
          this.left();
        } else if (kc == 'right') {
          this.right();
        } else if (kc == 'down') {
          this.down();
        }else if(kc == 'KeyEnter') {
          if(this.position==1){
            xha.pageLoad("搜索","",'搜索','','00',''); 
            c.routerSkip("","16",{},this.$router);
          }
        }
      },
      urls(){ //  url替换改写
        let url = window.location.pathname;
        let spstr = url.split("/");
        localStorage.setItem('type',spstr[spstr.length-1]);
      },
      back() {
        // this.closeApp();
        // yh.close_s_Video();
        // this.$refs.firm.updateStast(true);
        // this.isShow = false;
      }, left() {
            if(this.position==0){
              if(this.itemNo==0){
                xha.pageLoad("首页栏目切换","",'搜索',"","",'00'); 
                this.position=1;
                this.itemNo=0;
                this.isActive=-1;
                this.isSearch=1;
                this.isHiddenVideo();
              }else{
                  if(this.navBar[this.itemNo].catCode=="Live"){
                      yh.stopLivePlayer(); // 直播栏目切换星红按采集
                  }
                  this.itemNo--;
                  $('.systembar ul li').removeClass('hover');
                  let toLeft = this.getElementH(this.itemNo);
                  if(toLeft<60){
                      $('.yh-scroll').animate({'left':'+=120px'});
                  
              }
            }

        }
      }, right() {
        if(this.position==0){
          if(this.itemNo==(this.navBar.length-1))return;
              if(this.navBar[this.itemNo].catCode=="Live"){
                yh.stopLivePlayer(); // 直播栏目切换星红按采集
              }
              this.itemNo++;
              let toRight =  this.getElementH(this.itemNo);
              if(toRight>1170){
                $('.yh-scroll').animate({'left':'-=120px'});
              }
        }else{
            this.position=0;
            this.isSearch=0;
            this.isActive=0;
            this.changeIsActive(this.itemNo);
        }
      }, down() {
        if(this.loadStatus===false||this.loadStatus==undefined && (this.isShow==false)){  //键值未释放及数据未加载完不能向下
          // Toast("数据加载中,请稍等...");
          return false;
        }
        if(this.position==0){
          if (this.sNo <0 ){ // 键值处理逻辑
            this.isSelect = this.itemNo;
          }
          this.isActive  = -1;
          $('.systembar ul li').removeClass('hover');
        }else{
            this.isActive = -1;
            this.position =0;
            this.isSelect=0;
            this.isSearch=0;
        }
        this.isShow = false; // 关闭键值传递给父组件
        this.$refs.childContent.updateStast(true, "down", false);
      },IsNav(name, pos, show){
        this.isShow = show;
        if(this.position==0){
          this.isSelect = -1;
          this.isSearch=0;
          if(this.sNo==-1){
           this.isActive = this.itemNo;
          }
        }
      },scrollTop(show,name,pos){
        this.isShow = show;
        this.isSelect = -1;
        if(this.sNo==-1){
          this.isActive = this.itemNo;
        }
        $('.select').addClass('hover');
      },
      filtrateRouter(catCode){ // 查询栏目地址
        for(let i=0;i<this.navBar.length;i++){
            if(catCode == i){
               return this.navBar[i];
            }
        }
      },changeIsActive(index){ // 改变样式展示
        if(this.position==0){
            if(this.itemNo!=0){
                this.$lazyImages.removeAll();// 切换栏目需要移除懒加载图片队列
            }
            this.isActive  = index;
            clearTimeout(this.CLEARTIMES); // 防抖
            clearTimeout(this.HIDETIMES); // 防抖
            this.CLEARTIMES = null;
            this.HIDETIMES = null;
            if(this.itemNo!==0){
              this.$store.commit('GET_LOADSTATUS', false);
            }
            this.CLEARTIMES = setTimeout(() => {
                  this.leavePage();
                  this.isHiddenVideo();
                  this.$store.commit('GET_HTTPRESPONSETIME', new Date().getTime());
                  this.$store.commit('GET_NAVPOS', this.itemNo);
                  this.currentData = this.filtrateRouter(this.itemNo);
                  xha.pageLoad("首页栏目切换","",this.navBar[this.itemNo].catName,"","",c.BPTypefilters(this.navBar[this.itemNo].catCode)); // 星红按埋点
              },200);
            this.HIDETIMES = setTimeout(() => {
                    console.log("栏目切换关闭直播播放器");
                    this.isHiddenVideo();
                    this.getCatData();
            }, 100);
             
        }
      },
      getCatData(){ // 左右滑动需要重新请求栏目
        let _this = this;
          api.get(this.catList.jsonUrl+this.behindParams).then((response)=>{
            if (200 == response.code || "200" == response.code) {
                    console.log("栏目数据已更新！");
                    _this.navBar = response.data.catList;
                    _this.bgPhoto = this.navBar[0].bgPhoto;
                    _this.$store.commit('GET_CATEGORY', response.data.catList);
                    localStorage.setItem("Henan_Category", JSON.stringify(response.data.catList));
              }
          }).catch((error)=>{
              console.log("栏目数据刷新异常"+error);
          })
      },
      firm(bl){ // 按返回键弹窗
        if (bl){
          this.closeApp();
          yh.back();
        }else {
          this.isShow = true;
        }
      },
      ds() { // 时间倒计时
        setInterval(function () {
          var date = new Date();
          var h = date.getHours(); //获取小时
          var m = date.getMinutes(); //获取分钟
          var year =date.getFullYear()+'-'; //获取年
          var month =date.getMonth()+1+'-'; //获取月
          var day = date.getDate(); //获取日期
          $('.date').text(year+month+day);
          if (h<10){
            $('.time').text('0' +h +':'+  m );
          }else {
            $('.time').text(h +':'+  m  );
          }
          if (m<10){
            $('.time').text(h + ':0' + m );
          }else {
            $('.time').text(h + ':' + m );
          }
        }, 1000)
      },getElementH(id){
        let fEleDistance = $('#nav_'+id).offset().left;
        return fEleDistance;
      },closeApp(){
        try {
          const pageId = this.navBar[this.itemNo].catId;
          const keepTime = sessionStorage.getItem("appStartTime");
          bp.closeApp(pageId,keepTime);
        } catch (error) {
          console.log("采集关闭应用数据异常",e);
        }
      },isHiddenVideo(){ // 隐藏直播播放器
         if(this.navBar[this.itemNo].catCode != "Live"){
              try {
                  POPUP_PLAYER_ACTION.stopHide();
              } catch (error) {
                  console.log("POPUP_PLAYER_ACTION.stopHide,error");
              }
          }
      },leavePage(){
         const pageType = 1;
         const pageId = this.navBar[this.itemNo].catId;
         const trace_type = 1;
         const trace_id = this.navBar[this.itemNo].catId;
         let keepTime = 0;
         if(this.isOnce){
            keepTime = new Date().getTime() - this.httpResponseTime;
         }else{
           keepTime = new Date().getTime()-this.httpRequestTime;
           this.isOnce = true;
         }
         const special_type = "";
         bp.closPage(pageId, pageType, trace_type, trace_id,special_type,keepTime);
      }
    },
    components: {
        firm
    },watch:{
        itemNo(){
          if(this.itemNo==0){  // BS版本更新操作
               if(this.isRefesh){
                localStorage.setItem("isrefesh",true);
                window.location.reload(); // BS页面更新
                return false;
              }
          }
          this.changeIsActive(this.itemNo);
       
        },position(){
          if(this.position==1){
            const searchObj = {};
            searchObj.catCode="search";
            searchObj.catName ="搜索";
            searchObj.jsonUrl = "";
            searchObj.layout ="Search_box";
            this.currentData = searchObj;
          }
        },
      auth(){
        if(this.authStatus==false){
          console.log("鉴权不通过不可以操作!");
          this.isShow = this.authStatus;
        }else{
          console.log('鉴权通过，健值唤起')
          this.isShow = true;
        }
      }
    },beforeRouteLeave (to, from, next) {
      try {
        this.isLeave =true;
      } catch (error) {

      }finally{
        next();
      }
  }
  }
</script>



