<template>
	<div class="entertainment_recommend">
		<div v-for="(item,index) in initData" :key="item.id" :id="('dt_'+index)" class="item">
			<lazy-image
						:src='item.elementImg'
						:placeholder='placeholder'
						:imgClass = "round"
							/>
		</div>
		<keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
	</div>
</template>

<style scoped>
.entertainment_recommend{
	position: relative;
	width: 1142px;
	height: 170px;
	margin-bottom: 20px;
}
.entertainment_recommend .item{
	width: 360px;
	height: 170px;
	border-radius: 15px; 	
	background-color: #4b6676;
	float: left;
	margin-right: 20px;
}
.hover{
	border: 3px solid #ffffff;
	margin: -3px 0px -3px -3px;
	margin-right: 17px !important;
	-webkit-transform: scale(1.05);
	transform: scale(1.05);
	border-radius: 18px;
	z-index: 999;
}
.round{
	border-radius: 15px;
}
</style>
<script>
import c from "../../js/common.js";
import { bp } from "../../js/ga.js";
import { mapGetters } from "vuex";
import { client as yh } from "../../js/client.js";
import {xha} from '../../js/xha.js';
export default {
	props:['initData'],
	data(){
		return{
			placeholder: require("../../assets/img_loading_160x230.png"),
			isShow:false,
			itemNo:0,
			round:'round'
		}
	},
	created() {
	},
	methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
      
        }
			},
		back() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "back");
    },
    down() {
      if (!this.isEnd) {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.itemNo, this.eleId, "down", this.itemNo);
      }
    },
    up() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.itemNo, this.eleId, "up", this.itemNo);
    },
    left() {
      if (this.itemNo == 0) return;
      this.removeCss();
      this.itemNo--;
      this.addCss();
    },
    right() {
      if (this.itemNo == 2) return;
      this.removeCss();
      this.itemNo++;
      this.addCss();
    },
    addCss() {
      $("#dt_" + this.itemNo).addClass("hover");
    },
    removeCss() {
      $("#dt_" + this.itemNo).removeClass("hover");
    },
    updateStast(bl, pos, isEnd) {
      this.isShow = bl;
      // this.pos = pos;
      // this.isEnd = isEnd;
      if (pos == "down" && isEnd == false) {
        this.itemNo = 0;
      }
      this.addCss();
      if (bl == true && isEnd == false) {
        let eleH = this.cEleOffsetTop("#dt_" + this.itemNo);
        // console.log("组件距离文档的高度:"+eleH);
        this.$emit("eleScroll", this.name, this.pos, eleH);
      }
    },
    cEleOffsetTop(ele) {
      const fEleDistance = $(ele).offset().top;
      const realHeight = fEleDistance + 105;
      return realHeight;
    }
		},

}

</script>


