<template>
    <div class="searchBox">
        <div class="search bar5">
            <form>
                <input type="text" placeholder="请输入您要搜索的内容...">
                <button type="submit"></button>
            </form>
        </div>
          <div class="search_6">
            <ul>
              <li  v-for="(item,index) in specialList" v-if="index<6"  :key="item.id" :id="('as_'+eleId+'_'+index)"  >
                <!-- <img :src="item.elementImg"> -->
                <!-- <img :data-src="item.elementImg" v-imgLazy:asset="2" >  -->
                <lazy-image
                      :src='item.elementImg'
                      :placeholder='placeholder'
                      :initScore = 'initScore'
                      :score = "item.score"
                      :title = "item.elementName"
                      :imgClass="round"
                        />
              </li>
            </ul>
          </div>
          <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
    </div>
</template>
<style scoped>
.searchBox {
  position: relative;
  padding-top: 63px;
  height: 133px;
  width: 1130px;
}
.searchBox.search {
  padding: 30px 0;
}
form {
  position: relative;
  width: 480px;
  margin: 0 auto;
}
input,
button {
  border: none;
  outline: none;
}
input {
  width: 100%;
  height: 60px;
  padding-left: 13px;
}
button {
  height: 60px;
  width: 60px;
  cursor: pointer;
  position: absolute;
}
.bar5 input,
.bar5 button {
  background: transparent;
}
.bar5 input {
  border: 4px solid #f9f0da;
  border-radius: 15px;
  font-size: 20px;
}
.bar5 input::-webkit-input-placeholder {
  color: #ffffff;
}
.bar5 input::-moz-placeholder {
  color: #ffffff;
}
.bar5 input:-moz-placeholder {
  color: #ffffff;
}
.bar5 input:-ms-input-placeholder {
  color: #ffffff;
}
.bar5 button {
  top: 0;
  right: 0;
}
.bar5 button:before {
  content: "\f002";
  font-family: FontAwesome;
  font-size: 16px;
  color: #f9f0da;
}
.bar5 input.hover {
  border-color: #311c24;
}
.search_6 {
  position: relative;
  width: 1227px;
  min-height: 240px;
  margin-top: 150px;
}
.search_6 li {
  position: relative;
  float: left;
  margin-right: 30px;
  margin-bottom: 30px;
  width: 160px;
  height: 233px;
  z-index: 10;
  box-sizing: border-box;
}
.search_6 img {
  width: 100%;
  height: 100%;
}
.search_6 li.hover {
  margin: -3px 33px 27px -3px;
  border: 3px solid #ffffff;
  transform: scale(1.1);
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.55);
  border-radius: 18px;
}
.search_6 img,
.search_6 .imglazy {
  border-radius: 15px;
}
</style>
<script>
import c from "../../js/common.js";
import { bp } from "../../js/ga.js";
import { mapGetters } from "vuex";
import { client as yh } from "../../js/client.js";
import { HTTPUtil as api } from "../../fetch/api.js";
export default {
  props: ["initData","itemSort"],
  name: "SearchBox",
  data() {
    return {
      itemNo: 0,
      isShow: false,
      eleId: "SearchBox",
      pos: 0,
      isEnd: false,
      round: "round",
      placeholder: require("../../assets/img_loading_352x190.png"),
      initScore: require("../../assets/corner_mark.png"),
      url: "http://183.131.15.11:8380/?s=19|7&p=yhSearchRecommend&k=1&v=1",
      specialList: [
        {
          elementId: 2862,
          layout: "Detail_News",
          score: "6.4",
          elementImg:
            "http://183.131.15.11:8082/img/2018/5/24/182/182/20180524103459552mc9s7qiu.jpeg",
          jsonUrl:
            "http://183.131.15.11:8380?s=19|7&p=yhAssetDetail&k=1&v=1&assetId=2862&c=7",
          elementType: "1",
          elementName: "越战越勇"
        },
        {
          elementId: 2862,
          layout: "Detail_News",
          score: "6.4",
          elementImg:
            "http://183.131.15.11:8082/img/2018/5/24/182/182/20180524103459552mc9s7qiu.jpeg",
          jsonUrl:
            "http://183.131.15.11:8380?s=19|7&p=yhAssetDetail&k=1&v=1&assetId=2862&c=7",
          elementType: "1",
          elementName: "越战越勇"
        },
        {
          elementId: 2862,
          layout: "Detail_News",
          score: "6.4",
          elementImg:
            "http://183.131.15.11:8082/img/2018/5/24/182/182/20180524103459552mc9s7qiu.jpeg",
          jsonUrl:
            "http://183.131.15.11:8380?s=19|7&p=yhAssetDetail&k=1&v=1&assetId=2862&c=7",
          elementType: "1",
          elementName: "越战越勇"
        },
        {
          elementId: 2862,
          layout: "Detail_News",
          score: "6.4",
          elementImg:
            "http://183.131.15.11:8082/img/2018/5/24/182/182/20180524103459552mc9s7qiu.jpeg",
          jsonUrl:
            "http://183.131.15.11:8380?s=19|7&p=yhAssetDetail&k=1&v=1&assetId=2862&c=7",
          elementType: "1",
          elementName: "越战越勇"
        }
      ]
    };
  },
  created() {
    api.searchData().then(jsonData => {
      try {
        console.log(jsonData);
        this.specialList = jsonData.data.hotAssetList;
      } catch (error) {
        console.log("获取搜索数据异常");
      }
    });
  },
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        c.routerSkip("", "16", { type: false }, this.$router);
      }
    },
    back() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "back");
    },
    down() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "down");
    },
    up() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "up");
    },
    left() {
      if (this.itemNo == 0) return;
      this.removeCss();
      this.itemNo--;
      this.addCss();
    },
    right() {
      if (this.itemNo <= itemNo) return;
      this.removeCss();
      this.itemNo++;
      this.addCss();
    },
    addCss() {
      $(".bar5 input").addClass("hover");
    },
    removeCss() {
      $(".bar5 input").removeClass("hover");
    },
    updateStast(bl, pos, isEnd) {
      console.log(bl);
      this.isShow = bl;
      this.pos = pos;
      // this.isEnd = isEnd;
      // if (pos == "down"&& isEnd == false) {
      //   this.itemNo = 0;
      // }
      this.addCss();
    }
  }
};
</script>


