<template>
<!-- 大数据推荐 -->
    <div class="PopularLive">
      <h2>{{title}}</h2>
      <ul>
        <li  v-for="(item,index) in specialList"  :key="item.id" :id="('pl_'+eleId+'_'+index)"  >
          <lazy-image
                :src='item.elementImg'
                :placeholder='placeholder'
                :initScore = 'initScore'
                :score = "item.score"
                :title = "item.elementName"
                :imgClass = "round"
                  />
        </li>
      </ul>
        <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
    </div>
</template>
<style>
h2 {
  font-size: 30px;
  margin-bottom: 25px;
  font-weight: 400;
  letter-spacing: 2px;
  font-family: "Arial", "Microsoft YaHei", "黑体", "宋体", sans-serif;
}
.PopularLive {
  position: relative;
  width: 1227px;
  min-height: 620px;
  margin-top: 26px;
}
.PopularLive li {
  position: relative;
  float: left;
  margin-right: 32px;
  margin-bottom: 45px;
  width: 160px;
  height: 233px;
  z-index: 10;
  box-sizing: border-box;
}
.PopularLive img {
  width: 100%;
  height: 100%;
}
.PopularLive li.hover {
  margin: -4px 36px 49px -4px;
  border: 4px solid #fff;
  transform: scale(1.1);
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.55);
  border-radius: 18px;
}
.round {
  border-radius: 15px;
}
.circle {
  border-radius: 50%;
}
.appTitle {
  left: 0px;
}
</style>
<script>
import c from "../../js/common.js";
import { bp } from "../../js/ga.js";
import { mapGetters } from "vuex";
import {xha} from '../../js/xha.js'
export default {
  props: ["initData", "itemSort"],
  name: "BIGPopularLive",
  data() {
    return {
      title: "",
      name: "PopularLive",
      placeholder: require("../../assets/img_loading_160x230.png"),
      initScore: require("../../assets/corner_mark.png"),
      itemNo: 0,
      ClassNo: -1,
      position: 0,
      isShow: false,
      isEnd: false,
      specialList: [],
      eleId: "",
      timer: null,
      pos: "",
      round: "round"
    };
  },
  beforeMount() {
    this.specialList = this.initData.elementList;
    this.eleId = this.initData.specialId; //默认数据第一个为组件内的id
    this.name = "PopularLive_" + this.initData.specialId; //默认数据第一个为组件名字
    this.title = this.initData.specialName; //默认标题
  },
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        if (this.timer != null) {
          clearInterval(this.timer);
        }
        const eleDetail = this.specialList[this.itemNo];
        const elementId = this.specialList[this.itemNo].elementId;
        const trackId = this.category[this.navpos].catId + "|" + elementId;
        bp.categoryClick(
          this.category[this.navpos].catId,
          elementId,
          trackId,
          1,
          ""
        );
        let posId = "";
        const columnId = c.BPTypefilters(this.category[this.navpos].catCode);
        if(this.itemSort){
             if(this.itemNo>9){
                          posId =  this.itemSort  + (this.itemNo+1);
              }else{
                          posId =  this.itemSort + '0' +(this.itemNo+1);
              }
          }
        c.setPosition(posId,columnId); // 保存推荐位置
        xha.pageLoad(this.category[this.navpos].catName,"",eleDetail.elementName,elementId,posId,columnId); // xha 埋点
        c.routerSkip(
          eleDetail.jsonUrl,
          eleDetail.elementType,
          this.category[this.navpos],
          this.$router
        );
      }
    },
    back() {
      if (this.timer != null) {
        clearInterval(this.timer);
      }
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "back");
    },
    down() {
      // console.log("down:"+this.isEnd);
      if (this.itemNo <= 5) {
        this.removeCss();
        this.itemNo += 6;
        this.addCss();
        let eleH = this.cEleOffsetTop("#pl_" + this.eleId + "_" + this.itemNo);
        this.$emit("eleScroll", this.name, "down", eleH);
      } else {
        if (!this.isEnd) {
          this.removeCss();
          this.isShow = false;
          this.$emit("pressDown", this.isShow, this.eleId, "down");
        }
      }
    },
    up() {
      if (this.itemNo > 5) {
        this.removeCss();
        this.itemNo -= 6;
        this.addCss();
        let eleH = this.cEleOffsetTop("#pl_" + this.eleId + "_" + this.itemNo);
        this.$emit("eleScroll", this.name, "up", eleH);
      } else {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "up");
      }
    },
    left() {
      if (this.itemNo == 0 || this.itemNo == 6) return;
      this.removeCss();
      this.itemNo--;
      this.addCss();
    },
    right() {
      if (this.itemNo == 11 || this.itemNo == 5) return;
      this.removeCss();
      this.itemNo++;
      this.addCss();
    },
    addCss() {
      $("#pl_" + this.eleId + "_" + this.itemNo).addClass("hover");
      let p_title = $(
        "#pl_" + this.eleId + "_" + this.itemNo + " .s-title-name"
      ).text();
      if (p_title.length >= 10) {
        let id = "#pl_" + this.eleId + "_" + this.itemNo;
        let speed = 40;
        let that = this;
        let tab = document.querySelector(id + " .s-title");
        let tab1 = document.querySelector(id + " .s-title-name");
        let tab2 = document.querySelector(id + " .s-title-temp");
        tab.style.textOverflow = "clip";
        tab2.innerHTML = tab1.innerHTML;
        this.timer = setInterval(function() {
          that.Marquee(tab, tab1, tab2);
        }, speed);
      }
    },
    removeCss() {
      $("#pl_" + this.eleId + "_" + this.itemNo).removeClass("hover");
      if (this.timer != null) {
        clearInterval(this.timer);
        this.timer = null;
        let id = "#pl_" + this.eleId + "_" + this.itemNo;
        let tab = document.querySelector(id + " .s-title");
        tab.scrollLeft = 0;
        tab.style.textOverflow = "ellipsis";
      }
    },
    updateStast(bl, pos, isEnd) {
      this.isShow = bl;
      this.pos = pos;
      this.isEnd = isEnd;
      if (pos == "down" && this.itemNo > 5 && isEnd == false) {
        this.itemNo = 0;
      }
      this.addCss();
      if (bl == true && isEnd == false) {
        let eleH = this.cEleOffsetTop("#pl_" + this.eleId + "_" + this.itemNo);
        // console.log("组件距离文档的高度:"+eleH);
        this.$emit("eleScroll", this.name, this.pos, eleH);
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset().top;
      let realHeight = fEleDistance + 260;
      return realHeight;
    },
    Marquee(tab, tab1, tab2) {
      if (tab2.offsetWidth - tab.scrollLeft <= 0) {
        // tab.scrollLeft -= tab1.offsetWidth;
        tab.scrollLeft = 0; // 上面的写法会导致滚动时突然抖动
      } else {
        tab.scrollLeft++;
      }
    }
  },
  computed: {
    ...mapGetters(["category", "navpos"])
  },
  watch: {
    initData() {
      this.specialList = this.initData.elementList;
      // 数据更新组件id也需要更新
      this.specialList = this.initData.elementList;
      this.eleId = this.initData.specialId; //默认数据第一个为组件内的id
      this.name = "PopularLive_" + this.initData.specialId; //默认数据第一个为组件名字
      this.title = this.initData.specialName; //默认标题
      this.$nextTick(function() {
        this.$lazyImages.loadImage();
      });
    }
  }
};
</script>



