<template>
  <div id="main">
      <component v-for="(item,index) in child" :is="item.ele" :key="index" v-on:pressDown="keepDo" :itemSort="'0'+(index+1)"
                 v-on:eleScroll="changeHeight" ref="childDo" :cat-code="catType" :initData="item.childData"></component>
      <div class="marqueeS" id="marqueeS" v-show="isNotice" v-if="notice!=''"> 
          <div class="scrollText" id="scrollText" ><span id="scroll_span">{{notice}}</span><span  id="scroll_span_copy" style="padding-left:25px;" v-show="isScroll" >{{notice}}</span></div>
          <div class="tips"></div>
      </div>
  </div>
</template>
<style scoped>
  .scroll_box_wrap{position: relative;}
  .marqueeS{position: fixed;left:80px;top:665px;max-width: 600px;height: 80px;display: flex;flex-direction: column;overflow: hidden;z-index:999}
  .scrollText{position: relative;left:0px;height:41px;max-width:576px;line-height: 41px ;color: #1F436E;font-size: 20px; background-color: #ffffff;border-radius: 15px;margin-bottom: 4px;font-family:"Helvetica Neue","Regular", Arial, Helvetica, sans-serif;font-weight: 500;padding: 0px 24px 0px 24px;overflow: hidden;text-overflow:ellipsis;white-space:nowrap; }
  .scrollText span{max-width: 576px;height:41px;letter-spacing: 2px;}
  .tips{width: 10px;height: 10px;background-color: #FFFFFF;border-radius: 50%;margin-left: 30px;}
</style>
<script>
import Vue from 'vue'
import {mapGetters} from 'vuex'
import {HTTPUtil as api} from '../fetch/api.js'
import {South as south} from '../fetch/south.js'
import c from '../js/common.js'
import {bp} from '../js/ga.js'
import {xha} from '../js/xha.js'
import LiveMain from './heNan/Category_Live_Main.vue'
import MySelf from './heNan/MySelf_HN'
import {client as yh} from '../js/client.js'
import Toast from '../tool/toast/toast.js'
import { debuglog } from 'util';
export default {
  props:['categoryData','topNum'],
  name:'Main',
  data(){
      return{
        componentsId: [],
        pageSize: 0,
        child: [],
        cheight:[],
        catType:'',
        isScroll:false,
        notice:"",
        isOnce:false,
        isNext:false,
        timeOutS:0,
        finally:false,
        isNotice:false,
        isLeave:false,
      }
  },created(){
      if(!this.categoryData.jsonUrl)return;
      this.initData(this.categoryData);
      this.catType = this.categoryData.catCode;
  },mounted(){
    this.appStart();
    this.$nextTick(function(){
      yh.pageFinish(true);// 通知apk页面加载完成
      setTimeout(() => {
              if(this.isSouth){ // 豫南跑马灯
              south.marqueeUrl().then((jsonData)=>{
                    try {
                        this.notice = jsonData.data.rollingMessage;
                        } catch (error) {
                          console.log("跑马灯获取异常");
                      }
              }).catch(()=>{
                        console.log("跑马灯获取失败!");
              })
            }else{
                  api.marqueeUrl().then((jsonData)=>{ // 豫北跑马灯
                      try {
                        this.notice = jsonData.data.rollingMessage;
                      } catch (error) {
                        console.log("跑马灯获取异常"+error);
                      }
                    }).catch(()=>{
                      console.log("跑马灯获取失败!");
                    })
            }
          $('.openImage').hide();
          this.$emit('closeBg',true);
          this.$store.commit('GET_AREACODE', yh.areaCode);
          this.isNotice = true;
        }, 4000);
    })
  },activated(){
    $('.loading').hide(); // 关闭loading状态
    yh.stop(false); // 关闭点播小窗口 20190119改为王浩要求改为false
    // if(this.indexKey){
    // 设置此状态是因为客户端在全屏页面点击首页时会短暂停留在详情页，会占用点播播放器
    //   console.log("通过首页键返回首页不关闭点播播放器"); 
    //   yh.stop(true); // 因2018.12.6 只升级筛选，暂停升级
    // }else{
     
    // }
    this.$store.commit('INDEXKEY', false); // 首页键值重置默认

    this.isNotice = false; // 关闭滚动条
    c.clearTrackPath();
    this.$nextTick(function(){
      if(this.isOnce){
          const traceId = this.category[this.navpos].catId;;
          const traceType = 1;
          const pageType = 1;
          const pageId = this.category[this.navpos].catId;
          const parent_page_type = 0;
          const parent_page_id = 0;
          const special_type = "";
          const pageName = this.category[this.navpos].catName;
          const TYPE = c.BPTypefilters(this.category[this.navpos].catCode);
          if(this.httpResponseTime==0)return;
          let loadTime  = new Date().getTime()-this.httpResponseTime;
          bp.loadPage(traceId, traceType, pageType, pageId, parent_page_type, parent_page_id,special_type,loadTime);
     }
    });
  },updated(){
      this.$nextTick(function() {
        this.$lazyImages.loadImage();
       })
  },components:{LiveMain,MySelf}
  ,methods:{
       initData(purl){  // 请求栏目接口第一次拿3个组件
       console.log("数据加载状态:"+this.loadStatus);
        if(purl.jsonUrl==undefined||purl.jsonUrl==""){
          return false;
        }
        document.title =  purl.catName;
        let url = "";
        if(purl.catCode==="Live"){
           url = purl.jsonUrl+"&returnType=jsonp&psize=3&pindex=0&clientType=bs&playType="+yh.stbType+"&areaCode="+yh.areaCode;
           console.log("设置单播组播请求地址^^^^^^^"+url);
        }else if(purl.catCode==="Myself" ||purl.layout==="Category_Myself"){
             this.child = [];
             this.componentsId = [];
             this.child.push({ele: 'MySelf',childData:{}});
             this.componentsId.push('MySelf');
             setTimeout(() => {
               this.$store.commit('GET_LOADSTATUS', true); // 修改加载状态
               $('.loading').hide(); // 关闭loading状态
             }, 100);
             return false;
        }else{
           url = purl.jsonUrl+"&returnType=jsonp&psize=4&pindex=0";
        }
        // 缓存获取开始
        
        try {
            console.log("优先使用本地缓存");
            var templateData = c.getLocal(this.category[this.navpos].catId);
            if(templateData){
              var templateJson = JSON.parse(templateData);
              console.log("该栏目存在缓存数据");
              if(templateJson){
                  try {    
                    if(templateJson.liveList!=undefined && this.category[this.navpos].catCode!="Live"){
                      return false; // 解决直播接口返回过慢导致重复刷新页面 
                    }else if(templateJson!=undefined&&this.category[this.navpos].catCode=="Myself"){
                      return false; // 解决其他栏目刷新导致我的栏目丢失 
                    }
                  } catch (error) {
                      console.log("直播数据处理异常");
                  }
                  let components = [];
                  let componentsName = [];

                  if(templateJson.liveList!=undefined&&this.category[this.navpos].catCode=="Live"){
                    yh.isLiveColumn(true); // 20190122 王浩需要增加直播栏目判断
                    components.push({ele: 'LiveMain', childData:templateJson});
                    componentsName.push('LiveMain');
                  }else if(templateJson.specialList!=undefined){
                    yh.isLiveColumn(false); // 20190122 王浩需要增加直播栏目判断，非直播栏目为false
                    const specialList = templateJson.specialList;
                    for (let i = 0; i < specialList.length; i++){ // 化繁为简，重新设计数据结构
                        let  templateName = specialList[i].layout;
                        const isComponent = this.registerComponent(templateName);  // 此方案可行
                        if(isComponent){
                              components.push({ele: templateName, childData: specialList[i]});
                              componentsName.push(specialList[i].specialId);
                        }
                    }
                  }
                  this.child = components;
                  this.componentsId = componentsName;
                  console.log("使用本地缓存成功！");
                  this.$nextTick(function() {
                    $('.loading').hide(); // 关闭loading状态
                    if(url.indexOf(this.category[this.navpos].jsonUrl)>=0){
                      console.info("url数据一致");
                      this.$store.commit('GET_LOADSTATUS', true); // 修改加载状态
                    }else{
                      console.error("url数据不一致,不允许键值按下");
                    }
                  })
                }
            }
            } catch (error) {
              Toast('本地缓存应用失败，重新请求。');
            }
        // 缓存获取结束
        api.get(url).then((response)=>{
          // console.log(response);
          if(response.code==200){
            try {    
              if(response.data.liveList!=undefined && this.category[this.navpos].catCode!="Live"){
                return false; // 解决直播接口返回过慢导致重复刷新页面 
              }else if(response.data!=undefined&&this.category[this.navpos].catCode=="Myself"){
                return false; // 解决其他栏目刷新导致我的栏目丢失 
              }
            } catch (error) {
                console.log("直播数据处理异常");
            }

            var templateData = c.getLocal(this.category[this.navpos].catId);
            // console.log(templateData);
            c.setLocal(this.category[this.navpos].catId,JSON.stringify(response.data)); // 存储成功的记录
            if(!templateData){
              console.log('用户此栏目本地无缓存，优先使用接口数据');
                let components = [];
                let componentsName = [];

                if(response.data.liveList!=undefined&&this.category[this.navpos].catCode=="Live"){
                  yh.isLiveColumn(true); // 20190122 王浩需要增加直播栏目判断
                  components.push({ele: 'LiveMain', childData:response.data});
                  componentsName.push('LiveMain');
                }else if(response.data.specialList!=undefined){
                  yh.isLiveColumn(false); // 20190122 王浩需要增加直播栏目判断，非直播栏目为false
                  const specialList = response.data.specialList;
                  for (let i = 0; i < specialList.length; i++){ // 化繁为简，重新设计数据结构
                      let  templateName = specialList[i].layout;
                      const isComponent = this.registerComponent(templateName);  // 此方案可行
                      if(isComponent){
                            components.push({ele: templateName, childData: specialList[i]});
                            componentsName.push(specialList[i].specialId);
                      }
                  }
                }
                this.child = components;
                this.componentsId = componentsName;
                this.$nextTick(function() {
                  $('.loading').hide(); // 关闭loading状态
                  // console.log("当前url"+this.category[this.navpos].jsonUrl);
                  // console.log("请求数据url"+url);
                  if(url.indexOf(this.category[this.navpos].jsonUrl)>=0){
                    console.info("url数据一致");
                    this.$store.commit('GET_LOADSTATUS', true); // 修改加载状态
                  }else{
                    console.error("url数据不一致,不允许键值按下");
                  }
                })
            }
           
          }else{
            console.log("栏目接口请求异常："+response.code);
            Toast("栏目接口请求异常："+response.code);
            return false;
          }
         
         
        },(response)=>{
            // $('.loading').show(); // 打开loading状态
            console.log("栏目推荐数据获取超时重新获取",response);
            this.timeOutS++;
            if(this.timeOutS>1){
              this.timeOutS = 0;
              console.log('request programa Data error, reload three times  faill ');
              return false;
            }
            this.initData(this.categoryData);

        })
      },NextPage(purl){ // 请求下一页组件这次拿4个，最多2屏
        if(purl.jsonUrl===""||purl.jsonUrl===undefined)return;
        let url = purl.jsonUrl+"&returnType=jsonp&psize=4&pindex=4";
        api.get(url).then((response)=>{
          if(response.data==null)return;
          if(response.data.liveList!=undefined){
            
          }else{
            const specialList = response.data.specialList;
            this.isNext =true;
            for (let i = 0; i < specialList.length; i++){ // 化繁为简，重新设计数据结构
                let  templateName = specialList[i].layout;
                const isComponent = this.registerComponent(templateName);  // 此方案可行
                if(isComponent){
                      this.child.push({ele: templateName, childData: specialList[i]});
                      this.componentsId .push(specialList[i].specialId);
                }
            }
            this.$nextTick(function(){
               this.$lazyImages.loadImage();
            })
          }
        },(response)=>{
            console.log("栏目推荐数据第二屏获取超时重新获取",response);
            this.timeOutS++;
            if(this.timeOutS>1){
              this.timeOutS = 0;
              console.log('request programa Data error, reload three times  faill ');
              return false;
            }
            this.NextPage(this.categoryData);
        })
      },registerComponent(templateName){
          try { //  vue-loader 13.0.0升级以后不支持require vue文件需要单独在后面加default
              Vue.component(templateName,require(`./heNan/${templateName}.vue`).default);
              return true;
          } catch (error) {
              console.log("注册组件异常:"+error);
              return false;
        }
      },register(templateName){
          try {
               return import(`./heNan/${templateName}.vue`).then((component) => {
                    Vue.component(templateName, component);
                    return Promise.resolve(component);
                  })
           } catch (error) {
              console.log("注册组件异常:"+error);
           }
      },keepDo(bl, eleId, pos){
        //  console.log("组件上传信息");
        //  console.log(bl, eleId, pos);
         let childComponents = this.$refs.childDo;
         let componentsSize = childComponents.length - 1;

         for (let i = 0; i < childComponents.length; i++) { //遍历组件ref 给对应的组件排序
            if (eleId == childComponents[i].eleId) {// 当对应组件上下没有焦点时会抛出控制权，此时父组件需要根据子组件抛出的信息来决定控制权交给其他父组件
              let _num = this.componentsId.indexOf(eleId);//判断当前抛出键值的组件在数组中的位置
              // console.log("当前组件在数组中的第:"+this.componentsId.indexOf(eleId),this.componentsId.length);
              if(pos == 'back'){
                this.$emit('navigation', this.eleId,"back",true);
                $('.scroll_box_wrap').stop(false,true).animate({'top':'0px'},200);
              }
              if (pos == "down") {
                if(_num==componentsSize){
                  // console.error("已经到最后一个组件啦");
                 this.getChildRef(eleId).updateStast(true, pos, true);
                }else{
                  let _cdown = this.componentsId[_num+1];
                  this.getChildRef(_cdown).updateStast(true, pos, false);
                }
              } else if (pos == "up") {
                if(_num==0){
                   this.$emit('navigation',this.eleId,this.pos,true);
                   $('.scroll_box_wrap').stop(false,true).animate({'top':'0px'},200);
                }else {
                  let _cdown = this.componentsId[_num-1];
                  this.getChildRef(_cdown).updateStast(true, pos, false);
                }
                /* if (i == 0) {
                  childComponents[i - 1].updateStast(true, pos, false);
                } */
              }
              break; // 找到组件需终止循环foreach则不会，需要抛异常才会终止
            }
          }
      },changeHeight(name, pos, height){
            let scrollY  = $('.scroll_box_wrap').position().top-20; // 距离顶部窗口的距离需要减去
            let ebox = document.getElementsByClassName('scroll_box_wrap')[0];
            let _this =  this;
            if($('.scroll_box_wrap').is(":animated")){return}
            if(pos=='down'){
              if(height>700){
                let _temp = height-640;
                scrollY = Math.round(scrollY-_temp);
                console.log("down:"+scrollY);
                $('.scroll_box_wrap').stop(false,true).animate({'top': scrollY+'px'},200,function(){
                   _this.cheight.push(_temp);
                   _this.pageSize++;
                });
                  this.$nextTick(function() { // 刷新图片
                    this.$lazyImages.loadImage();
                  })
              }
            }else if(pos=='up'){
              if(this.pageSize==0){
                 this.pageSize = 0;
                 this.cheight =[];
                 $('.scroll_box_wrap').stop(false,true).animate({'top':'0px'},200);
                  return;
              };
              scrollY += this.cheight[this.pageSize-1];
              $('.scroll_box_wrap').stop(true,true).animate({'top':scrollY+'px'},200,function(){
                     _this.cheight.splice((_this.pageSize-1),1);
                     _this.pageSize--;
              });
            }

      },getChildRef(id){ // 根据元素ID查找ref中的组件
          let childComponents = this.$refs.childDo;
          for(let i=0;i<childComponents.length;i++){
              if(id==childComponents[i].eleId){
                return childComponents[i];
              }
          }
      },updateStast(bl,pos,isEnd){ // 移动到最近的组件
        if(this.isNext==false){
          this.NextPage(this.categoryData);// 下拉请求新的组件
        }
        this.pageSize = 0;
        this.cheight =[];
        let cs = this.$refs.childDo;
        for(let i=0;i<cs.length;i++){
          if(this.componentsId[0]==cs[i].eleId){
            cs[i].updateStast(true, pos, false);
            return;
          }
        }
      },Marquee(tab,tab1,tab2){
        if(tab2.offsetWidth - tab.scrollLeft <=10){
            tab.scrollLeft  = 0;
        }else{
           tab.scrollLeft++;
        }
      },marqueetoWord(){
          setTimeout(() => {
              console.log("关闭滚动消息");
              this.isNotice = false;
           }, 60000);
           let noticeLen =  this.notice.length;
           if(noticeLen>25){
            // console.log("跑马灯消息滚动");
            this.isScroll = true;
            let speed = 40;
            let that = this;
            let tab =   document.querySelector('#scrollText');
            let tab1 =  document.querySelector('#scroll_span');
            let tab2 =  document.querySelector('#scroll_span_copy');
            tab.style.textOverflow = 'clip';
            this.timer = setInterval(function(){
                that.Marquee(tab,tab1,tab2)
            },speed);
          }
      },appStart(){
        this.$nextTick(function(){
          try {// 开机启动进入首页埋点信息
            const startTime =  new Date().getTime();
            const cacheStartTime = sessionStorage.getItem('appStartTime');
            if(c.isAllNull(cacheStartTime)){// 第一次进入首页记录开机时间
              console.log("app启动完成，采集开机信息");
              sessionStorage.setItem('appStartTime',startTime);
              bp.openApp();
            }
          } catch (error) {
            console.log(error);
          }
        })
      },isHiddenVideo(){
         if(this.category[this.navpos].catCode != "Live"){
              try {
                  POPUP_PLAYER_ACTION.stopHide();
              } catch (error) {
                  console.log("POPUP_PLAYER_ACTION.stopHide,error");
              }
          }
      }
  },computed:{
      ...mapGetters([
        'category',
        'catList',
        'navpos',
        'httpResponseTime',
        'httpRequestTime',
        'loadStatus',
        'isSouth',
        'areaCode',
        'indexKey'
      ])
  },watch:{
      categoryData(){
        this.isNext = false;
        this.initData(this.categoryData);
        this.catType = this.categoryData.catCode;
        this.$lazyImages.loadImage();
        this.$nextTick(function(){
          this.isHiddenVideo();
          const traceId = this.category[this.navpos].catId;;
          const traceType = 1;
          const pageType = 1;
          const pageId = this.category[this.navpos].catId;
          const parent_page_type = 0;
          const parent_page_id = 0;
          const special_type = "";
          const pageName = this.category[this.navpos].catName;
          const TYPE = c.BPTypefilters(this.category[this.navpos].catCode);
          let loadTime =0;
          if(this.isOnce){
             loadTime = new Date().getTime()-this.httpResponseTime;
          }else{
             loadTime = window.performance.timing.domContentLoadedEventEnd-window.performance.timing.navigationStart;
          };
          this.isOnce = true;
          bp.loadPage(traceId, traceType, pageType, pageId, parent_page_type, parent_page_id,special_type,loadTime);
       });
      },notice(){
        // console.log("跑马灯信息返回成功");
        this.$nextTick(function(){
           this.marqueetoWord();
        })
      }
  },beforeRouteLeave (to, from, next) {
    try {
      if(to.path == '/detail'){
        window.localStorage.setItem('EnterType','1');
      }
      this.isLeave =true;
      window.clearInterval(this.timer);
      this.timer =null;
      c.setTrackPath(this.category[this.navpos].catId,1);
    } catch (error) {

    }finally{
      next();
    }
  }
}
</script>



