<template>
    <div class="superVipList">
        <h2>{{title}}</h2>
        <ul>
            <li v-for="(item,index) of elementList" :key="'superVipList'+index">
                <div class="superVipListPos">
                    <lazy-image :src='item.elementImg' :placeholder='placeholder' :title="item.elementName" :imgClass="round" :titleClass="superVipTitle" />
                </div>
            </li>
        </ul>
        <!-- <div class="superVip1">
            <div v-for="(item,index) in elementList" :key="('sv_'+index)" v-if="index>6&&index<9" class="superVipDouble"><img :src="item.elementImg"></div>
            <div  v-for="(item,index) in elementList" :key="('sv1_'+index)" v-if="index>8&&index<12" class="superVipSingle"><img :src="item.elementImg"></div>
        </div> -->
        <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
    </div>
</template>
<style scoped>
.superVipList h2 {
  margin-bottom: 25px;
}
.superVipDouble {
  width: 303px;
  height: 58px;
}
.superVipSingle {
  width: 145px;
  height: 58px;
}
.superVipDouble.hover,
.superVipSingle.hover {
  border: 4px solid #ffffff;
  border-radius: 18px;
  margin: -4px 12px -4px -4px;
  transform: scale(1.05);
  box-shadow: 0px 30px 40px rgba(0, 0, 0, 0.4);
}
.superVipList {
  position: relative;
  width: 1195px;
  min-height: 140px;
  margin-top: 20px;
}
.superVipListPos {
  position: relative;
  width: 145px;
  height: 58px;
  border-radius: 15px;
  color: #ffffff;
  display: flex;
  flex-direction: column;
}
.superVipListPos .imglazy {
  border-radius: 15px;
  border-style: none;
}
.superVipList li {
  display: inline-block;
  font-size: 18px;
  margin-right: 16px;
  margin-bottom: 15px;
  text-align: center;
  line-height: 50px;
  overflow: hidden;
  border-radius: 15px;
}
.superVipList li.hover {
  border: 4px solid #ffffff;
  border-radius: 20px;
  margin: -4px 12px 11px -4px;
  transform: scale(1.05);
  box-shadow: 0px 30px 40px rgba(0, 0, 0, 0.4);
}
.superVipListPos div img {
  width: 100%;
  height: 100%;
  border-radius: 15px 15px 0px 0px;
}
</style>

<script>
import { mapGetters } from "vuex";
import c from "../../js/common.js";
import { bp } from "../../js/ga.js";
import {xha} from '../../js/xha.js'
export default {
  name: "SuperVipListSix", //  合作专区 6
  props: ["initData", "itemSort"],
  data() {
    return {
      isShow: false,
      placeholder: require("../../assets/img_loading_352x190.png"),
      initScore: require("../../assets/corner_mark.png"),
      itemNo: 0,
      pos: 0,
      eleId: "",
      elementList: [],
      round: "round",
      superVipTitle: "appTitle"
    };
  },
  created() {
    this.elementList = this.initData.elementList;
    this.eleId = this.initData.specialId;
    this.title = this.initData.specialName; //默认标题
  },
  mounted() {},
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        const elementId = this.elementList[this.itemNo].elementId;
        const trackId = this.category[this.navpos].catId + "|" + elementId;
        bp.categoryClick(
          this.category[this.navpos].catId,
          elementId,
          trackId,
          1,
          ""
        );
        const eleDetail = this.elementList[this.itemNo];
        let posId = "";
        const columnId = c.BPTypefilters(this.category[this.navpos].catCode);
        if(this.itemSort){
          if(this.itemNo>9){
            posId =  this.itemSort  + (this.itemNo+1);
          }else{
            posId =  this.itemSort + '0' +(this.itemNo+1);
          }
        }
        c.setPosition(posId,columnId); // 保存推荐位置
        xha.pageLoad(this.category[this.navpos].catName,"",eleDetail.elementName,elementId,posId,columnId); // xha 埋点
        try {
          let playType = eleDetail.elementType;
          if (playType === "8" || 8 === playType) {
            c.routerSkip(
              eleDetail.jsonUrl,
              eleDetail.elementType,
              {},
              this.$router
            );
          } else {
            c.routerSkip(
              eleDetail.jsonUrl,
              "12",
              eleDetail.elementCode,
              this.$router
            );
          }
        } catch (error) {
          c.routerSkip(
            eleDetail.jsonUrl,
            "12",
            eleDetail.elementCode,
            this.$router
          );
        }
      }
    },
    back() {
      this.removeCss();
      this.itemNo = 0;
      this.pos = 0;
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "back");
    },
    left() {
      if (this.itemNo == 0) return;
      this.removeCss();
      this.itemNo--;
      this.addCss();
    },
    right() {
      if (this.itemNo === this.elementList.length - 1) return;
      this.removeCss();
      this.itemNo++;
      this.addCss();
    },
    up() {
      if (this.itemNo < 7) {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "up");
      } else {
        this.removeCss();
        this.itemNo -= 7;
        this.addCss();
      }
    },
    down() {
      if (this.itemNo > Math.floor((this.elementList.length - 1) / 7) * 7 - 1) {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "down");
        return;
      }
      if (this.itemNo + 7 > this.elementList.length - 1) {
        this.removeCss();
        this.itemNo = this.elementList.length - 1;
        this.addCss();
      } else {
        this.removeCss();
        this.itemNo += 7;
        this.addCss();
      }
    },
    addCss() {
      if (this.pos == 0) {
        $(".superVipList li")
          .eq(this.itemNo)
          .addClass("hover");
      } else if (this.pos == 1) {
        if (this.itemNo == 0 || this.itemNo == 1) {
          $(".superVipDouble")
            .eq(this.itemNo)
            .addClass("hover");
        } else {
          $(".superVipSingle")
            .eq(this.itemNo - 2)
            .addClass("hover");
        }
      }
    },
    removeCss() {
      if (this.pos == 0) {
        $(".superVipList li")
          .eq(this.itemNo)
          .removeClass("hover");
      } else if (this.pos == 1) {
        if (this.itemNo == 0 || this.itemNo == 1) {
          $(".superVipDouble")
            .eq(this.itemNo)
            .removeClass("hover");
        } else {
          $(".superVipSingle")
            .eq(this.itemNo - 2)
            .removeClass("hover");
        }
      }
    },
    updateStast(bl, pos, isEnd) {
      this.isShow = bl;
      this.addCss();
      if (bl) {
        let eleH = this.cEleOffsetTop(
          ".superVipListPos :eq(" + this.itemNo + ")"
        );
        this.$emit("eleScroll", this.name, pos, eleH);
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset().top;
      let realHeight = 0;
      realHeight = fEleDistance + 100;
      return realHeight;
    }
  },
  watch: {
    initData() {
      this.eleId = this.initData.specialId;
      this.elementList = this.initData.elementList;
      this.title = this.initData.specialName; //默认标题
    }
  },
  computed: {
    ...mapGetters(["category", "navpos"])
  }
};
</script>



