<template>
	<div class="recomment_wrap">
    <div class="item_wrap" v-for="(item,index) in dataList" :key="index">
		  <RecommentItem v-on:pressDown="keepDo" v-on:eleScroll="sysHeight" :initData="dataList[index]" ref="entertainmentItem"></RecommentItem>
    </div>
    <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
	</div>
</template>

<style scoped>
.recomment_wrap{
	width: 1142px;
}
</style>

<script>
import RecommentItem from './Category_En_Rec_Item'
import { debug } from 'util';
import { deepEqual } from 'assert';
export default {
	props:['initData',"itemSort"],
  data() {
    return {
      dataList:[],
      isShow:false,
      itemNo:0,
      eleId:'',
    };
  },
  components: {},
  created() {
    this.dataList = this.split_array(this.initData.elementList,3)
    this.eleId = this.initData.specialId;
  },
  methods: {
    keepDo(bl, eleId, pos, itemNo) {
      debugger
      if (pos == "down") {
        if (eleId == "1") {
          // this.$refs.systemMenu2.updateStast(true, pos, false, itemNo);
        } else if (eleId == "2") {
          // this.$refs.systemMenu3.updateStast(true, pos, false, itemNo);
        } else if (eleId == "3") {
          // this.$refs.systemMenu4.updateStast(true, pos, true, itemNo);
        }
      } else if (pos == "up") {
        if (eleId == "4") {
          // this.$refs.systemMenu3.updateStast(true, pos, false, itemNo);
        } else if (eleId == "3") {
          // this.$refs.systemMenu2.updateStast(true, pos, false, itemNo);
        } else if (eleId == "2") {
          // this.$refs.systemMenu1.updateStast(true, pos, false, itemNo);
        } else if (eleId == "1") {
          // this.$emit("pressDown", false, "MySelf", "up");
        }
      } else if (pos == "back") {
        this.$emit("pressDown", false, "MySelf", pos);
      }
    },
    sysHeight(name, pos, height) {
      console.log(name, pos, height);
    },
    updateStast(bl, pos, isEnd) {
      // 移动到最近的组件
      if (isEnd == false) {
        this.$refs.entertainmentItem[0].updateStast(bl, pos, isEnd);
      }
    },
    split_array(arr, len){
      var a_len = arr.length;
      var result = [];    
      for(var i=0;i<a_len;i+=len){        
        result.push(arr.slice(i,i+len));    
      }    
        return result;
    },
	},
	components: {RecommentItem}
}
</script>


