<template>
  <div class="liveRecommendTop">
      <div class="lt_1">
          <div class="lt_1_1">
               <lazy-image v-for="(item,index) in elementList" :key="'lt_1_'+index" v-if="index>0&&index<3"
                    :src='item.elementImg'
                    :placeholder='placeholder'
                    :imgClass="round"
                  />
                <!-- <div v-for="(item,index) in elementList" :key="'lt_1_'+index" v-if="index>0&&index<3" class="imglazy">
                  <img :src='item.elementImg' width="100%" height="100%"  class="round"/>
                </div> -->
          </div>
          <div class="lt_1_2">
                  <div v-for="(item,index) in liveColumn"  :key="'lt_'+index"><span :class="('lt_1_2_')+index"></span><span>{{item.elementName}}</span></div>
          </div>
      </div>
      <div class="lt_2" id="lt_2">
          <img :src="liveInitBg"  />
      </div>
      <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
  </div>
</template>
<style scoped>
.liveRecommendTop {
  position: relative;
  width: 1170px;
  height: 320px;
  display: flex;
  justify-content: space-between;
  flex-flow: row-reverse;
}
.lt_1 {
  width: 600px;
  height: 310px;
  position: relative;
}
.lt_2 {
  width: 540px;
  height: 310px;
  margin-right: 30px;
}
.lt_1_1 {
  height: 250px;
  width: 580px;
}
.lt_1_2 {
  height: 60px;
  width: 590px;
}
.lt_1_1 div {
  width: 267px;
  height: 228px;
  display: inline-block;
  margin-right: 15px;
}
.lt_1_1 div.hover {
  border: 3px solid #ffffff;
  margin: -3px 12px -3px -3px;
  border-radius: 18px;
  transform: scale(1.05);
}
.lt_2 img {
  width: 100%;
  height: 100%;
}
.lt_2.hover {
  border: 3px solid #ffffff;
  margin: -3px 27px -3px -3px;
}
.lt_1_2 div {
  display: inline-block;
  width: 162px;
  height: 55px;
  margin-right: 30px;
  /* background-color: #4d5e71; */
   background-image: url('../../assets/img_loading_160x230.png');
  border: 1px #707070 solid;
  border-radius: 18px;
  color: #ffffff;
  text-align: center;
  line-height: 55px;
  font-size: 20px;
}
.lt_1_2 div.hover {
  border: 3px solid #ffffff;
  background: #0098bc;
  margin: -2px 28px -2px -2px;
  transform: scale(1.05);
}
.lt_1_2_0 {
  width: 35px;
  height: 35px;
  padding:5px 7px 8px 35px;
  background: url("../../assets/icon.png") no-repeat -245px -105px;
}

.lt_1_2_1 {
  width: 35px;
  height: 35px;
  padding:5px 7px 8px 35px;
  background: url("../../assets/icon.png") no-repeat -35px -175px;
}
.lt_1_2_2 {
  width: 35px;
  height: 35px;
  padding:5px 7px 8px 35px;
  background: url("../../assets/icon.png") no-repeat -105px -175px;
}
</style>
<script>
import { mapGetters } from "vuex";
import { bp } from "../../js/ga.js";
import c from "../../js/common.js";
import { client as yh } from "../../js/client.js";
import {xha} from '../../js/xha.js';
export default {
  name: "LiveRecommendTop",
  props: ["initData",'itemSort'],
  data() {
    return {
      isShow: false,
      placeholder: require("../../assets/img_loading_352x190.png"),
      initScore: require("../../assets/corner_mark.png"),
      liveInitBg:require("../../../static/bg.png"),
      itemNo: 0,
      pos: 0,
      eleId: "liveTopRec",
      elementList: [],
      round: "round",
      timer:0,
      name:'liveTopRec',
      liveColumn:[{elementName:'全部频道'},{elementName:'热门频道'},{elementName:'频道回看'}],
      enterType:1,
      playCsList:null,
      historyBack:{}, // 用户上一次的点播记录
      isCreate:false // 是否初始化
    };
  },
  created() {
    window.smallCallBack = this.smallCallBack;
    this.elementList = this.initData.recList;
    const liveUrl = this.initData.liveJsonUrl;
    this.isCreate = true;
    if(liveUrl){
      this.filterUrl(liveUrl);
    }
  },
  mounted() {
    const eleH =$(".lt_2").offset();
    // yh.updatePlayPosition(eleH.left,eleH.top,540,310);// 因客户端将窗口固定因此代码多余2018.09.17
    const historyLive = localStorage.getItem("historyLive");
    // console.log("本地历史记录："+historyLive);
    let liveUrl = null;
    if(c.isAllNull(historyLive)){
        liveUrl = this.elementList[0].jsonUrl;
        console.log("本地没有直播历史，使用推荐："+liveUrl);
    }else{
        this.historyBack = JSON.parse(historyLive);
        liveUrl = this.historyBack.jsonUrl;
        console.log("本地直播历史记录不为空，设置直播地址:"+liveUrl);
    }
    yh.isSendCsXhaLog(true); // 2019/01/22 王浩要求判断组播环境小屏切大屏不要发星红桉日志
    this.showVideo(liveUrl);
  },updated(){
    // if(!this.isShow){
    //   console.log("键值变化");
    //   yh.close_s_Video();
    // }
  },
  methods: {
    smallCallBack(data){ // 全屏切换小屏回调函数
     console.log('直播全屏切换小屏需要更新常看频道');
     let jsonObj = null;
     if(typeof(jsonObj)==="object"){
       jsonObj = data ;
     }else{
       jsonObj = JSON.parse(data);
     }
      this.$emit("pressDown", this.isShow, this.eleId, "updateChannel");
      if(jsonObj.jsonUrl){ // 判断返回的历史是否为空
          console.log("用户直播历史记录:"+JSON.stringify(jsonObj));
          localStorage.setItem("historyLive",JSON.stringify(jsonObj));
          this.historyBack = jsonObj;
          console.log("this.isShow=="+this.isShow);
          if(this.isShow){
            if(yh.stbType==="1"||1===yh.stbType){// 组播大小屏切换需要再播放一次
              if(jsonObj.jsonUrl!=null&&jsonObj.jsonUrl!=undefined){
                yh.isSendCsXhaLog(false); // 2019/01/22 王浩要求判断组播环境小屏切大屏不要发星红桉日志
                yh.isLiveStopXhaLog(false); // 2019/12/22 应王浩要求需要在退出直播告诉客户端的时候发送日志
                this.showVideo(jsonObj.jsonUrl);
              }
            }
          }
      }else{
        console.log("返回直播历史记录为空,使用默认推荐");
        localStorage.setItem("historyLive","");
        this.historyBack = null;
        // let liveUrl = this.elementList[0].jsonUrl;
        // this.showVideo(liveUrl);
      }
      
    },
    filterUrl(url){
      if(url.indexOf("clientType=bs")>=0){
       this.playCsList =  url.replace('clientType=bs','clientType=cs');
       console.log("直播节目单地址："+this.playCsList);
      }
    },
    keyCode(kc) {
     
      if (kc == "KeyBack") {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "back");
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        //  yh.close_s_Video();
       
        c.logCsSplice(4,"","","","",this.category[this.navpos].catCode,"","","","","");// B透传C埋点信息
        let posId = '';
        let columnId = c.BPTypefilters(this.category[this.navpos].catCode);
        if (this.pos == 0) {
          const eleDetail = this.elementList[this.itemNo];
          const elementId = this.elementList[this.itemNo].elementId;
          const trackId = this.category[this.navpos].catId + "|" + elementId;
          if(this.itemSort){
            posId =  this.itemSort + '0'+(this.itemNo+1)
          }
          xha.pageLoad(this.category[this.navpos].catName,"",eleDetail.elementName,this.elementList[this.itemNo].elementId,posId,columnId); // xha 埋点
          bp.categoryClick(this.category[this.navpos].catId,elementId,trackId,1,"");
          if(this.itemNo==0){
            // yh.LiveToFullScreen(); // 大小屏切换
             yh.isSendCsXhaLog(false); // 2019/01/22 王浩要求判断组播环境小屏切大屏不要发星红桉日志
             yh.isLiveStopXhaLog(false); // 2019/12/22 应王浩要求需要在退出直播告诉客户端的时候发送日志
            if(this.historyBack.elementType){
               yh.fullLivePlay(this.playCsList,this.historyBack.type,this.historyBack.elementType,this.historyBack.elementId,false,'',true,true);
            }else{
              yh.fullLivePlay(this.playCsList,3,eleDetail.elementType,eleDetail.elementId,false,'',true,true);
            }
             this.loading();
          }else if(this.itemNo==1){
             yh.isLiveStopXhaLog(false); // 2019/12/22 应王浩要求需要在退出直播告诉客户端的时候发送日志
             yh.isSendCsXhaLog(true); // 2019/01/22 王浩要求判断组播环境小屏切大屏不要发星红桉日志
            if(eleDetail.linkType==7){// 直播
              yh.fullLivePlay(this.playCsList,3,eleDetail.elementType,eleDetail.elementId,false,'',true,false);
            }else if(eleDetail.linkType==23){ // 回看节目单
              yh.fullLivePlay(this.playCsList,4,eleDetail.elementType,eleDetail.elementId,true,this.initData.recList[1].jsonUrl,true,true);
            }else{
              yh.fullLivePlay(this.playCsList,4,eleDetail.elementType,eleDetail.elementId,true,this.initData.recList[1].jsonUrl,true,true);
            }
            this.enterType=21;
            this.loading();
          }else if(this.itemNo==2){
             yh.isLiveStopXhaLog(false); // 2019/12/22 应王浩要求需要在退出直播告诉客户端的时候发送日志
             yh.isSendCsXhaLog(true); // 2019/01/22 王浩要求判断组播环境小屏切大屏不要发星红桉日志
            if(eleDetail.linkType==7){ // 直播
               yh.fullLivePlay(this.playCsList,3,eleDetail.elementType,eleDetail.elementId,false,'',true,false);
            }else if(eleDetail.linkType==23){ // 回看节目单
              yh.fullLivePlay(this.playCsList,4,eleDetail.elementType,eleDetail.elementId,true,this.initData.recList[2].jsonUrl,true,true);
            }else{
              yh.fullLivePlay(this.playCsList,4,eleDetail.elementType,eleDetail.elementId,true,this.initData.recList[2].jsonUrl,true,true);
            }
            this.enterType=22;
            this.loading();
          }
        } else if (this.pos == 1) {
          const eleDetail = this.liveColumn[this.itemNo];
          const elementId = "";
          const trackId = this.category[this.navpos].catId + "|" + elementId;
          if(this.itemSort){
            posId =  this.itemSort + '0'+(this.itemNo+4)
          }
          xha.pageLoad(this.category[this.navpos].catName,"",eleDetail.elementName,'',posId,columnId); // xha 埋点
          bp.categoryClick(
            this.category[this.navpos].catId,
            elementId,
            trackId,
            1,
            ""
          );
          yh.isSendCsXhaLog(false); // 2019/01/22 王浩要求判断组播环境小屏切大屏不要发星红桉日志
          yh.isLiveStopXhaLog(false); // 2019/12/22 应王浩要求需要在退出直播告诉客户端的时候发送日志
          if (this.itemNo == 0) {
            yh.fullLivePlay(this.playCsList, 3, "", "",false,'',true,true);
            this.enterType=3;
            this.loading();
          } else if (this.itemNo == 1) {
            yh.fullLivePlay(this.playCsList, 0, "", "",false,'',true,true);
            this.enterType=4;
            this.loading();
          } else if (this.itemNo == 2) {
            yh.fullLivePlay(this.playCsList, 4, "", "",false,'',true,true);
            this.enterType=5;
            this.loading();
          }
        }
      }
    },
     loading(){
             try{
               //加载yh埋点
               const BiJson = {};
               BiJson.cat_id = this.category[this.navpos].catId;  //所在栏目id
               BiJson.trace_id = c.getTrackPath(); //当前访问页面id路径、轨迹，以“|”分割
               BiJson.trace_type = c.getTrackPathType(); //当前访问页面路径、轨迹，以“|”分割的页面类型
               BiJson.parent_page_type = c.getParentPageType();
               BiJson.parent_page_id = c.getParentPageId(); //父级页面id
               BiJson.special_type = "";
               // BiJson.special_type =c.getParentPageType()=="8"?special_type:""; //专题也才传 专题未传参
               BiJson.recommend_place = this.category[this.navpos].catName; //未知
               BiJson.special_id = "";
               BiJson.cat_name = this.category[this.navpos].catName; //所在栏目名称
               BiJson.enterType =this.enterType; //进入点播方式 哪些是点播资源/嗨看资源？
               BiJson.filmType = "1"; //影片类型(0:嗨看 1: 点播) 我们都是点播
               BiJson.type="0"+c.getParentPageType();
               BiJson.typeId=this.category[this.navpos].catId=="202652"?"202652":"0";
               BiJson.pagePath=c.getTrackPathType().replace(/\|/gi, '_');
               BiJson.srcPagePath=c.getTrackPathType().slice(0,c.getTrackPathType().length-2).replace(/\|/gi, '_');
               yh.setBiJson(JSON.stringify(BiJson));
             } catch(error){
               console.log(error);
             }
          },

    left() {
      if (this.pos == 0) {
        if (this.itemNo == 0) return;
        this.removeCss();
        this.itemNo--;
        this.addCss();
      } else if (this.pos == 1) {
        if (this.itemNo == 0) {
          this.removeCss();
          this.pos = 0;
          this.itemNo = 0;
          this.addCss();
        } else {
          this.removeCss();
          this.itemNo--;
          this.addCss();
        }
      }
    },
    right() {
      if (this.pos == 0) {
        if (this.itemNo > 1) return;
        this.removeCss();
        this.itemNo++;
        this.addCss();
      } else if (this.pos == 1) {
        if(this.itemNo === 2) return;
        this.removeCss();
        if (this.itemNo == 2) {
          this.pos = 0;
        } else {
          this.itemNo++;
        }
        this.addCss();
      }
    },
    up() {
      if (this.pos == 0) {
        // this.hideVideo();
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "up");
      } else if (this.pos == 1) {
        this.removeCss();
        this.pos = 0;
        this.itemNo = 1;
        this.addCss();
      }
    },
    down() {
      if (this.pos == 0) {
        if (this.itemNo == 2 || this.itemNo == 1) {
          this.removeCss();
          this.pos = 1;
          this.itemNo = 0;
          this.addCss();
        } else if (this.itemNo == 0) {
          yh.isLiveStopXhaLog(true); // 2019/12/22 应王浩要求需要在退出直播告诉客户端的时候发送日志
          yh.isSendCsXhaLog(true); // 2019/01/22 王浩要求判断组播环境小屏切大屏不要发星红桉日志
          this.hideVideo();
          this.removeCss();
          this.isShow = false;
          this.$emit("pressDown", this.isShow, this.eleId, "down");
        }
      } else {
        this.removeCss();
        this.isShow = false;
        yh.isLiveStopXhaLog(true); // 2019/12/22 应王浩要求需要在退出直播告诉客户端的时候发送日志
        yh.isSendCsXhaLog(true); // 2019/01/22 王浩要求判断组播环境小屏切大屏不要发星红桉日志
        this.hideVideo();
        this.$emit("pressDown", this.isShow, this.eleId, "down");
      }
    },
    addCss() {
      if (this.pos == 0) {
        if (this.itemNo == 0) {
          $(".lt_2").addClass("hover");
        } else {
          $(".lt_1_1 div.imglazy").eq(this.itemNo-1).addClass('hover');
        }
      } else if (this.pos == 1) {
        $(".lt_1_2 div")
          .eq(this.itemNo)
          .addClass("hover");
      }
    },
    removeCss() {
      if (this.pos == 0) {
        if (this.itemNo == 0) {
          $(".lt_2").removeClass("hover");
        } else {
          $(".lt_1_1 div.imglazy").eq(this.itemNo-1).removeClass('hover');
        }
      } else if (this.pos == 1) {
        $(".lt_1_2 div")
          .eq(this.itemNo)
          .removeClass("hover");
      }
    },
    updateStast(bl, pos, isEnd) {
      this.isShow = bl;
      if (bl) {
        if (this.isShow) {
            if(isEnd!="init"){
              console.log("初始化小窗口");
              this.pos = 0;
              this.itemNo = 0;
              let liveUrl = this.elementList[0].jsonUrl;
              setTimeout(() => {
                this.showVideo(liveUrl);
              }, 500);
            }else{
              if(this.liveStatus){
                console.log("从导航条移动到直播小窗口需要打开直播小窗口");
                this.pos = 0;
                this.itemNo = 0;
                let liveUrl = this.elementList[0].jsonUrl;
                setTimeout(() => {
                  this.showVideo(liveUrl);
                }, 500);
              }
              console.log("不需要重新打开小窗口");
            }
        }
        this.addCss();
        let eleH = 0;
        if (this.pos == 1) {
          eleH = this.cEleOffsetTop(".lt_1_2 div:eq(" + this.itemNo + ")");
        } else {
          eleH = this.cEleOffsetTop(".lt_2");
        }
        this.$emit("eleScroll", this.name, pos, eleH);
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset().top;
      let realHeight = 0;
      realHeight = fEleDistance + 310;
      return realHeight;
    },
    hideVideo() {
      this.$store.commit('GET_LIVESTATUS', true); // 小窗口是否关闭
      yh.close_s_Video();
    },
    showVideo(url) {
      c.logCsSplice(4,"","","","",this.category[this.navpos].catCode,"","","","","");// B透传C埋点信息
      const eleDetail = this.elementList[this.itemNo];
      this.$store.commit('GET_LIVESTATUS', false); // 小窗口是否关闭
      if(this.historyBack.jsonUrl!=null&&this.historyBack.jsonUrl!=undefined){
        console.log("存在直播历史记录,使用历史播放");
        if(this.historyBack.elementId=="" ||this.historyBack.elementId==null ||this.historyBack.elementId==undefined){
          console.log("历史记录ID为空，使用默认推荐");
          yh.fullLivePlay(this.playCsList,3,eleDetail.elementType,eleDetail.elementId,false,'',false,false); // 当历史ID为空时，播放推荐资产
        }else{
          yh.fullLivePlay(this.playCsList,this.historyBack.type,this.historyBack.elementType,this.historyBack.elementId,false,'',false,false);
        }
        // yh.sVideo(this.historyBack.jsonUrl);
      }else{
        console.log("使用推荐直播资产");
        //  yh.sVideo(url);
        yh.fullLivePlay(this.playCsList,3,eleDetail.elementType,eleDetail.elementId,false,'',false,false);
      }


    }
  },
  computed: {
    ...mapGetters(["category", "navpos","liveStatus"])
  },
  watch: {
    initData() {
      this.elementList = this.initData.recList;
      const liveUrl = this.initData.liveJsonUrl;
      if(liveUrl){
        this.filterUrl(liveUrl);
      }
    }
  }
};
</script>


