<template>
  <div class="systemSetting2">
    <ul>
<li v-for="(item,index) in elementList" :key="index+'system'" :class="('systemSetting_'+eleId+'_'+index)">

        <div>
          <span :class="('jlt_')+index"></span>
          <span>{{item.elementName}}</span>
        </div>
      </li>
    </ul>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
  </div>
</template>
<style scoped>
.systemSetting2 {
  position: relative;
  width: 1104px;
  height: 105px;
  margin-bottom: 20px;
}

.systemSetting2 ul {
  display: flex;
  justify-content: space-around;
}

.systemSetting2 li:nth-child(1) {
  width: 726px;
  height: 105px;
  border-radius: 10px;
  background-color: #4b6676;
  text-align: center;
}

.systemSetting2 li:nth-child(2) {
  width: 356px;
  height: 105px;
  border-radius: 10px;
  background-color: #4b6676;
  text-align: center;
}

.systemSetting2 li:nth-child(2) span:nth-child(2) {
  padding-right: 20px;
}

.systemSetting2 li.hover {
  margin: -4px;
  border: 4px solid #fff;
  transform: scale(1.1);
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.55);
  border-radius: 18px;
}

li div {
  width: 182px;
  height: 50px;
  padding: 28px 84px 27px 90px;
  line-height: 50px;
}
div span:nth-child(2) {
  float: right;
  font-size: 20px;
}

.jlt_0,
.jlt_1 {
  float: left;
  vertical-align: middle;
  background: url("../../assets/spriteImg_50x50.png") 0 0 no-repeat;
  width: 50px;
  height: 50px;
}

.systemSetting_2_0 .jlt_0 {
  background-position: -159px 0;
}
.systemSetting_2_1 .jlt_1 {
  background-position: 0 -55px;
}
</style>
<script>
import c from "../../js/common.js";
import { bp } from "../../js/ga.js";
import { mapGetters } from "vuex";
import { client as yh } from "../../js/client.js";
export default {
  props: ["initData", "itemSort"],
  name: "SystemList2",
  data() {
    return {
      itemNo: 0,
      isShow: false,
      elementList: [],
      eleId: "",
      pos: 0,
      round: "round",
      link: ""
    };
  },
  created() {
    this.elementList = this.initData.elementList;
    this.eleId = this.initData.specialId; //默认数据第一个为组件内的id
  },
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        const element = this.elementList[this.itemNo].elementName;
        if (element == "帐号信息") {
          c.routerSkip("", "13", 0, this.$router);
        } else if (element == "我的账单") {
          // window.location.href =this.link;
          let payfor =
            "http://171.14.99.98:7001/hnitv-api/hd/index.jsp?epg_info=<account>" +
            yh.userId +
            "</account><back_url>" +
            location.href +
            "?pos=8</back_url>";
          HyBirdWebCallBack.showPage(payfor);
        }
      }
    },
    back() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "back");
    },
    down() {
      if (!this.isEnd) {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "down");
      }
    },
    up() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "up");
    },
    left() {
      if (this.itemNo == 0) return;
      this.removeCss();
      this.itemNo--;
      this.addCss();
    },
    right() {
      if (this.itemNo == 1) return;
      this.removeCss();
      this.itemNo++;
      this.addCss();
    },
    addCss() {
      $(".systemSetting2 li:eq(" + this.itemNo + ")").addClass("hover");
    },
    removeCss() {
      $(".systemSetting2 li:eq(" + this.itemNo + ")").removeClass("hover");
    },
    updateStast(bl, pos, isEnd) {
      this.isShow = bl;
      this.pos = pos;
      this.isEnd = isEnd;
      if (pos == "down" && this.itemNo > 3 && isEnd == false) {
        this.itemNo = 0;
      }
      this.addCss();
      if (bl == true && isEnd == false) {
        let eleH = this.cEleOffsetTop(
          ".systemSetting2 li:eq(" + this.itemNo + ")"
        );
        // console.log("组件距离文档的高度:"+eleH);
        this.$emit("eleScroll", this.name, this.pos, eleH);
      }
    },
    cEleOffsetTop(ele) {
      const fEleDistance = $(ele).offset().top;
      const realHeight = fEleDistance + 105;
      return realHeight;
    }
  },
  computed: {
    ...mapGetters(["category", "navpos"])
  },
  watch: {
    initData() {
      this.elementList = this.initData.elementList;
      this.eleId = this.initData.specialId; //默认数据第一个为组件内的id
      // 数据更新组件id也需要更新
      this.$nextTick(function() {
        this.$lazyImages.loadImage();
      });
    }
  }
};
</script>



