<template>
    <div class="fw_recommend" :id="'fwr_'+eleId">
        <lazy-image
                :src='background'
                :placeholder='placeholder'
                :imgClass='round'
                  />
        <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
    </div>
</template>
<style scoped>
.fw_recommend {
  position: relative;
  width: 1118px;
  height: 140px;
  border-radius: 17px;
  border: 1px solid #707070;
  margin-top: 20px;
}
.fw_recommend.hover {
  border: 3px solid #ffffff;
  margin: 18px -2px -2px -2px;
  transform: scale(1.05);
}
</style>
<script>
import { mapGetters } from "vuex";
import c from "../../js/common.js";
import { bp } from "../../js/ga.js";
import {xha} from '../../js/xha.js';
export default {
  name: "fw_recommend",
  props: ["initData", "itemSort"],
  data() {
    return {
      isShow: false,
      placeholder: require("../../assets/img_loading_352x190.png"),
      initScore: require("../../assets/corner_mark.png"),
      itemNo: 0,
      pos: 0,
      eleId: "",
      elementList: [],
      round: "round",
      background: ""
    };
  },
  created() {
    this.elementList = this.initData.elementList;
    this.eleId = this.initData.specialId;
    this.background = this.initData.backgroungImg;
  },
  mounted() {},
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
      } else if (kc == "left") {
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        let posId = '';
        let columnId = c.BPTypefilters(this.category[this.navpos].catCode);
        if(this.itemSort){
          posId =  this.itemSort + '0'+(this.itemNo+1)
        }
         c.setPosition(posId,columnId); // 保存推荐位置
         xha.pageLoad(this.category[this.navpos].catName,"",this.elementList[this.itemNo].elementName,this.elementList[this.itemNo].elementId,posId,columnId); // xha 埋点
        const elementId = this.elementList[this.itemNo].elementId;
        const trackId = this.category[this.navpos].catId + "|" + elementId;
        bp.categoryClick(
          this.category[this.navpos].catId,
          elementId,
          trackId,
          1,
          ""
        );
        const eleDetail = this.elementList[this.itemNo];
        c.routerSkip(
          eleDetail.jsonUrl,
          eleDetail.elementType,
          {},
          this.$router
        );
      }
    },
    up() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "up");
    },
    down() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "down");
    },
    back() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "back");
    },
    addCss() {
      $("#fwr_" + this.eleId).addClass("hover");
    },
    removeCss() {
      $("#fwr_" + this.eleId).removeClass("hover");
    },
    updateStast(bl, pos, isEnd) {
      this.isShow = bl;
      this.addCss();
      if (bl) {
        let eleH = this.cEleOffsetTop("#fwr_" + this.eleId);
        this.$emit("eleScroll", this.eleId, pos, eleH);
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset().top;
      let realHeight = 0;
      realHeight = fEleDistance + 140;
      return realHeight;
    }
  },
  computed: {
    ...mapGetters(["category", "navpos"])
  },
  watch: {
    initData() {
      this.elementList = this.initData.elementList;
      this.eleId = this.initData.specialId;
      this.background = this.initData.backgroungImg;
    }
  }
};
</script>



