<template>
    <div class="VipBootom">
        <div v-for="(item,index) in elementList" :key="'vb'+index" v-if="index<6" :class="('vb_'+index)">
            <lazy-image
                :src='item.elementImg'
                :placeholder='placeholder'
                :imgClass='round'
                  />
        </div>
      <keyDo v-on:listenKeyCode="keyCode" v-show="false"  v-if="isShow"></keyDo>
    </div>
</template>
<style scoped>
.VipBootom {
  position: relative;
  width: 1190px;
  height: 425px;
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
}
.VipBootom div {
  width: 355px;
  height: 202px;
  border: 1px solid #707070;
  border-radius: 15px;
  margin-right: 20px;
  margin-bottom: 20px;
}
.VipBootom div div.hover {
  border: 3px solid #ffffff;
  margin: -2px 18px 18px -2px;
  transform: scale(1.07);
  border-radius: 18px;
}
</style>
<script>
import { mapGetters } from "vuex";
import c from "../../js/common.js";
import { bp } from "../../js/ga.js";
import {xha} from '../../js/xha.js';
export default {
  name: "VipBottom",
  props: ["initData", "itemSort"],
  data() {
    return {
      isShow: false,
      placeholder: require("../../assets/img_loading_352x190.png"),
      initScore: require("../../assets/corner_mark.png"),
      itemNo: 0,
      pos: 0,
      eleId: "",
      elementList: [],
      round: "round"
    };
  },
  created() {
    this.elementList = this.initData.elementList;
    this.eleId = this.initData.specialId;
  },
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.removeCss();
        this.itemNo = 0;
        this.pos = 0;
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "back");
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        const elementId = this.elementList[this.itemNo].elementId;
        const trackId = this.category[this.navpos].catId + "|" + elementId;
        bp.categoryClick(
          this.category[this.navpos].catId,
          elementId,
          trackId,
          1,
          ""
        );
        let posId = '';
        let columnId = c.BPTypefilters(this.category[this.navpos].catCode);
        if(this.itemSort){
          posId =  this.itemSort + '0' +(this.itemNo+1);
        };
         c.setPosition(posId,columnId); // 保存推荐位置
        xha.pageLoad(this.category[this.navpos].catName,'',this.elementList[this.itemNo].elementName,elementId,posId,columnId);
        const obj = this.elementList[this.itemNo];
        if (obj.elementType == "4") {
          if(obj.layout == "Subject_Home"){
            c.routerSkip(
              obj.jsonUrl,
              "12",
              this.category[this.navpos].catCode,
              this.$router
            );
          }else if (obj.layout == "Mix_Subject") {
            c.routerSkip(
              obj.jsonUrl,
              "5",
              this.category[this.navpos],
              this.$router
            );
          } else {
            c.routerSkip(
              obj.jsonUrl,
              "4",
              this.category[this.navpos],
              this.$router
            );
          }
        } else {
          c.routerSkip(
            obj.jsonUrl,
            this.initData.elementList[this.itemNo].elementType,
            this.category[this.navpos],
            this.$router
          );
        }
      }
    },
    left() {
      if (this.itemNo == 0) return;
      this.removeCss();
      this.itemNo--;
      this.addCss();
    },
    right() {
      if (this.itemNo == 2 || this.itemNo > 4) return;
      this.removeCss();
      this.itemNo++;
      this.addCss();
    },
    up() {
      if (this.itemNo > 2) {
        this.removeCss();
        this.itemNo -= 3;
        this.addCss();
        let eleH = this.cEleOffsetTop(".vb_" + this.itemNo);
        this.$emit("eleScroll", this.eleId, "up", eleH);
      } else {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "up");
      }
    },
    down() {
      if (this.itemNo <= 2) {
        this.removeCss();
        this.itemNo += 3;
        this.addCss();
        let eleH = this.cEleOffsetTop(".vb_" + this.itemNo);
        this.$emit("eleScroll", this.eleId, "down", eleH);
      } else {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "down");
      }
    },
    addCss() {
      if (this.pos == 0) {
        $(".vb_" + this.itemNo + " div").addClass("hover");
      }
    },
    removeCss() {
      if (this.pos == 0) {
        $(".vb_" + this.itemNo + " div").removeClass("hover");
      }
    },
    updateStast(bl, pos, isEnd) {
      this.isShow = bl;
      this.addCss();
      if (bl == true && isEnd == false) {
        let eleH = this.cEleOffsetTop(".vb_" + this.itemNo);
        this.$emit("eleScroll", this.eleId, "down", eleH);
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset().top;
      let realHeight = fEleDistance + 200;
      return realHeight;
    }
  },
  watch: {
    initData() {
      this.elementList = this.initData.elementList;
      this.eleId = this.initData.specialId;
    }
  },
  computed: {
    ...mapGetters(["category", "navpos"])
  }
};
</script>



