<template>
    <div class="appList">
        <h2 v-if="elementList.length>0">{{title}}</h2>
        <ul>
            <li v-for="(item,index) of elementList" :key="'appList'+index" :id="'app_index_'+index" v-if="index<16">
                <div class="appListPos">
                    <lazy-image :src='("data:image/png;base64,"+item.appIcon)' :placeholder='placeholder' :imgClass="round" :title="item.appName" :titleClass="appTitle" />
                    <!-- <p class="appText"><span>{{item.appName}}</span><span>{{item.appName}}</span></p> -->
                </div>
            </li>
        </ul>
        <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
        <alert-msg ref="appAlert" :isNotImg="isImg" v-on:listernpopup="updateKeyDo" :alertMsg="tips" :sure="openApp" :cancle="uninstall"></alert-msg>
    </div>
</template>

<style scoped>
.appList {
  position: relative;
  width: 1195px;
  height: 360px;
  margin-top: 30px;
  
}
.appList ul{
  position: relative;
  width: 1195px;
  height: 360px;
  margin-top: 30px;
  display: flex;
  flex-direction:row;
  flex-wrap: wrap;
}
.appListPos {
  position: relative;
  width: 108px;
  height: 117px;
  border-radius: 15px;
  color: #ffffff;
  display: flex;
  flex-direction: column;
}
.appList li {
  /* float: left; */
  font-size: 18px;
  border-radius: 15px;
  margin-right: 35px;
  margin-bottom: 35px;
  text-align: center;
  line-height: 50px;
  height: 110px;
}
.appList li div.hover {
  border: 3px solid #ffffff;
  border-radius: 17px;
  /* margin: -2px 33px 33px -2px; */
  transform: scale(1.1);
  box-shadow: 0px 30px 40px rgba(0, 0, 0, 0.4);
}
.appListPos div{
  position: relative;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 15px;
  width: 108px;
  height: 112px;
  margin-bottom: 0px;
}
.appListPos div img {
  width: 100%;
  height: 100%;
  border-radius: 15px 15px 0px 0px;
}
.appListPos div .s-title {
  left: -3px;
  top: 72px;
  line-height: 25px;
}
.appTitle {
  background: "";
}
</style>

<script>
import { mapGetters } from "vuex";
import { client as yh } from "../../js/client.js";
import AlertMsg from "../firm";
import c from "../../js/common.js";
import { bp } from "../../js/ga.js";
import {xha} from '../../js/xha.js'

export default {
  name: "AppList", // 本地app应用
  props: ["initData", "itemSort"],
  components: {
    AlertMsg
  },
  data() {
    return {
      isShow: false,
      placeholder: require("../../assets/img_loading_352x190.png"),
      initScore: require("../../assets/corner_mark.png"),
      itemNo: 0,
      pos: 0,
      eleId: "",
      elementList: [
        {appName:'大象融媒app安装卸载',appPackageName:'123',appIcon:''},
        {appName:'测试app测试app',appPackageName:'123',appIcon:''},
        {appName:'爱奇艺',appPackageName:'123',appIcon:''},
        {appName:'腾讯视频',appPackageName:'123',appIcon:''},
        {appName:'测试app测试app',appPackageName:'123',appIcon:''},
        {appName:'爱奇艺',appPackageName:'123',appIcon:''},
        {appName:'腾讯视频',appPackageName:'123',appIcon:''},
        {appName:'测试app测试app',appPackageName:'123',appIcon:''},
        {appName:'爱奇艺',appPackageName:'123',appIcon:''},
        {appName:'腾讯视频',appPackageName:'123',appIcon:''}
      ],
      round: "round",
      appTitle: "appTitle",
      title: "",
      tips: "应用操作",
      openApp: "打开应用",
      uninstall: "卸载应用",
      isImg: false,
      timer: null
    };
  },
  created() {
    this.eleId = this.initData.specialId;
    this.title = this.initData.specialName; //默认标题
    window.getAppList = this.getAppList;
    // this.$hybrid.getAppList =  this.getAppList;
    try {
      // let jsonArray = yh.getAppList("Hybrid.getAppList");
      let jsonArray = yh.getAppList("getAppList");
      // console.log('开始获取本地app应用列表'+jsonArray);
      this.elementList = JSON.parse(jsonArray);
    } catch (error) {
      console.log("获取本地app应用error");
    }
  },
  updated() {
    this.$nextTick(function() {
      this.$lazyImages.loadImage();
    });
  },
  methods: {
    getAppList(jsonArray) {
      console.log("卸载成功，重新刷新appList");
      if(typeof(jsonArray)==='string'){
         console.log("string="+jsonArray);
         jsonArray = JSON.parse(jsonArray);
      }else if(typeof(jsonArray)==='object'){
         console.log("jsonArrayType="+JSON.stringify(jsonArray));
      }
      try {
        // let jsonArray = yh.getAppList("getAppList");
        this.elementList = jsonArray;
        if (this.elementList.length == 0) {
          this.removeCss();
          this.isShow = false;
          this.$emit("pressDown", this.isShow, this.eleId, "up");
          return;
        } else if (this.elementList.length == this.itemNo) {
          // 卸载更新后需要判断应用个数是否为0
          this.itemNo--;
          if (this.itemNo < 0) {
            // 为零键值传递给上一个组件
            this.removeCss();
            this.isShow = false;
            this.$emit("pressDown", this.isShow, this.eleId, "up");
            return;
          } else {
            this.addCss();
          }
        }
      } catch (error) {
        console.log("卸载成功后获取本地app应用error");
      }
    },
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        const element = this.elementList[this.itemNo];
        const elementId = element.elementId;
        const elementName = element.elementName;
        let posId = "";
        console.log(this.category[this.navpos].catCode);
        const columnId = c.BPTypefilters(this.category[this.navpos].catCode);
        if(this.itemSort){
          if(this.itemNo>9){
            posId =  this.itemSort  + (this.itemNo+1);
          }else{
            posId =  this.itemSort + '0' +(this.itemNo+1);
          }
        }
        c.setPosition(posId,columnId); // 保存推荐位置
        xha.pageLoad(this.category[this.navpos].catName,"",elementName,elementId,posId,columnId); // xha 埋点
        this.isShow = false;
        this.$refs.appAlert.dialog(
          true,
          element.appIcon,
          element.appPackageName,
          element.elementName
        );
      }
    },
    back() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "back");
    },
    left() {
      if (this.itemNo == 0) return;
      this.removeCss();
      this.itemNo--;
      this.addCss();
    },
    right() {
      if (this.itemNo == this.elementList.length - 1) return;
      if (this.itemNo == 7) return;
      this.removeCss();
      this.itemNo++;
      this.addCss();
    },
    up() {
      if (this.itemNo < 8) {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "up");
      } else {
        this.removeCss();
        this.itemNo -= 8;
        this.addCss();
      }
    },
    down() {
      if (this.itemNo > 7) {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "down");
      } else {
        if (this.elementList.length <= 8) return;
        this.removeCss();
        this.itemNo += 8;
        if(this.itemNo>(this.elementList.length-1)){
          this.itemNo = 8;
        }
        this.addCss();
      
      }
    },
    addCss() {
      if (this.pos == 0) {
        $(".appList li .imglazy")
          .eq(this.itemNo)
          .addClass("hover");
        let p_title = $("#app_index_" + this.itemNo + " .s-title-name").text();
        if (p_title.length >= 6) {
          let id = "#app_index_" + this.itemNo;
          let speed = 40;
          let that = this;
          let tab = document.querySelector(id + " .s-title");
          let tab1 = document.querySelector(id + " .s-title-name");
          let tab2 = document.querySelector(id + " .s-title-temp");
          tab.style.textOverflow = "clip";
          tab2.innerHTML = tab1.innerHTML;
          this.timer = setInterval(function() {
            that.marquee(tab, tab1, tab2);
          }, speed);
        }
        //   $('.appListPos div:eq(1)').eq(this.itemNo).css("background",'#0098BC');
      }
    },
    removeCss() {
      if (this.pos == 0) {
        $(".appList li .imglazy")
          .eq(this.itemNo)
          .removeClass("hover");
        //   $('.appListPos div:eq(1)').eq(this.itemNo).css("background",'rgba(255, 255, 255, 0.2)');
        if (this.timer != null) {
          clearInterval(this.timer);
          this.timer = null;
          let id = "#app_index_" + this.itemNo;
          let tab = document.querySelector(id + " .s-title");
          tab.scrollLeft = 0;
          tab.style.textOverflow = "ellipsis";
        }
      }
    },
    updateStast(bl, pos, isEnd) {
      if (this.elementList.length == 0) {
        // 获取本地应用为空,不需展示此组件
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "up");
        return false;
      }
      this.isShow = bl;
      this.addCss();
      if (bl) {
        let eleH = this.cEleOffsetTop(".appListPos :eq(" + 0 + ")");
        console.log("组件距离文档的高度:" + eleH);
        this.$emit("eleScroll", this.name, pos, eleH);
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset().top;
      let realHeight = 0;
      realHeight = fEleDistance + 400;
      return realHeight;
    },
    updateKeyDo(args) {
      this.isShow = true;
    },
    marquee(tab, tab1, tab2) {
      if (tab2.offsetWidth - tab.scrollLeft <= 0) {
        // tab.scrollLeft -= tab1.offsetWidth;
        tab.scrollLeft = 0; // 上面的写法会导致滚动时突然抖动
      } else {
        tab.scrollLeft++;
      }
    }
  },
  computed: {
    ...mapGetters(["category", "navpos"])
  },
  watch: {
    initData() {
      this.eleId = this.initData.specialId;
      // this.elementList = this.initData.elementList;
      this.title = this.initData.specialName; //默认标题
    }
  },
  beforeRouteLeave(to, from, next) {
    window.clearInterval(this.timer);
    this.timer = null;
    next();
  }
};
</script>



