<template>
  <div class="systemSetting">
    <ul>
      <li v-for="(item,index) in elementList" :key="index+'system'" :id="('systemSetting_'+eleId+'_'+index)">
        <div><span :class="('jlt_')+index"></span><span>{{item.elementName}}</span></div>
      </li>
    </ul>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
  </div>
</template>
<style scoped>
.systemSetting {
  position: relative;
  width: 1104px;
  height: 105px;
  margin-bottom: 20px;
}

.systemSetting ul {
  display: flex;
  justify-content: space-around;
}

.systemSetting li {
  width: 356px;
  height: 105px;
  border-radius: 10px;
  /* background-color: #4b6676; */
  background-image: url('../../assets/img_loading_352x190.png');
  text-align: center;
}

.systemSetting li.hover {
  margin: -4px;
  border: 4px solid #fff;
  transform: scale(1.05);
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.55);
  border-radius: 18px;
}

li div {
  width: 182px;
  height: 50px;
  padding: 28px 84px 27px 90px;
  line-height: 50px;
}
div span:nth-child(2) {
  float: right;
  font-size: 20px;
}

.jlt_0,
.jlt_1,
.jlt_2 {
  float: left;
  vertical-align: middle;
  background: url("../../assets/spriteImg_50x50.png") 0 0 no-repeat;
  width: 50px;
  height: 50px;
}
.smile {
  float: left;
  vertical-align: middle;
  background: url("../../assets/smile.png") no-repeat;
  width: 50px;
  height: 50px;
}
#systemSetting_1_0 .jlt_0 {
  background-position: 0 0;
}
#systemSetting_1_1 .jlt_1 {
  background-position: -55px 0px;
}
#systemSetting_1_2 .jlt_2 {
  background-position: -52px -105px;
}
#systemSetting_2_0 .jlt_0 {
  background-position: -158px -0px;
}
#systemSetting_2_1 .jlt_1 {
  background-position: -3px -58px;
}
#systemSetting_2_2 .jlt_2 {
  background-position: -104px -107px;
}
#systemSetting_3_0 .jlt_0 {
  background-position: -158px -55px;
}
#systemSetting_3_1 .jlt_1 {
  background-position: -107px -55px;
}
#systemSetting_3_2 .jlt_2 {
  background-position: -52px -55px;
}
#systemSetting_4_0 .jlt_0 {
  background-position: 0px -105px;
}
#systemSetting_4_1 .jlt_1 {
  background-position: -107px 0px;
}
#systemSetting_4_2 .jlt_2 {
  float: left;
  vertical-align: middle;
  background: url("../../assets/smile.png") no-repeat;
  width: 50px;
  height: 50px;
}
</style>
<script>
import c from "../../js/common.js";
import { bp } from "../../js/ga.js";
import { mapGetters } from "vuex";
import { client as yh } from "../../js/client.js";
import {xha} from '../../js/xha.js';
export default {
  props: ["initData", "itemSort"],
  name: "SystemList",
  data() {
    return {
      itemNo: 0,
      isShow: false,
      elementList: [],
      eleId: "",
      pos: 0,
      round: "round",
      isEnd: false
    };
  },
  created() {
    this.elementList = this.initData.elementList;
    this.eleId = this.initData.specialId; //默认数据第一个为组件内的id
  },
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        let posId = '';
        let columnId = c.BPTypefilters(this.category[this.navpos].catCode);
        posId =  $("#systemSetting_" + this.eleId + "_" + this.itemNo).parent('ul').parent('.systemSetting').attr('data-itemSort') + '0'+(this.itemNo+1)
        c.setPosition(posId,columnId); // 保存推荐位置
        const element = this.elementList[this.itemNo].elementName;
        xha.pageLoad("我的","",element,'',posId,columnId); // xha 埋点
        if (element == "最近观看") {
          c.routerSkip("", "15", { type: false }, this.$router);
        } else if (element == "我的收藏") {
          c.routerSkip("", "15", { type: true }, this.$router);
        } else if (element == "本地播放") {
          yh.openLocalPlay();
        } else if (element == "应用管理") {
          c.routerSkip("", "14", { type: true }, this.$router);
        } else if (element == "网络设置") {
          yh.settingNetwork();
        } else if (element == "一键加速") {
          yh.turboMemory();
        } else if (element == "网络测试") {
          yh.networkSpeed();
        } else if (element == "垃圾清理") {
          yh.clearRubbish();
        } else if (element == "系统信息") {
          c.routerSkip("", "13", 1, this.$router);
        } else if (element == "帐号信息") {
          c.routerSkip("", "13", 0, this.$router);
        } else if (element == "我的账单") {
          let payfor =
            "http://171.14.99.98:7001/hnitv-api/hd/index.jsp?epg_info=<account>" +
            yh.userId +
            "</account><back_url>" +
            location.href +
            "?pos=8</back_url>";
          HyBirdWebCallBack.showPage(payfor);
        }
      }
    },
    back() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "back");
    },
    down() {
      if (!this.isEnd) {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.itemNo, this.eleId, "down", this.itemNo);
      }
    },
    up() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.itemNo, this.eleId, "up", this.itemNo);
    },
    left() {
      if (this.itemNo == 0) return;
      this.removeCss();
      this.itemNo--;
      this.addCss();
    },
    right() {
      if (this.itemNo == 2) return;
      this.removeCss();
      this.itemNo++;
      this.addCss();
    },
    addCss() {
      $("#systemSetting_" + this.eleId + "_" + this.itemNo).addClass("hover");
    },
    removeCss() {
      $("#systemSetting_" + this.eleId + "_" + this.itemNo).removeClass(
        "hover"
      );
    },
    updateStast(bl, pos, isEnd, itemNo) {
      this.isShow = bl;
      this.pos = pos;
      this.isEnd = isEnd;
      if (pos == "down" && this.itemNo > 3 && isEnd == false) {
        this.itemNo = 0;
      }
      if (itemNo) {
        this.itemNo = itemNo;
      } else {
        this.itemNo = 0;
      }
      this.addCss();
      if (bl == true && isEnd == false) {
        let eleH = this.cEleOffsetTop(
          "#systemSetting_" + this.eleId + "_" + this.itemNo
        );
        // console.log("组件距离文档的高度:"+eleH);
        this.$emit("eleScroll", this.name, this.pos, eleH);
      }
    },
    cEleOffsetTop(ele) {
      const fEleDistance = $(ele).offset().top;
      const realHeight = fEleDistance + 105;
      return realHeight;
    }
  },
  computed: {
    ...mapGetters(["category", "navpos"])
  },
  watch: {
    initData() {
      this.elementList = this.initData.elementList;
      this.eleId = this.initData.specialId; //默认数据第一个为组件内的id
      // 数据更新组件id也需要更新
      this.$nextTick(function() {
        this.$lazyImages.loadImage();
      });
    }
  }
};
</script>



