<template>
  <div class="liveColumn">
      <ul>
        <li v-for="(item,index) of leboList8" :key="'livePos'+index" v-if="index<8">
              <div class="livePos">
                  <div><img :src="item.logo" /></div>
                  <div>{{item.channelName}}</div>
              </div>
          </li>
      </ul>
      <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
  </div>
</template>
<style scoped>
    .liveColumn{position: relative;width: 1195px;height:110px;margin-top: 20px;}
    .livePos{position: relative;width: 107px;height: 102px;border-radius: 15px;color: #FFFFFF;display: flex;flex-direction: column;}
    .livePos div{position: relative;height:53px;}
    .liveColumn li{float: left;font-size: 18px;background-color: rgba(255, 255, 255, 0.2); border:1px solid #FFFFFF;border-radius: 15px;margin-right: 35px;text-align: center;line-height: 50px;overflow: hidden}
    .liveColumn li.hover{border:3px solid #FFFFFF;border-radius: 17px;margin: -2px 33px -2px -2px;transform: scale(1.1);box-shadow:0px 30px 40px rgba(0, 0, 0, 0.4);  }
    .livePos div img{width: 100%;height: 100%;border-radius: 15px 15px 0px 0px;};
</style>
<script>
import {mapGetters} from 'vuex'
import {bp} from '../../js/ga.js'
import c from '../../js/common.js'
import {client as yh} from '../../js/client.js'
import {xha} from '../../js/xha.js'
export default {
  name:"LiveHot8",
  props:['initData','itemSort'],
  data(){
      return{
          isShow:false,
          placeholder:require('../../assets/img_loading_352x190.png'),
          initScore:require('../../assets/corner_mark.png'),
          itemNo:0,
          pos:0,
          eleId:'liveCommon',
          name:'liveCommon',
          elementList:[],
          leboList:[],
          historyList: [],
          round:'round',
          leboList8:[],
          enterType:6,
          playCsList:null,
          historyCommonLength:0,
      }
  },created(){
      window.getUserChannel = this.getUserChannel;
      window.getOffLineList = this.getOffLineList;
      this.elementList = this.fliteData(this.initData.liveList,"hot");
      this.leboList=  this.elementList.channelList.slice(12,20);
      const liveUrl = this.initData.liveJsonUrl;
      if(liveUrl){
        this.filterUrl(liveUrl);
      }
      this.initHistory(true);
  },mounted(){

  },activated(){
    
  },methods:{
      getOffLineList(data){
        console.log("频道下线通知,不处理");
        // console.log(data);
      },
      getUserChannel(data){ // 常看频道节目单下线刷新
        console.log("查询已下线节目单接口通知==="+data);
        if(typeof(data)=='object'){
          console.log("客户端返回json对象");
          this.historyList=data; 
        }else{
          this.historyList=JSON.parse(data); 
        }
        
        this.historyCommonLength = this.historyList.length;
        // console.log("常看记录下线查询结果返回:"+JSON.stringify(this.historyList));
        
        let channelList = this.distinct(this.historyList); // 常看频道去重
        
        this.leboList8 = this.historyList.concat(channelList);
        
      },
      filterUrl(url){
        if(url.indexOf("clientType=bs")>=0){
        this.playCsList =  url.replace('clientType=bs','clientType=cs');
        }
      },
      initHistory(isonce){  // 初始化请求常看频道会异步去刷新常看频道是否下线,isonce是否是第一次
        try {
          // 这里commonList 常看频道
          let commonList =null;
          if(isonce){
             commonList = yh.getCommonChannel('getUserChannel');
          }else{
             commonList = yh.getCommonChannel('getOffLineList');//如果不是第一次请求就不需要回调节目单下线通知
          }
          this.historyList=JSON.parse(commonList); 

          this.historyCommonLength = this.historyList.length;
          
          let channelList = this.distinct(this.historyList); // 常看频道去重
          
          this.leboList8 = this.historyList.concat(channelList);
          
          console.log("常看记录:"+this.historyList);
        }catch(error){
          console.log("获取历史数据异常");
          this.leboList8=this.leboList;
        }
      },
      keyCode(kc) {
        if (kc == "KeyBack") {
          this.$store.commit('GET_LIVESTATUS', true); // 小窗口是否关闭
          this.removeCss();
          this.isShow = false;
          this.$emit('pressDown', this.isShow,this.eleId,"back");
        }else if(kc == "right"){
                this.right();
        }else if(kc == "left"){
                this.left();
        }else if(kc == "down"){
                this.down();
        }else if(kc == "up"){
                this.up();
        }else if(kc == "KeyEnter"){
                const eleDetail = this.leboList8[this.itemNo];
                const elementId = this.leboList8[this.itemNo].elementId;
                const trackId = this.category[this.navpos].catId +"|"+elementId;
                let posId = '';
                let columnId = c.BPTypefilters(this.category[this.navpos].catCode);
                if(this.itemSort){
                  posId =  this.itemSort + '0'+(this.itemNo+1)
                }
                xha.pageLoad(this.category[this.navpos].catName,"",eleDetail.channelName,this.leboList[this.itemNo].channelId,posId,columnId); // xha 埋点
                bp.categoryClick(this.category[this.navpos].catId, elementId, trackId,1, "");
                if(this.itemNo>(this.historyCommonLength-1)){
                  console.log("热播频道点击");
                   c.logCsSplice(4,"","","","",this.category[this.navpos].catCode,"","","","","");// B透传C埋点信息
                  yh.fullLivePlay(this.playCsList,0,this.elementList.categoryType,eleDetail.channelId,"","",true,false);
                }else{
                  console.log("常看频道点击");
                   c.logCsSplice(7,"","","","",this.category[this.navpos].catCode,"","","","","");// B透传C埋点信息
                  yh.fullLivePlay(this.playCsList,2,eleDetail.channelType,eleDetail.channelId,"","",true,false);
                }
                this.loading();
        }
      },
      loading(){
        try{
           //加载yh埋点
          const BiJson = {};
          BiJson.cat_id = this.category[this.navpos].catId;  //所在栏目id
          BiJson.trace_id = c.getTrackPath(); //当前访问页面id路径、轨迹，以“|”分割
          BiJson.trace_type = c.getTrackPathType(); //当前访问页面路径、轨迹，以“|”分割的页面类型
          BiJson.parent_page_type = c.getParentPageType();
          BiJson.parent_page_id = c.getParentPageId(); //父级页面id
          BiJson.special_type="";
          // BiJson.special_type =c.getParentPageType()=="8"?special_type:""; //专题也才传 专题未传参
          BiJson.recommend_place = this.category[this.navpos].catName; //未知
          BiJson.special_id="";
          BiJson.cat_name = this.category[this.navpos].catName; //所在栏目名称
          BiJson.enterType =this.enterType; //进入点播方式 哪些是点播资源/嗨看资源？
          BiJson.filmType = "1"; //影片类型(0:嗨看 1: 点播) 我们都是点播
          BiJson.assetId="";
          BiJson.assetName="";
          BiJson.type="0"+c.getParentPageType();
          BiJson.typeId=this.category[this.navpos].catId=="202652"?"202652":"0";
          BiJson.pagePath=c.getTrackPathType().replace(/\|/gi, '_');
          BiJson.srcPagePath=c.getTrackPathType().slice(0,c.getTrackPathType().length-2).replace(/\|/gi, '_');
          yh.setBiJson(JSON.stringify(BiJson));
      } catch(error){
        console.log(error);
      }
          
      },left(){
        if(this.itemNo==0)return;
        this.removeCss();
        this.itemNo--;
        this.addCss();
      },right(){
        if(this.itemNo==7)return;
        this.removeCss();
        this.itemNo++;
        this.addCss();
      },up(){
        this.removeCss();
        this.isShow=false;
        this.$emit('pressDown', this.isShow,this.eleId,"up");
      },down(){
        this.removeCss();
        this.isShow=false;
        this.$emit('pressDown', this.isShow,this.eleId,"down");
      },addCss(){
          if(this.pos==0){
              $('.liveColumn li').eq(this.itemNo).addClass('hover');
               $('.livePos:eq('+this.itemNo+') div').eq(1).css("background",'#0098BC');
          }
      },removeCss(){
          if(this.pos==0){
              $('.liveColumn li').eq(this.itemNo).removeClass('hover');
              $('.livePos:eq('+this.itemNo+') div').eq(1).css("background",'');
          }
      },updateStast(bl,pos,isEnd){
        this.isShow = bl;
        this.addCss();
        if(bl){
            let eleh = ".liveColumn li:eq("+this.itemNo+")";
            let  element =this.cEleOffsetTop(eleh);
           this.$emit('eleScroll',this.name,pos,element);
        }
      },cEleOffsetTop(ele){
            let fEleDistance = $(ele).offset().top;
            let realHeight=0;
            realHeight = fEleDistance+120;
            return realHeight;
      },fliteData(args,type){
          for (let index = 0; index < args.length; index++) {
            const element = args[index];
            if(element.categoryType==type){
              return element;
            }
          }
      },updateChannel(){ //  大小窗口切换需要更新常看频道是否下线操作
        console.log("更新常看频道");
        this.initHistory(false);
      },distinct(commonList){ // 热播频道去重
         const  hotList = this.leboList.slice(0); // 数组深拷贝
         const  uniqueList = [];
         for (let k = 0; k < hotList.length; k++) {
           const elementK = hotList[k];
           let isAdd = true;
           for (let j = 0; j < commonList.length; j++) {
             const elementJ = commonList[j];
             if(elementK.channelId === elementJ.channelId){
                console.log("常看频道去重^^^^^"+elementK.channelId);
                isAdd = false;
             }
           }
           if(isAdd){
             uniqueList.push(elementK);
           }else{
             isAdd = true;
           }
         }
         return uniqueList;
      }
  },watch:{
    initData(){
        this.elementList = this.fliteData(this.initData,"1");
        const liveUrl = this.initData.liveJsonUrl;
        if(liveUrl){
          this.filterUrl(liveUrl);
        }
     }
  },computed:{
      ...mapGetters([
        'category',
        'navpos',
        'liveStatus'
      ])
  }
}
</script>



