<template>
<!-- 大数据推荐 -->
    <div class="asset_6">
      <!-- <h2>{{title}}</h2> -->
      <ul>
        <li  v-for="(item,index) in specialList" v-if="index<6"  :key="item.id" :id="('as_'+eleId+'_'+index)"  >
          <!-- <img :src="item.elementImg"> -->
          <!-- <img :data-src="item.elementImg" v-imgLazy:asset="2" >  -->
          <lazy-image
                :src='item.elementImg'
                :placeholder='placeholder'
                :initScore = 'initScore'
                :score = "item.score"
                :title = "item.elementName"
                :imgClass="round"
                  />
        </li>
      </ul>
        <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
    </div>
</template>
<style scoped>
h2 {
  color: #ffffff;
  font-size: 30px;
  margin-bottom: 35px;
  font-family: "Arial", "Microsoft YaHei", "黑体", "宋体", sans-serif;
  text-align: left;
}
.asset_6 {
  position: relative;
  width: 1227px;
  min-height: 240px;
  margin-top: 30px;
}
.asset_6 li {
  position: relative;
  float: left;
  margin-right: 30px;
  margin-bottom: 30px;
  width: 160px;
  height: 233px;
  z-index: 10;
  box-sizing: border-box;
}
.asset_6 img {
  width: 100%;
  height: 100%;
}
.asset_6 li.hover {
  margin: -3px 33px 27px -3px;
  border: 3px solid #ffffff;
  transform: scale(1.1);
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.55);
  border-radius: 18px;
}
.asset_6 img,
.asset_6 .imglazy {
  border-radius: 15px;
}
</style>
<script>
import { mapGetters } from "vuex";
import c from "../../js/common.js";
import { bp } from "../../js/ga.js";
import {xha} from '../../js/xha.js';
export default {
  props: ["initData", "catId", "itemSort"],
  name: "Asset_6",
  data() {
    return {
      title: "",
      name: "asset_6",
      placeholder: require("../../assets/img_loading_160x230.png"),
      initScore: require("../../assets/corner_mark.png"),
      itemNo: 0,
      ClassNo: -1,
      position: 0,
      isShow: false,
      isEnd: false,
      specialList: [],
      eleId: "",
      timer: null,
      pos: "",
      round: "round"
    };
  },
  beforeMount() {
    this.specialList = this.initData.elementList;
    this.eleId = this.initData.specialId; //默认数据第一个为组件内的id
    this.name = "asset_" + this.initData.specialId; //默认数据第一个为组件名字
    this.title = this.initData.specialName; //默认标题
  },
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        if (this.timer != null) {
          clearInterval(this.timer);
        };
        let posId = '';
        let columnId = c.BPTypefilters(this.category[this.navpos].catCode);
        if(this.itemSort){
          posId =  this.itemSort + '0'+(this.itemNo+1)
        }
        c.setPosition(posId,columnId); // 保存推荐位置
        xha.pageLoad(this.category[this.navpos].catName,"",this.specialList[this.itemNo].elementName,this.specialList[this.itemNo].elementId,posId,columnId); // xha 埋点
        const elementId = this.specialList[this.itemNo].elementId;
        const trackId = this.category[this.navpos].catId + "|" + elementId;
        bp.categoryClick(
          this.category[this.navpos].catId,
          elementId,
          trackId,
          1,
          ""
        );
        let url = this.specialList[this.itemNo].jsonUrl;
        try {
          let playType = this.specialList[this.itemNo].elementType;
          if (playType === "8" || 8 === playType) {
            c.routerSkip(url, playType, {}, this.$router);
          }else if(eleDetail.layout=="Subject_Home"){
             c.routerSkip(
                  url,
                  "12",
                  this.category[this.navpos].catCode,
                  this.$router
                );
          } else {
            this.$router.push({
              name: "detail",
              params: {
                jsonUrl: url,
                catCode: 1,
                catId: this.catId,
                parent: 1,
                parentId: this.catId
              }
            });
          }
        } catch (error) {
          this.$router.push({
            name: "detail",
            params: {
              jsonUrl: url,
              catCode: 1,
              catId: this.catId,
              parent: 1,
              parentId: this.catId
            }
          });
        }
      }
    },
    back() {
      if (this.timer != null) {
        clearInterval(this.timer);
      }
      this.removeCss();
      this.isShow = false;
      this.itemNo = 0;
      this.$emit("pressDown", this.isShow, this.eleId, "back");
    },
    down() {
      if (!this.isEnd) {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "down");
      }
    },
    up() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "up");
    },
    left() {
      if (this.itemNo == 0 || this.itemNo == 6) return;
      this.removeCss();
      this.itemNo--;
      this.addCss();
    },
    right() {
      if (this.itemNo == 11 || this.itemNo == 5) return;
      this.removeCss();
      this.itemNo++;
      this.addCss();
    },
    addCss() {
      $("#as_" + this.eleId + "_" + this.itemNo).addClass("hover");
      let p_title = $(
        "#as_" + this.eleId + "_" + this.itemNo + " .s-title-name"
      ).text();
      if (p_title.length >= 10) {
        let id = "#as_" + this.eleId + "_" + this.itemNo;
        let speed = 40;
        let that = this;
        let tab = document.querySelector(id + " .s-title");
        let tab1 = document.querySelector(id + " .s-title-name");
        let tab2 = document.querySelector(id + " .s-title-temp");
        tab.style.textOverflow = "clip";
        tab2.innerHTML = tab1.innerHTML;
        this.timer = setInterval(function() {
          that.Marquee(tab, tab1, tab2);
        }, speed);
      }
    },
    removeCss() {
      $("#as_" + this.eleId + "_" + this.itemNo).removeClass("hover");
      if (this.timer != null) {
        clearInterval(this.timer);
        this.timer = null;
        let id = "#as_" + this.eleId + "_" + this.itemNo;
        let tab = document.querySelector(id + " .s-title");
        tab.scrollLeft = 0;
        tab.style.textOverflow = "ellipsis";
      }
    },
    updateStast(bl, pos, isEnd) {
      this.isShow = bl;
      this.pos = pos;
      // console.log(this.name,bl,pos,isEnd);
      this.isEnd = isEnd;
      if (pos == "down" && this.itemNo > 5 && isEnd == false) {
        this.itemNo = 0;
      }
      this.addCss();
      if (bl == true && isEnd == false) {
        let eleH = this.cEleOffsetTop("#as_" + this.eleId + "_" + this.itemNo);
        // console.log("组件距离文档的高度:"+eleH);
        this.$emit("eleScroll", this.name, pos, eleH);
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset().top;
      // console.log(this.name,"元素距离文档顶部的距离:"+(fEleDistance));
      // console.log(this.name,"元素距离文档顶部的高度:"+(fEleDistance+258));
      let realHeight = fEleDistance + 260;
      return realHeight;
    },
    Marquee(tab, tab1, tab2) {
      if (tab2.offsetWidth - tab.scrollLeft <= 0) {
        // tab.scrollLeft -= tab1.offsetWidth;
        tab.scrollLeft = 0; // 上面的写法会导致滚动时突然抖动
      } else {
        tab.scrollLeft++;
      }
    }
  },
  computed: {
    ...mapGetters(["category", "navpos"])
  },
  watch: {
    initData() {
      this.specialList = this.initData.elementList;
      // 数据更新组件id也需要更新
      this.specialList = this.initData.elementList;
      this.eleId = this.initData.specialId; //默认数据第一个为组件内的id
      this.name = "asset_" + this.initData.specialId; //默认数据第一个为组件名字
      this.title = this.initData.specialName; //默认标题
      this.$nextTick(function() {
        this.$lazyImages.loadImage();
      });
    }
  }
};
</script>



