<template>
    <div class="Pop3">
        <div v-for="(item,index) in elementList" :key="'pop3'+index" v-if="index>4" :class="('pop3_'+(index-5))">
            <lazy-image
                :src='item.elementImg'
                :placeholder='placeholder'
                :imgClass='round'
                  />
        </div>
      <keyDo v-on:listenKeyCode="keyCode" v-show="false"  v-if="isShow"></keyDo>
    </div>
</template>
<style scoped>
.Pop3 {
  position: relative;
  width: 1190px;
  height: 425px;
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
}
.Pop3 div {
  width: 355px;
  height: 202px;
  border: 1px solid #707070;
  border-radius: 15px;
  margin-right: 20px;
  margin-bottom: 20px;
}
.Pop3 div div.hover {
  border: 3px solid #ffffff;
  margin: -2px 18px 18px -2px;
  transform: scale(1.07);
  border-radius: 18px;
}
</style>
<script>
import { mapGetters } from "vuex";
import c from "../../js/common.js";
import { bp } from "../../js/ga.js";
import {xha} from '../../js/xha.js'
export default {
  name: "PopThreeList",
  props: ["initData", "itemSort"],
  data() {
    return {
      isShow: false,
      placeholder: require("../../assets/img_loading_352x190.png"),
      initScore: require("../../assets/corner_mark.png"),
      itemNo: 0,
      pos: 0,
      eleId: "",
      elementList: [],
      round: "round",
      typess: ""
    };
  },
  created() {
    this.elementList = this.initData.elementList;
    this.eleId = this.initData.specialId;
  },
  mounted() {},
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        const elementId = this.elementList[this.itemNo + 5].elementId;
        const trackId = this.category[this.navpos].catId + "|" + elementId;
        bp.categoryClick(
          this.category[this.navpos].catId,
          elementId,
          trackId,
          1,
          ""
        );
        let posId = "";
        const columnId = c.BPTypefilters(this.category[this.navpos].catCode);
        if(this.itemSort){
              posId =  this.itemSort + '0' +(this.itemNo+1);
        }
        c.setPosition(posId,columnId); // 保存推荐位置
        xha.pageLoad(this.category[this.navpos].catName,"",this.elementList[this.itemNo + 5].elementName,elementId,posId,columnId); // xha 埋点
        const obj = this.elementList[this.itemNo + 5];
        try {
          let playType = obj.elementType;
          if (playType === "8" || 8 === playType) {
            c.routerSkip(obj.jsonUrl, obj.elementType, {}, this.$router);
          } else {
            c.routerSkip(
              obj.jsonUrl,
              obj.elementType,
              this.category[this.navpos],
              this.$router
            );
          }
        } catch (error) {
          c.routerSkip(
            obj.jsonUrl,
            obj.elementType,
            this.category[this.navpos],
            this.$router
          );
        }
      }
    },
    back() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "back");
      //      this.$emit('navigation',this.isShow,'back',true);
    },
    left() {
      if (this.itemNo == 0) return;
      this.removeCss();
      this.itemNo--;
      this.addCss();
    },
    right() {
      if (this.itemNo >= 2) return;
      this.removeCss();
      this.itemNo++;
      this.addCss();
    },
    up() {
      if (this.itemNo > 2) {
        this.removeCss();
        this.itemNo -= 3;
        this.addCss();
        let eleH = this.cEleOffsetTop(".pop3_" + this.itemNo);
        this.$emit("eleScroll", this.eleId, "up", eleH);
      } else {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "up");
      }
    },
    down() {
      if (this.typess != "专区") {
        if (this.itemNo <= 2) {
          this.removeCss();
          this.itemNo += 3;
          this.addCss();
          let eleH = this.cEleOffsetTop(".pop3_" + this.itemNo);
          this.$emit("eleScroll", this.eleId, "down", eleH);
        } else {
          this.removeCss();
          this.isShow = false;
          this.$emit("pressDown", this.isShow, this.eleId, "down");
        }
      }
    },
    addCss() {
      if (this.pos == 0) {
        $(".pop3_" + this.itemNo + " div").addClass("hover");
      }
    },
    removeCss() {
      if (this.pos == 0) {
        $(".pop3_" + this.itemNo + " div").removeClass("hover");
      }
    },
    updateStast(bl, pos, isEnd, lie) {
      this.isShow = bl;
      this.typess = lie;
      this.addCss();
      if (bl == true && isEnd == false) {
        let eleH = this.cEleOffsetTop(".pop3_" + this.itemNo);
        this.$emit("eleScroll", this.eleId, "down", eleH);
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset().top;
      let realHeight = fEleDistance + 200;
      return realHeight;
    }
  },
  watch: {
    initData() {
      this.elementList = this.initData.elementList;
      this.eleId = this.initData.specialId;
    }
  },
  computed: {
    ...mapGetters(["category", "navpos"])
  }
};
</script>



