<template>
    <div>
        <Menu1 v-on:pressDown="keepDo"
                 v-on:eleScroll="sysHeight" ref="systemMenu1"  :initData="itemData[0]" data-itemSort="01"></Menu1>
        <Menu1  v-on:pressDown="keepDo"
                 v-on:eleScroll="sysHeight" ref="systemMenu2"  :initData="itemData[1]" data-itemSort="02"></Menu1>
        <Menu1 v-on:pressDown="keepDo"
                 v-on:eleScroll="sysHeight" ref="systemMenu3"  :initData="itemData[2]" data-itemSort="03"></Menu1>
        <Menu1 v-on:pressDown="keepDo"
                 v-on:eleScroll="sysHeight" ref="systemMenu4"  :initData="itemData[3]" data-itemSort="04"></Menu1>
    </div>
</template>
<script>
import Menu1 from "./Category_SystemList_HN";
import Menu2 from "./Category_SystemList2_HN";
export default {
  props:["itemSort"],
  name: "MySelf",
  data() {
    return {
      itemData: [
        {
          specialId: "1",
          elementList: [
            { elementName: "最近观看" },
            { elementName: "我的收藏" },
            { elementName: "本地播放" }
          ]
        },
        {
          specialId: "2",
          elementList: [
            { elementName: "帐号信息" },
            { elementName: "我的账单" },
            { elementName: "应用管理" }
          ]
        },
        {
          specialId: "3",
          elementList: [
            { elementName: "系统信息" },
            { elementName: "网络设置" },
            { elementName: "一键加速" }
          ]
        },
        {
          specialId: "4",
          elementList: [
            { elementName: "网络测试" },
            { elementName: "垃圾清理" },
            { elementName: "敬请期待" }
          ]
        }
      ],
      eleId: "MySelf"
    };
  },
  components: { Menu1, Menu2 },
  created() {},
  methods: {
    keepDo(bl, eleId, pos, itemNo) {
      if (pos == "down") {
        if (eleId == "1") {
          this.$refs.systemMenu2.updateStast(true, pos, false, itemNo);
        } else if (eleId == "2") {
          this.$refs.systemMenu3.updateStast(true, pos, false, itemNo);
        } else if (eleId == "3") {
          this.$refs.systemMenu4.updateStast(true, pos, true, itemNo);
        }
      } else if (pos == "up") {
        if (eleId == "4") {
          this.$refs.systemMenu3.updateStast(true, pos, false, itemNo);
        } else if (eleId == "3") {
          this.$refs.systemMenu2.updateStast(true, pos, false, itemNo);
        } else if (eleId == "2") {
          this.$refs.systemMenu1.updateStast(true, pos, false, itemNo);
        } else if (eleId == "1") {
          this.$emit("pressDown", false, "MySelf", "up");
        }
      } else if (pos == "back") {
        this.$emit("pressDown", false, "MySelf", pos);
      }
    },
    sysHeight(name, pos, height) {
      console.log(name, pos, height);
    },
    updateStast(bl, pos, isEnd) {
      // 移动到最近的组件
      if (isEnd == false) {
        this.$refs.systemMenu1.updateStast(bl, pos, isEnd);
      }
    },
  }
};
</script>



