<!-- 推荐头部 -->
<template>
    <div class="recommendTop">
        <div class="rt-1">
           <lazy-image 
                :src="elementList[0].elementImg"
                :placeholder='placeholder'
                :imgClass = "round"
             />
        </div>
        <div class="rt-2">
             <div class="rt-l-1">
                <div v-for="(item,index) in elementList" :key="'rt_'+index" v-if="index>0&&index<3"><lazy-image :src="item.elementImg" :placeholder='placeholder' :imgClass = "round" /></div>
            </div>
            <div class="rt-l-2">
                <div class="rt-l-2-1">
                    <ul>
                        <li v-for="(item,index) in newHistory" :key="'ht_'+index" :id="'ht_'+index" v-if="index<3">
                            <div><span :id="('ht_title_'+(index))">{{item.elementName?item.elementName:item.assetName}}<i style="font-style:normal;margin-left: 30px;">{{item.watchTime?item.watchTime:''}}</i></span><span :id="('ht_copy_'+(index))" style="padding-left:20px;">{{item.elementName?item.elementName:item.assetName}}<i style="font-style:normal;margin-left: 30px;">{{item.watchTime?item.watchTime:''}}</i></span></div>
                        </li>
                    </ul>
                    <div class="History"><i class="liulanjilu"></i><h3>{{HistoryName}}</h3></div>
                </div>
                <div class="rt-l-2-2">
                   <div class="record"><span  class="jlt1"></span>记录收藏</div>
                   <div class="userCenter"><span class="jlt2"></span>个人中心</div>
                </div>
            </div>
            
        </div>
        <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
    </div>
</template>
<style scoped>
.recommendTop {
  position: relative;
  width: 1120px;
  height: 306px;
  display: flex;
}
.recommendTop img {
  border-radius: 11px;
}
.History {
  position: relative;
  display: inline-block;
  width: 100px;
  height: 100%;
  border-left: 1px solid #ffffff;
  text-align: center;
}
.History.hover {
  border: 3px solid #ffffff;
  margin: -3px;
  border-radius: 15px;
  box-shadow: 0px 20px 40px #000000;
}
.rt-1 {
  position: relative;
  width: 540px;
  height: 310px;
  margin-right: 24px;
}
.rt-2 {
  position: relative;
  width: 540px;
  height: 310px;
}
.rt-2 div img {
  width: 100%;
  height: 100%;
  border-radius: 15px;
}
.rt-l-1 {
  width: 560px;
  height: 144px;
  margin-bottom: 22px;
}
.rt-l-2 {
  width: 560px;
  height: 156px;
}
.rt-2.hover,.rt-1.hover {
  border: 3px solid #ffffff;
  margin: -3px 21px -3px -3px;
  transform: scale(1.05);
  box-shadow: #000000 0px 5px 10px 0px, #000000 0px 10px 20px 0px;
  border-radius: 15px;
}
.rt-l-1 div {
  width: 271px;
  height: 144px;
  display: inline-block;
}
.rt-l-1 div:nth-child(1) {
  margin-right: 12px;
}
.rt-l-1 div img {
  width: 100%;
  height: 100%;
}
.rt-l-1 div.hover {
  border-radius: 15px;
  border: 3px solid #ffffff;
  margin: -3px -3px -3px -3px;
  box-shadow: #000000 0px 5px 10px 0px, #000000 0px 10px 20px 0px;
}
.rt-l-1 div.hover1 {
  border-radius: 15px;
  border: 3px solid #ffffff;
  margin: -3px 9px -3px -3px;
  box-shadow: #000000 0px 5px 10px 0px, #000000 0px 10px 20px 0px;
}
.rt-l-2-1 {
  position: relative;
  width: 358px;
  height: 136px;
  display: inline-block;
  margin-right: 10px;
  float: left;
  border-radius: 15px;
  background-color: rgba(10, 128, 172, 0.35);
  border: 1px solid #ffffff;
}
.rt-l-2-2 {
  position: relative;
  width: 180px;
  height: 136px;
  display: inline-block;
  margin: 0px;
}
.record,
.userCenter {
  position: relative;
  font-weight: 400;
  width: 160px;
  height: 60px;
  text-align: left;
  line-height: 66px;
  padding-left: 17px;
  border: 1px solid #ffffff;
  background-color: rgba(10, 128, 172, 0.35);
  color: #ffffff;
  font-family: "PingFang HK", "Microsoft YaHei", "Arial";
  font-size: 20px;
  margin-bottom: 13px;
  border-radius: 15px;
}
.record.hover,
.userCenter.hover {
  border: 3px solid #ffffff;
  border-radius: 15px;
  margin: -2px -2px 11px -2px;
  background-color: #0098bc;
  box-shadow: 0px 20px 40px #000000;
}
.rt-l-2-2 i {
  font-size: 30px;
}
.rt-l-2-2 span {
  margin: 0 12px;
}
.rt-l-2-1 ul {
  position: relative;
  display: inline-block;
  float: left;
  width: 255px;
}
.rt-l-2-1 ul li {
  margin-left: 10px;
  box-sizing: border-box;
  height: 45px;
  font-weight: 400;
  font-family: "Helvetica Neue", "Regular", "Arial";
  font-size: 20px;
  line-height: 45px;
  border-bottom: 1px solid #ffffff;
}
.rt-l-2-1 ul li:nth-child(3) {
  border-bottom: none;
}
.rt-l-2-1 ul li div span:nth-of-type(2) {
  display: none;
}
.rt-l-2-1 ul li div {
  width: 235px;
  overflow: hidden;
  padding-left: 10px;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.rt-l-2-1 ul li div.hover {
  position: absolute;
  left: -3px;
  margin-left: 8px;
  background-color: #0098bc;
  border: 3px solid #ffffff;
  box-shadow: 0px 20px 40px #000000;
  transform: scale(1.05);
  display: block;
  width: 228px;
  height: 32px;
  border-radius: 15px;
  line-height: 32px;
}
.lishi {
  display: block;
  font-size: 33px;
  margin-top: 29px;
}
.History h3 {
  height: 40px;
  line-height: 40px;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
}
.jlt1 {
  width: 35px;
  height: 35px;
  display: inline-block;
  vertical-align: middle;
  padding-bottom: 13px;
  background: url("../../assets/icon.png") no-repeat;
  background-position: -105px -35px;
}
.jlt2 {
  width: 35px;
  height: 35px;
  display: inline-block;
  vertical-align: middle;
  padding-bottom: 13px;
  background: url("../../assets/icon.png") no-repeat;
  background-position: -175px -35px;
}
.liulanjilu {
  width: 33px;
  height: 36px;
  margin-top: 36px;
  display: inline-block;
  vertical-align: middle;
  background: url("../../assets/icon.png") no-repeat;
  background-position: -36px -35px;
}
</style>
<script>
import { bp } from "../../js/ga.js";
import { mapGetters } from "vuex";
import c from "../../js/common.js";
import { client as yh } from "../../js/client.js";
import {xha} from '../../js/xha.js'
export default {
  name: "RecommendTopIndex",
  props: ["initData", "itemSort"],
  data() {
    return {
      isShow: false,
      placeholder: require("../../assets/img_loading_352x190.png"),
      initScore: require("../../assets/corner_mark.png"),
      webp: require("../../assets/webpTest.webp"),
      itemNo: 0,
      pos: 0,
      eleId: "",
      elementList: [],
      timer: null,
      hotMoive: [],
      historyList: [],
      tempHistory: [],
      historyJ: false,
      jsontyb: [],
      round: "round",
      HistoryName: "热门影视",
      newHistory: [
        {
          elementId: 0,
          layout: "",
          score: "",
          elementImg: "",
          jsonUrl: "",
          elementType: "",
          elementName: ""
        },
        {
          elementId: 0,
          layout: "",
          score: "",
          elementImg: "",
          jsonUrl: "",
          elementType: "",
          elementName: ""
        },
        {
          elementId: 0,
          layout: "",
          score: "",
          elementImg: "",
          jsonUrl: "",
          elementType: "",
          elementName: ""
        },
        {
          elementId: 0,
          layout: "",
          score: "",
          elementImg: "",
          jsonUrl: "",
          elementType: "",
          elementName: ""
        },
        {
          elementId: 0,
          layout: "",
          score: "",
          elementImg: "",
          jsonUrl: "",
          elementType: "",
          elementName: ""
        }
      ]
    };
  },
  created() {
    this.elementList = this.initData.elementList;
    this.eleId = this.initData.specialId;
    this.initHistory();
  },
  activated() {
    this.initHistory();
  },
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        if (this.timer != null) {
          clearInterval(this.timer);
        }
        let posId = "";
        const columnId = c.BPTypefilters(this.category[this.navpos].catCode);
        if (this.pos == 0) {
          const eleDetail = this.elementList[this.itemNo];
          const elementId = this.elementList[this.itemNo].elementId;
          const trackId = this.category[this.navpos].catId + "|" + elementId;
          bp.categoryClick( // bi埋点
            this.category[this.navpos].catId,
            elementId,
            trackId,
            1,
            ""
          );
          if(this.itemSort){
            posId =  this.itemSort + '0' +(this.itemNo+1);
          }
          c.setPosition(posId,columnId); // 保存推荐位置
          xha.pageLoad(this.category[this.navpos].catName,"",eleDetail.elementName,elementId,posId,columnId); // xha 埋点
          if(eleDetail.layout=="Subject_Home"){
             c.routerSkip(
              eleDetail.jsonUrl,
              "12",
              eleDetail.elementCode,
              this.$router
            );
          }else{
            c.routerSkip(
              eleDetail.jsonUrl,
              eleDetail.elementType,
              eleDetail.elementCode,
              this.$router
            );
          }
          
        } else if (this.pos == 1) {
          if (this.itemNo < 3) { // 历史和收藏
           if(this.itemSort){
            posId =  this.itemSort + '0' +(this.itemNo+4);
           }
           let assetName = "";
           let assetId = "";
            const eleDetail = this.newHistory[this.itemNo];
            console.log("推荐历史控件:"+JSON.stringify(eleDetail));
            if(eleDetail.assetName){
               assetName = eleDetail.assetName;
              assetId = eleDetail.assetId;
            }else{
              assetName = eleDetail.elementName;
               assetId = eleDetail.elementId
            }
            c.setPosition(posId,columnId); // 保存推荐位置
            xha.pageLoad(this.category[this.navpos].catName,"",assetName,assetId,posId,columnId); // xha 埋点
            c.routerSkip(this.newHistory[this.itemNo].jsonUrl
                ? this.newHistory[this.itemNo].jsonUrl
                : this.newHistory[this.itemNo].layoutUrl,
              "1",
              "推荐",
              this.$router
            );
          } else if (this.itemNo == 4) { // 历史收藏
            if(this.itemSort){
              posId =  this.itemSort + '0' + 7;
            }
            c.setPosition(posId,columnId); // 保存推荐位置
            xha.pageLoad(this.category[this.navpos].catName,"","历史收藏","",posId,columnId); // xha 埋点
            c.routerSkip("", "15", this.category[this.navpos], this.$router);
          } else if (this.itemNo == 5) { // 个人中心
            if(this.itemSort){
              posId =  this.itemSort + '0' + 8;
            }
             c.setPosition(posId,columnId); // 保存推荐位置
            xha.pageLoad(this.category[this.navpos].catName,"","个人中心","",posId,columnId); // xha 埋点
            c.routerSkip("", "11", this.category[this.navpos], this.$router);
          }
        }
      }
    },
    left() {
      if (this.pos == 0) {
        if (this.itemNo == 0) return;
        this.removeCss();
        this.itemNo--;
        this.addCss();
      } else if (this.pos == 1) {
        if (this.itemNo < 3) {
          this.removeCss();
          this.itemNo = 0;
          this.pos = 0;
          this.addCss();
        } else if (this.itemNo == 4 || this.itemNo == 5) {
          this.removeCss();
          this.itemNo = 0;
          this.addCss();
        } else if (this.itemNo == 3) {
          this.removeCss();
          this.itemNo = 0;
          this.addCss();
        }
      }
    },
    back() {
      clearInterval(this.timer);
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "back");
    },
    right() {
      if (this.pos == 0) {
        if (this.itemNo == 2) return;
        this.removeCss();
        this.itemNo++;
        this.addCss();
      } else if (this.pos == 1) {
        if (this.itemNo < 3) {
          this.removeCss();
          this.itemNo = 4;
          this.addCss();
        } else if (this.itemNo == 3) {
          this.removeCss();
          this.itemNo = 4;
          this.addCss();
        }
      }
    },
    up() {
      if (this.pos == 0) {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "up");
      } else if (this.pos == 1) {
        if (this.itemNo > 0) {
          this.removeCss();
          if (this.itemNo == 4) {
            this.pos = 0;
            this.itemNo = 2;
          } else {
            this.itemNo--;
          }
          this.addCss();
        } else if (this.itemNo == 0) {
          this.removeCss();
          this.pos = 0;
          this.itemNo = 1;
          this.addCss();
        }
      }
    },
    down() {
      if (this.pos == 0) {
        if (this.itemNo == 0) {
          this.removeCss();
          this.isShow = false;
          this.$emit("pressDown", this.isShow, this.eleId, "down");
        } else {
          this.removeCss();
          this.pos = 1;
          this.itemNo = 0;
          this.addCss();
        }
      } else if (this.pos == 1) {
        if (this.itemNo < 2) {
          this.removeCss();
          this.itemNo++;
          this.addCss();
        } else if (this.itemNo == 4) {
          this.removeCss();
          this.itemNo = 5;
          this.addCss();
        } else if (this.itemNo == 2 || this.itemNo == 5) {
          this.removeCss();
          this.isShow = false;
          this.$emit("pressDown", this.isShow, this.eleId, "down");
        }
      }
    },
    addCss() {
      if (this.pos == 0) {
        if (this.itemNo == 0) {
          $(".rt-1").addClass("hover");
        } else if (this.itemNo == 1) {
          $(".rt-l-1")
            .children("div")
            .eq(0)
            .addClass("hover1");
        } else if (this.itemNo == 2) {
          $(".rt-l-1")
            .children("div")
            .eq(1)
            .addClass("hover");
        }
      } else if (this.pos == 1) {
        if (this.itemNo < 3) {
          let wordSize = $("#ht_" + this.itemNo + " span:eq(0)").text().length;
          if (wordSize > 13) {
            $("#ht_" + this.itemNo + " span:eq(1)").css(
              "display",
              "inline-block"
            );
            let speed = 40;
            let that = this;
            let tab = document.querySelector("#ht_" + this.itemNo + " div");
            let tab1 = document.querySelector("#ht_title_" + this.itemNo);
            let tab2 = document.querySelector("#ht_copy_" + this.itemNo);
            tab.style.textOverflow = "clip";
            this.timer = setInterval(function() {
              that.Marquee(tab, tab1, tab2);
            }, speed);
          }
          $("#ht_" + this.itemNo + " div").addClass("hover");
        } else if (this.itemNo == 3) {
          $(".History").addClass("hover");
        } else if (this.itemNo == 4) {
          $(".record").addClass("hover");
        } else if (this.itemNo == 5) {
          $(".userCenter").addClass("hover");
        }
      }
    },
    removeCss() {
      if (this.pos == 0) {
        if (this.itemNo == 0) {
          $(".rt-1").removeClass("hover");
        } else if (this.itemNo == 1) {
          $(".rt-l-1")
            .children("div")
            .eq(0)
            .removeClass("hover1");
        } else if (this.itemNo == 2) {
          $(".rt-l-1")
            .children("div")
            .eq(1)
            .removeClass("hover");
        }
      } else if (this.pos == 1) {
        if (this.itemNo < 3) {
          $("#ht_" + this.itemNo + " span:eq(1)").css("display", "none");
          if (this.timer != null) {
            clearInterval(this.timer);
            let tab = document.querySelector("#ht_" + this.itemNo + " div");
            tab.scrollLeft = 0;
            tab.style.textOverflow = "ellipsis";
          }
          $("#ht_" + this.itemNo + " div").removeClass("hover");
        } else if (this.itemNo == 3) {
          $(".History").removeClass("hover");
        } else if (this.itemNo == 4) {
          $(".record").removeClass("hover");
        } else if (this.itemNo == 5) {
          $(".userCenter").removeClass("hover");
        }
      }
    },
    updateStast(bl, pos, isEnd) {
      this.isShow = bl;
      this.addCss();
      if (bl) {
        let eleH = "";
        if (this.position == 0) {
          eleH = this.cEleOffsetTop(".rt-2");
        } else {
          eleH = this.cEleOffsetTop(".userCenter");
        }
        this.$emit("eleScroll", this.name, pos, eleH);
      }
    },
    Marquee(tab, tab1, tab2) {
      if (tab2.offsetWidth - tab.scrollLeft <= 0) {
        tab.scrollLeft = 0;
      } else {
        tab.scrollLeft++;
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset().top;
      let realHeight = 0;
      if (this.position == 0) {
        realHeight = fEleDistance + 306;
      } else {
        realHeight = fEleDistance + 140;
      }
      return realHeight;
    },
    initHistory() {
      // 获取点播历史信息
      let jsonObject = null;
      if (!this.isSouth) {
        jsonObject = yh.queryHistory();
      } else {
        jsonObject = window.localStorage.getItem("historyArr");
      }
      try {
        let arr = JSON.parse(jsonObject);
        let initList = [
          this.elementList[3],
          this.elementList[4],
          this.elementList[5]
        ];
        this.historyList = this.changeJson(arr);
        if (this.historyList.length > 0) {
          console.log("首页历史记录：" + this.historyList.length);
          this.HistoryName = "历史记录";
        }
        this.newHistory = this.historyList.concat(initList);
        console.log(JSON.stringify(this.newHistory));
        let result = [];
        let obj = {};
        for(let i =0; i<this.newHistory.length; i++){
          let resourceName = "";
          if(this.newHistory[i].elementName){
            resourceName = this.newHistory[i].elementName;
          }else{
            resourceName = this.newHistory[i].assetName;
          }
          if(!obj[resourceName]){
            result.push(this.newHistory[i]);
            obj[resourceName] = true;
          }
        }
        console.log("首页资产去重!"+JSON.stringify(result));
        this.newHistory = result;
        for (let i = 0; i < this.newHistory.length; i++) {
          const element = this.newHistory[i];
          if (element.watchTime) {
            element.watchTime = this.dateFormat(element.watchTime);
          }
        }
      } catch (error) {
        try {
          this.newHistory = [
            this.elementList[3],
            this.elementList[4],
            this.elementList[5]
          ];
        } catch (error) {
          console.log("获取热门推荐异常");
        }
        console.log("获取历史记录异常" + error);
      }
    },
    changeJson(jsonDB) {
      //        按时间排序
      let jsontyb = jsonDB;
      let _tempArray = []; //  sort 作用域在windows下面需要注意
      let sortedObjKeys = Object.keys(jsontyb).sort(function(a, b) {
        return jsontyb[b].recordTime - jsontyb[a].recordTime;
      });
      for (let i in sortedObjKeys) {
        if (sortedObjKeys.hasOwnProperty(i)) {
          _tempArray.push(jsontyb[sortedObjKeys[i]]);
        }
      }
      return _tempArray;
    },
    dateFormat(mss) {
      let hours = parseInt((mss % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      let minutes = parseInt((mss % (1000 * 60 * 60)) / (1000 * 60));
      let seconds = parseInt((mss % (1000 * 60)) / 1000);
      if (hours < 10) {
        hours = "0" + hours;
      }
      if (minutes < 10) {
        minutes = "0" + minutes;
      }
      if (seconds < 10) {
        seconds = "0" + seconds;
      }
      return hours + ":" + minutes + ":" + seconds;
    }
  },
  computed: {
    ...mapGetters(["category", "navpos", "isSouth"])
  },
  watch: {
    initData() {
      this.eleId = this.initData.specialId;
      this.elementList = this.initData.elementList;
    }
  }
};
</script>



