<template>
    <div class="localApp">
        <ul>
            <li v-for="(item,index) of elementList" :key="'localApp'+index" :id="'app_index_'+eleId+'_'+index" v-if="index<12">
                <div class="appListPos">
                    <lazy-image :src='("data:image/png;base64,"+item.appIcon)' :placeholder='placeholder' :imgClass="round"  :titleClass="appTitle" />
                    <!-- <p class="appText"><span>{{item.appName}}</span><span>{{item.appName}}</span></p> -->
                </div>
            </li>
        </ul>
        <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
        <alert-msg ref="appAlert" :isNotImg="isImg" v-on:listernpopup="updateKeyDo" :alertMsg="tips" :sure="openApp" :cancle="uninstall"></alert-msg>
    </div>
</template>

<style scoped>
.localApp {
  position: relative;
  width: 1195px;
  height: 360px;
  
}
.localApp ul{
  position: relative;
  width: 1195px;
  height: 360px;
  display: flex;
  flex-direction:row;
  flex-wrap: wrap;
}
.appListPos {
  position: relative;
  width: 170px;
  height: 170px;
  border-radius: 15px;
  color: #ffffff;
  display: flex;
  flex-direction: column;
}
.localApp li {
  /* float: left; */
  font-size: 18px;
  border-radius: 15px;
  margin-right: 20px;
  margin-bottom: 20px;
  text-align: center;
  line-height: 50px;
  height: 170px;
}
.localApp li div.hover {
  border: 3px solid #ffffff;
  border-radius: 17px;
  /* margin: -2px 33px 33px -2px; */
  transform: scale(1.1);
  box-shadow: 0px 30px 40px rgba(0, 0, 0, 0.4);
}
.appListPos div{
  position: relative;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 15px;
  width: 170px;
  height: 164px;
  margin-bottom: 0px;
}
.appListPos div img {
  width: 100%;
  height: 100%;
  border-radius: 15px 15px 0px 0px;
}
.appListPos div .s-title {
  left: -3px;
  top: 72px;
  line-height: 25px;
}
.appTitle {
  background: "";
}
</style>

<script>
import { mapGetters } from "vuex";
import { client as yh } from "../../js/client.js";
import AlertMsg from "../firm";
import c from "../../js/common.js";
import { bp } from "../../js/ga.js";
import {xha} from '../../js/xha.js'

export default {
  name: "LocalAPP", // 本地app应用
  props: ["initData", "itemSort"],
  components: {
    AlertMsg
  },
  data() {
    return {
      isShow: false,
      placeholder: require("../../assets/img_loading_352x190.png"),
      initScore: require("../../assets/corner_mark.png"),
      itemNo: 0,
      pos: 0,
      eleId: "",
      elementList: [
        {appName:'大象融媒app安装卸载',appPackageName:'123',appIcon:''},
        {appName:'测试app测试app',appPackageName:'123',appIcon:''},
        {appName:'爱奇艺',appPackageName:'123',appIcon:''},
        {appName:'腾讯视频',appPackageName:'123',appIcon:''},
        {appName:'测试app测试app',appPackageName:'123',appIcon:''},
        {appName:'爱奇艺',appPackageName:'123',appIcon:''}
      ],
      round: "round",
      appTitle: "appTitle",
      title: "",
      tips: "应用操作",
      openApp: "打开应用",
      uninstall: "卸载应用",
      isImg: false,
      timer: null,
      applength:0,
    };
  },
  created() {
    this.eleId = this.initData.specialId;
    this.title = this.initData.specialName; //默认标题
    window.getAppList = this.getAppList;
    this.$hybrid.getAppList =  this.getAppList;
    try {
      // let jsonArray = yh.getAppList("Hybrid.getAppList");
      let jsonArray = yh.getAppList("getAppList");
      console.log('开始获取本地app应用列表'+jsonArray);
      this.elementList = JSON.parse(jsonArray);
      this.$store.commit("LOCALAPPSNUM", this.elementList.length); //  获取本地app数量
      this.applength = this.appsNum;
    } catch (error) {
      console.log("获取本地app应用error");
    }
    // this.elementList = this.initData.elementList;
  },
  updated() {
    this.$nextTick(function() {
      this.$lazyImages.loadImage();
    });
  },
  methods: {
    getAppList(jsonArray) {
      console.log("卸载成功，重新刷新appList");
      if(typeof(jsonArray)==='string'){
        //  console.log("string="+jsonArray);
         jsonArray = JSON.parse(jsonArray);
      }else if(typeof(jsonArray)==='object'){
        //  console.log("jsonArrayType="+JSON.stringify(jsonArray));
      }
      try {
        // let jsonArray = yh.getAppList("getAppList");
        this.elementList = jsonArray;
        this.$store.commit("LOCALAPPSNUM", this.elementList.length); //  获取本地app数量
        console.log("当前app数量"+this.appsNum);
        if (this.elementList.length == 0) {
          this.removeCss();
          this.isShow = false;
          this.$emit("pressDown", this.isShow, this.eleId, "up");
          return;
        } else if (this.elementList.length == this.itemNo) {
          // 卸载更新后需要判断应用个数是否为0
          this.itemNo--;
          if (this.itemNo < 0) {
            // 为零键值传递给上一个组件
            this.removeCss();
            this.isShow = false;
            this.$emit("pressDown", this.isShow, this.eleId, "up");
            return;
          } else {
            this.addCss();
          }
        }
      } catch (error) {
        console.log("卸载成功后获取本地app应用error");
      }
    },
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        const element = this.elementList[this.itemNo];
        const elementId = "";
        let posId = "";
        const columnId = c.BPTypefilters(this.category[this.navpos].catCode);
        if(this.itemSort){
           if(this.itemNo>9){
                          posId =  this.itemSort  + (this.itemNo+1);
            }else{
                          posId =  this.itemSort + '0' +(this.itemNo+1);
            }
        }
        c.setPosition(posId,columnId); // 保存推荐位置
        this.$store.commit("DOWNLOADAPPNAME",element.appName); //  打开的app名称
        xha.pageLoad(this.category[this.navpos].catName,"",element.appName,elementId,posId,columnId); // xha 埋点
        this.isShow = false;
        this.$refs.appAlert.dialog(
          true,
          element.appIcon,
          element.appPackageName,
          element.appName
        );
      }
    },
    back() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "back");
    },
    left() {
      if (this.itemNo == 0) return;
      this.removeCss();
      this.itemNo--;
      this.addCss();
    },
    right() {
      if (this.itemNo == this.elementList.length - 1) return;
      if (this.itemNo == 5) return;
      this.removeCss();
      this.itemNo++;
      this.addCss();
    },
    up() {
      if (this.itemNo < 6) {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "up");
      } else {
        this.removeCss();
        this.itemNo -= 6;
        this.addCss();
      }
    },
    down() {
      if (this.itemNo > 5) {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "down");
      } else {
        if ((this.elementList.length-1) <= 5) return;
        this.removeCss();
        this.itemNo += 6;
        if(this.itemNo>(this.elementList.length-1)){
          this.itemNo = 5;
        }
        this.addCss();
      
      }
    },
    addCss() {
      if (this.pos == 0) {
        $("#app_index_"+this.eleId+'_'+this.itemNo+" .imglazy").addClass("hover");
        // let p_title = $("#app_index_" + this.itemNo + " .s-title-name").text();
        // if (p_title.length >= 6) {
        //   let id = "#app_index_" + this.itemNo;
        //   let speed = 40;
        //   let that = this;
        //   let tab = document.querySelector(id + " .s-title");
        //   let tab1 = document.querySelector(id + " .s-title-name");
        //   let tab2 = document.querySelector(id + " .s-title-temp");
        //   tab.style.textOverflow = "clip";
        //   tab2.innerHTML = tab1.innerHTML;
        //   this.timer = setInterval(function() {
        //     that.marquee(tab, tab1, tab2);
        //   }, speed);
        // }
        //   $('.appListPos div:eq(1)').eq(this.itemNo).css("background",'#0098BC');
      }
    },
    removeCss() {
      if (this.pos == 0) {
        $("#app_index_"+this.eleId+'_'+this.itemNo+" .imglazy").removeClass("hover");
        //   $('.appListPos div:eq(1)').eq(this.itemNo).css("background",'rgba(255, 255, 255, 0.2)');
        // if (this.timer != null) {
        //   clearInterval(this.timer);
        //   this.timer = null;
        //   let id = "#app_index_" + this.itemNo;
        //   let tab = document.querySelector(id + " .s-title");
        //   tab.scrollLeft = 0;
        //   tab.style.textOverflow = "ellipsis";
        // }
      }
    },
    updateStast(bl, pos, isEnd) {
      if (this.elementList.length == 0) {
        // 获取本地应用为空,不需展示此组件
        this.$store.commit('NO_SCROLL', true);
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "up");
        return false;
      }
      this.isShow = bl;
      this.addCss();
      if (bl) {
        let eleH = this.cEleOffsetTop("#app_index_"+this.eleId+'_'+0);
        console.log("组件距离文档的高度:" + eleH);
        this.$emit("eleScroll", this.name, pos, eleH);
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset().top;
      let realHeight = 0;
      realHeight = fEleDistance + 350;
      return realHeight;
    },
    updateKeyDo(args) {
      this.isShow = true;
    },
    marquee(tab, tab1, tab2) {
      if (tab2.offsetWidth - tab.scrollLeft <= 0) {
        // tab.scrollLeft -= tab1.offsetWidth;
        tab.scrollLeft = 0; // 上面的写法会导致滚动时突然抖动
      } else {
        tab.scrollLeft++;
      }
    }
  },
  computed: {
    ...mapGetters(["category", "navpos","noScroll","appsNum","downloadAppName"])
  },
  watch: {
    initData() {
      this.eleId = this.initData.specialId;
      // this.elementList = this.initData.elementList;
      this.title = this.initData.specialName; //默认标题
    },appsNum(){
            console.log(this.applength);
            console.log("本地app数量已变化"+this.appsNum);
            if(this.appsNum>this.applength){
              console.log("app已下载");
              xha.app(this.downloadAppName, "1", "2");
            }else if(this.appsNum<this.applength){
              if(this.$route.name=="appManage"){
                xha.app(this.downloadAppName,'2','4');
                }else{
                  xha.app(this.downloadAppName,'1','4');
                }
            }
           this.applength = this.appsNum;
           console.log("需要更新本地的app数量");
    }
  },
  beforeRouteLeave(to, from, next) {
    window.clearInterval(this.timer);
    this.timer = null;
    next();
  }
};
</script>



