<template>
  <div class="teleplayTop">
      <div class="seriesTop0"><img v-if="elementList[0]" :src="elementList[0].elementImg" ></div>
      <div class="seriesTop1"><img v-if="elementList[1]" :src="elementList[1].elementImg" ></div>
      <div class="seriesTop2">
          <img v-if="elementList[2]" :src="elementList[2].elementImg" />
          <img v-if="elementList[3]" :src="elementList[3].elementImg" />
      </div>
      <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
  </div>
</template>
<style scoped>
.teleplayTop {
  position: relative;
  width: 1190px;
  height: 300px;
  display: flex;
}
.seriesTop0,
.seriesTop1 {
  position: relative;
  width: 354px;
  height: 290px;
  border-radius: 11px;
  border: 1px solid #707070;
  margin-right: 20px;
}
.seriesTop2 {
  position: relative;
  justify-content: space-between;
  width: 354px;
  height: 290px;
  border-radius: 11px;
  margin-right: 20px;
}
.teleplayTop img {
  width: 100%;
  height: 100%;
  border-radius: 11px;
}
.seriesTop2 img {
  width: 360px;
  height: 136px;
}
.seriesTop2 {
  display: flex;
  flex-direction: column;
}
.seriesTop0.hover,
.seriesTop1.hover {
  border: 3px solid #ffffff;
  margin: -2px 18px -2px -2px;
  transform: scale(1.05);
  border-radius: 14px;
}
.seriesTop2 img.hover {
  border: 3px solid #ffffff;
  margin: -3px -3px -3px -3px;
  border-radius: 15px;
  transform: scale(1.05);
}
</style>
<script>
import { mapGetters } from "vuex";
import c from "../../js/common.js";
import { bp } from "../../js/ga.js";
import {xha} from '../../js/xha.js';
export default {
  name: "SeriesTop",
  props: ["initData", "itemSort"],
  data() {
    return {
      isShow: false,
      placeholder: require("../../assets/img_loading_352x190.png"),
      initScore: require("../../assets/corner_mark.png"),
      itemNo: 0,
      pos: 0,
      eleId: "",
      elementList: [{"elementImg":''},{"elementImg":''},{"elementImg":''},{"elementImg":''},{"elementImg":''}],
      round: "round"
    };
  },
  created() {
    this.elementList = this.initData.elementList;
    this.eleId = this.initData.specialId;
  },
  mounted() {},
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "back");
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        let posId = '';
        let columnId = c.BPTypefilters(this.category[this.navpos].catCode);
        if(this.itemSort){
          posId =  this.itemSort + '0'+(this.itemNo+1)
        }
         c.setPosition(posId,columnId); // 保存推荐位置
        xha.pageLoad(this.category[this.navpos].catName,"",this.elementList[this.itemNo].elementName,this.elementList[this.itemNo].elementId,posId,columnId); // xha 埋点
        const elementId = this.elementList[this.itemNo].elementId;
        const trackId = this.category[this.navpos].catId + "|" + elementId;
        bp.categoryClick(
          this.category[this.navpos].catId,
          elementId,
          trackId,
          1,
          ""
        );
        const eleDetail = this.elementList[this.itemNo];
        let playType = eleDetail.elementType;
        if (playType === "8" || 8 === playType) {
          c.routerSkip(
            eleDetail.jsonUrl,
            eleDetail.elementType,
            {},
            this.$router
          );
        } else {
          if(eleDetail.layout === 'Subject_Home'){
                c.routerSkip(
                  eleDetail.jsonUrl,
                  '12',
                  this.category[this.navpos],
                  this.$router
                );
          }else{
              c.routerSkip(
                eleDetail.jsonUrl,
                eleDetail.elementType,
                this.category[this.navpos],
                this.$router
              );
          }
        
        }
      }
    },
    left() {
      if (this.itemNo == 0) return;
      this.removeCss();
      if (this.itemNo == 2 || this.itemNo == 3) {
        this.itemNo = 1;
      } else {
        this.itemNo--;
      }
      this.addCss();
    },
    right() {
      if (this.itemNo >= 3) return;
      this.removeCss();
      this.itemNo++;
      this.addCss();
    },
    up() {
      if (this.itemNo == 3) {
        this.removeCss();
        this.itemNo--;
        this.addCss();
      } else {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "up");
      }
    },
    down() {
      if (this.itemNo == 2) {
        this.removeCss();
        this.itemNo++;
        this.addCss();
      } else {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "down");
      }
    },
    addCss() {
      if (this.pos == 0) {
        if (this.itemNo == 0 || this.itemNo == 1) {
          $(".seriesTop" + this.itemNo).addClass("hover");
        } else if (this.itemNo == 2 || this.itemNo == 3) {
          $(".seriesTop2 img")
            .eq(this.itemNo - 2)
            .addClass("hover");
        }
      }
    },
    removeCss() {
      if (this.pos == 0) {
        if (this.itemNo == 0 || this.itemNo == 1) {
          $(".seriesTop" + this.itemNo).removeClass("hover");
        } else if (this.itemNo == 2 || this.itemNo == 3) {
          $(".seriesTop2 img")
            .eq(this.itemNo - 2)
            .removeClass("hover");
        }
      }
    },
    updateStast(bl, pos, isEnd) {
      this.isShow = bl;
      this.addCss();
      if (bl) {
        let eleH = this.cEleOffsetTop(".seriesTop0");
        this.$emit("eleScroll", this.name, pos, eleH);
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset().top;
      let realHeight = 0;
      realHeight = fEleDistance + 310;
      return realHeight;
    }
  },
  watch: {
    initData() {
      this.eleId = this.initData.specialId;
      this.elementList = this.initData.elementList;
    }
  },
  computed: {
    ...mapGetters(["category", "navpos"])
  }
};
</script>



