<template>
<!-- 大数据推荐 -->
    <div class="liveHot">
      <h2>{{title}}</h2>
      <ul>
        <li  v-for="(item,index) in specialList.channelList" v-if="index<12" :key="item.id" :id="('lh_'+eleId+'_'+index)"  >
          <lazy-image
                :src='item.elementImg'
                :placeholder='placeholder'
                :title = "(item.channelName?item.channelName:'')+'-'+(item.programName?item.programName:'')"
                :imgClass = 'round'
                  />
        </li>
      </ul>
        <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
    </div>
</template>
<style scoped>
    h2{
      font-size:30px;
      margin-bottom: 25px;
      font-weight: 400;
      letter-spacing: 2px;
      font-family: "Arial","Microsoft YaHei","黑体","宋体",sans-serif;
    }
    .liveHot{
      position: relative;
      width: 1227px;
      min-height: 620px;
      margin-top: 26px;
    }
    .liveHot li{
      position:relative;
      float: left;
      margin-right: 16px;
      margin-bottom: 40px;
      width: 172px;
      height: 230px;
      z-index:10;
      display: inline-block;
      box-sizing: border-box;
    }
    .liveHot img{
      width: 100%;
      height: 100%;
    }
    .liveHot li.hover{
      margin: -4px 20px 44px -4px;
      border: 4px solid #FFF;
      transform: scale(1.05);
      box-shadow:0px 10px 20px rgba(0, 0, 0, 0.55);
      border-radius: 18px;
    }
    .round{
        border-radius: 15px;
    }
    .circle{border-radius: 50%;}
    .appTitle{left:0px;}
</style>
<script>
import c from '../../js/common.js';
import {bp} from '../../js/ga.js';
import {mapGetters} from 'vuex';
import {client as yh } from '../../js/client.js';
import {xha} from '../../js/xha.js';
    export default {
        props:['initData','itemSort'],
        name:'LiveHot12',
        data(){
          return{
            title:'正在热播',
            name:'liveHot',
            placeholder:require('../../assets/img_loading_160x230.png'),
            initScore:require('../../assets/corner_mark.png'),
            ClassNo:-1,
            position:0,
            itemNo:0,
            isShow:false,
            isEnd:false,
            specialList:[],
            eleId:'LiveHot12',
            timer:null,
            pos:'',
            round:'round',
            enterType:7,
            playCsList:null,
          }
        },created(){
           this.specialList = this.fliteData(this.initData.liveList,"hot");
           const liveUrl = this.initData.liveJsonUrl;
           if(liveUrl){
              this.filterUrl(liveUrl);
            }
        },mounted(){
          
        },updated() {
          if(this.isShow){
             this.$store.commit('GET_LIVESTATUS', true); // 小窗口是否关闭
            yh.close_s_Video();
          }
        },
        methods: {
            filterUrl(url){
              if(url.indexOf("clientType=bs")>=0){
                this.playCsList =  url.replace('clientType=bs','clientType=cs');
              }
            },
            keyCode(kc) {
                if (kc == "KeyBack") {
                    this.back();
                }else if(kc == "right"){
                  this.right();
                }else if(kc == "left"){
                  this.left();
                }else if(kc == "down"){
                  this.down();
                }else if(kc == "up"){
                  this.up();
                }else if(kc == "KeyEnter"){
                  if(this.timer != null){
                    clearInterval(this.timer)
                  }
                    c.logCsSplice(4,"","","","","","","","","","");// B透传C埋点信息
                    let posId = '';
                    let columnId = c.BPTypefilters(this.category[this.navpos].catCode);
                    if(this.itemSort){
                      if(this.itemNo>9){
                          posId =  this.itemSort  + (this.itemNo+1);
                        }else{
                          posId =  this.itemSort + '0' +(this.itemNo+1);
                        }
                    }
                     const eleDetail = this.specialList.channelList[this.itemNo];
                     const elementId = this.specialList.channelList[this.itemNo].channelId;
                     const trackId = this.category[this.navpos].catId +"|"+elementId;
                    xha.pageLoad(this.category[this.navpos].catName,"",eleDetail.channelName,this.specialList.channelList[this.itemNo].channelId,posId,columnId); // xha 埋点
                     bp.categoryClick(this.category[this.navpos].catId, elementId, trackId,1, "");
                     yh.fullLivePlay(this.playCsList,0,this.specialList.categoryType,eleDetail.channelId,'','',true,false);
                     this.loading();
                }
            },
            loading(){
                  try{
                     //加载yh埋点
                    const BiJson = {};
                    BiJson.cat_id = this.category[this.navpos].catId;  //所在栏目id
                    BiJson.trace_id = c.getTrackPath(); //当前访问页面id路径、轨迹，以“|”分割
                    BiJson.trace_type = c.getTrackPathType(); //当前访问页面路径、轨迹，以“|”分割的页面类型
                    BiJson.parent_page_type = c.getParentPageType();
                    BiJson.parent_page_id = c.getParentPageId(); //父级页面id
                    BiJson.special_type="";
                    // BiJson.special_type =c.getParentPageType()=="8"?special_type:""; //专题也才传 专题未传参
                    BiJson.recommend_place = this.category[this.navpos].catName; //未知
                    BiJson.special_id=this.$route.params.bp.special_id||"";
                    BiJson.cat_name = this.category[this.navpos].catName; //所在栏目名称
                    BiJson.enterType =this.enterType; //进入点播方式 哪些是点播资源/嗨看资源？
                    BiJson.filmType = "1"; //影片类型(0:嗨看 1: 点播) 我们都是点播
                    BiJson.assetId="";
                    BiJson.assetName="";
                    BiJson.type="0"+c.getParentPageType();
                    BiJson.typeId=this.category[this.navpos].catId=="202652"?"202652":"0";
                    BiJson.pagePath=c.getTrackPathType().replace(/\|/gi, '_');
                    BiJson.srcPagePath=c.getTrackPathType().slice(0,c.getTrackPathType().length-2).replace(/\|/gi, '_');
                    yh.setBiJson(JSON.stringify(BiJson));
                } catch(error){
                  console.log(error);
                }
            },
             back() {
               if(this.timer!=null){
                 clearInterval(this.timer);
                }
                this.$store.commit('GET_LIVESTATUS', true); // 小窗口是否关闭
                this.removeCss();
                this.position=0;
                this.itemNo=0;
                this.isShow = false;
                this.$emit('pressDown', this.isShow,this.eleId,"back");
            },down(){
              // console.log("down:"+this.isEnd);
              if(this.itemNo<=5){
                this.removeCss();
                this.itemNo+=6;
                this.addCss();
                let eleH = this.cEleOffsetTop('#lh_'+this.eleId+"_"+this.itemNo);
                this.$emit('eleScroll',this.name,"down",eleH);
              }else{
                if(!this.isEnd){
                  this.removeCss();
                  this.isShow = false;
                  this.$emit('pressDown', this.isShow,this.eleId,"down");
                }
              }
          },up(){
            if(this.itemNo>5){
              this.removeCss();
              this.itemNo-=6;
              this.addCss();
              let eleH = this.cEleOffsetTop('#lh_'+this.eleId+"_"+this.itemNo);
              this.$emit('eleScroll',this.name,'up',eleH);
            }else{
              this.removeCss();
              this.isShow = false;
              this.$emit('pressDown', this.isShow,this.eleId,"up");
            }


          },left(){
              if(this.itemNo==0 || this.itemNo == 6)return;
                this.removeCss();
                this.itemNo--;
                this.addCss();
          },right(){
            if(this.itemNo==11||this.itemNo==5)return;
            this.removeCss();
            this.itemNo++;
            this.addCss();
          },addCss(){
            $('#lh_'+this.eleId+"_"+this.itemNo).addClass('hover');
            let p_title = $('#lh_'+this.eleId+"_"+this.itemNo+' .s-title-name').text();
            if(p_title.length>=10){
              let id = '#lh_'+this.eleId+"_"+this.itemNo;
              let speed = 40;
              let that = this;
              let tab =  document.querySelector(id+' .s-title');
              let tab1 =  document.querySelector(id+' .s-title-name');
              let tab2 =  document.querySelector(id+' .s-title-temp');
              tab.style.textOverflow = 'clip';
              tab2.innerHTML = tab1.innerHTML;
               this.timer = setInterval(function(){
                that.Marquee(tab,tab1,tab2)
              },speed);
            }
             
          },removeCss(){
             $('#lh_'+this.eleId+"_"+this.itemNo).removeClass('hover');
            if(this.timer!=null){
              clearInterval(this.timer);
              this.timer = null;
              let id = '#lh_'+this.eleId+"_"+this.itemNo;
              let tab = document.querySelector(id+' .s-title');
              tab.scrollLeft = 0;
              tab.style.textOverflow = 'ellipsis';
            }

          },updateStast(bl,pos,isEnd){
            this.isShow = bl;
            this.pos = pos;
            this.isEnd = isEnd;
            if(pos=="down" && this.itemNo>5&&isEnd==false){
              this.itemNo = 0;
            }
            this.addCss();
            if(bl==true){
              let eleH = this.cEleOffsetTop('#lh_'+this.eleId+"_"+this.itemNo);
              // console.log("组件距离文档的高度:"+eleH);
              this.$emit('eleScroll',this.name,this.pos,eleH);
            }
          },cEleOffsetTop(ele){
            let fEleDistance = $(ele).offset().top;
            let realHeight = fEleDistance+260;
            return realHeight;
          }, Marquee(tab,tab1,tab2){
              if(tab2.offsetWidth - tab.scrollLeft <=0){
                  // tab.scrollLeft -= tab1.offsetWidth;
                  tab.scrollLeft  = 0; // 上面的写法会导致滚动时突然抖动
              }else{
                  tab.scrollLeft++;
              }
            },fliteData(args,type){
              for (let index = 0; index < args.length; index++) {
                const element = args[index];
                if(element.categoryType==type){
                  return element;
                }
              }
            }
        },computed:{
          ...mapGetters([
            'category',
            'navpos',
            'liveStatus'
          ])
      },watch:{
          initData(){
            this.specialList = this.fliteData(this.initData,"hot");
             const liveUrl = this.initData.liveJsonUrl;
            if(liveUrl){
              this.filterUrl(liveUrl);
            }
            // 数据更新组件id也需要更新
            this.$nextTick(function() {
               this.$lazyImages.loadImage();
           })
          }
        }
    }
</script>



